<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <!--  Modal Header -->
                        <div class="modal-header ui-draggable-handle py-1">
                            <h5 name="header" class="pt-2">
                                Select Conditions
                            </h5>
                            <button class="close" @click="ClosePopUp">
                                <em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                        </div>

                        <!--  Modal Body -->
                        <div class="modal-body">
                            <!--  Search Bar -->
                            <div class="mb-3 position-relative">
                                <em class="fa fa-search position-absolute text-muted"
                                    style="left: 10px; top: 50%; transform: translateY(-50%);"></em>
                                <input type="text" class="form-control pl-4" placeholder="Search conditions"
                                    v-model="searchQuery" />
                            </div>

                            <!--  Scrollable List -->
                            <ul class="list-group conditions-list mb-3">
                                <li v-for="(condition, index) in filteredConditions" :key="index"
                                    class="list-group-item condition-item"
                                    :class="{ active: isConditionSelected(condition.SERVICEID) }"
                                    @click="toggleCondition(condition)">
                                    <strong>{{ condition.SERVICENAME }}</strong>
                                    <br />
                                    <small class="text-muted">{{ condition.CONDITION }}</small>

                                    <span class="badge badge-light float-right font-weight-medium p-2"
                                        v-if="isConditionSelected(condition.SERVICEID)">
                                        Added
                                    </span>
                                </li>

                                <li v-if="filteredConditions.length === 0"
                                    class="list-group-item text-center text-danger">
                                    No conditions found
                                </li>
                            </ul>
                        </div>

                        <!--  Modal Footer -->
                        <div class="modal-footer py-2">

                            <div class="row flex-row-reverse">
                                <div class="col-lg-12 text-right p-0">
                                    <a href="javascript:;" @click="applySelectedConditions" class="btn btn-success mr-2"
                                        title="@Resources.Save"><em class="fa fa-save pr-2"></em>Update Conditions</a>
                                    <a href="javascript:;" @click="ClosePopUp" class="btn btn-danger"
                                        title="@Resources.Cancel"><em class="fa fa-times pr-2"></em>Cancel</a>
                                </div>

                            </div>

                            <!-- <button type="button" class="btn btn-secondary btn-sm" @click="ClosePopUp">
                                Cancel
                            </button>
                            <button type="button" class="btn btn-primary btn-sm" @click="applySelectedConditions">
                                Update Conditions
                            </button> -->

                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from "../../services/DataService";

export default {
    props: {
        // List of already selected service IDs (from parent)
        selectedServiceIds: {
            type: Array,
            default: () => []
        }
    },
    data() {
        return {
            ConditionsData: [],
            PageSize: 1000,
            PageNumber: 1,
            SortBy: "",
            SortExp: "",
            TotalRecords: 0,
            searchQuery: "",
            // Local state for user-selected conditions
            selectedConditions: []
        };
    },
    computed: {
        filteredConditions() {
            const query = this.searchQuery.toLowerCase();
            return this.ConditionsData.filter(
                item =>
                    !this.searchQuery ||
                    (item.SERVICENAME &&
                        item.SERVICENAME.toLowerCase().includes(query))
            );
        }
    },
    created() {
        debugger
        this.getConditionRecords();
        //  Initialize with already selected service IDs (from parent)
        this.selectedConditions = [...this.selectedServiceIds];
    },
    methods: {
        ClosePopUp() {
            this.$parent.IsShowCondition = false;
        },

        //  Check if condition is currently selected
        isConditionSelected(serviceId) {
            return this.selectedConditions.includes(parseInt(serviceId));
        },

        //  Toggle condition on click
        toggleCondition(condition) {
            debugger
            const id = parseInt(condition.SERVICEID);
            const index = this.selectedConditions.indexOf(id);

            if (index === -1) {
                this.selectedConditions.push(id);
            } else {
                this.selectedConditions.splice(index, 1);
            }
        },

        //  Emit all selected conditions back to parent
        applySelectedConditions() {
            debugger
            const selected = this.ConditionsData.filter(cond =>
                this.selectedConditions.includes(parseInt(cond.SERVICEID))
            );

            this.$emit("conditions-selected", selected);
            this.ClosePopUp();
        },

        //  Fetch conditions from API
        getConditionRecords() {
            const vm = this;
            const url = `serviceName=&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&setupId=`;

            DataService.GetBillServiceListing(url).then(response => {
                if (response && response.data && response.data.length > 0) {
                    vm.ConditionsData = response.data;
                    vm.TotalRecords =
                        response.data[0].TOTALRECORDS || response.data.length;
                } else {
                    vm.ConditionsData = [];
                    vm.TotalRecords = 0;
                }
            });
        }
    }
};
</script>

<style scoped>
.form-control.pl-4 {
    padding-left: 2rem !important;
}

.conditions-list {
    max-height: 500px;
    overflow-y: auto;
    border: 0px solid #ddd;
    border-radius: 4px;
}

.list-group-item {
    margin: 8px 5px 8px 0px;
    border: 1px solid #cacaca !important;
    border-radius: 10px !important;
    margin-top: 10px;
}

.conditions-list::-webkit-scrollbar {
    width: 6px;
}

.conditions-list::-webkit-scrollbar-thumb {
    background-color: #bbb;
    border-radius: 3px;
}

.conditions-list::-webkit-scrollbar-thumb:hover {
    background-color: #888;
}

.condition-item {
    cursor: pointer;
    user-select: none;
}

.condition-item.active {
    background-color: #59a1da;
    color: #fff;
    margin-top: 1px !important;
    border-color: #4a88ba !important
}

.condition-item.active small {
    color: #e9ecef;
}

.modal-footer {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
}

.condition-item.active small.text-muted {
    color: #fff !important;
}
</style>
