<template>
    <section>
        <div class="col-md-12 border p-3">
            <div class="col-md-12 p-0">

                <dynamic-form lang="en" ref="refAddress" :buttons="false" :schema="FormSchema">

                    <template v-slot:tgslot-PaymentTerm="{ data }">
                        <div class="listing mb-0">
                            <div class="table-responsive table-fix-header">
                                <table class="table table-bordered mt-0">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th>{{ $t('TermDays') }}</th>
                                            <th>{{ $t('TermPercentage') }}</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(row, index) in termRows" :key="index" class="termPay">
                                            <td>
                                                <div class="d-flex">
                                                    <input type="text" v-model="row.days"
                                                        :class="{ 'form-control col-8': true, 'disabled': row.days == 'On Registration', 'border-danger': row.daysError }"
                                                        @change="handleUntilDays(row)">
                                                    <span class="col-4 float-left mt-1">{{ $t('days') }}</span>
                                                </div>
                                            </td>
                                            <td>
                                                <div class="d-flex">
                                                    <input type="number"
                                                        :class="{ 'form-control col-8': true, 'border-danger': row.exceedError, 'border-danger': row.inpError }"
                                                        :disabled="index === termRows.length - 1"
                                                        v-model="row.percentage"
                                                        @change="handlePercentageChange(index)">
                                                    <span class="col-4 float-left mt-1">%</span>
                                                </div>
                                                <span v-if="row.exceedError" class="text-danger small">{{
                                                    $t('ValidateTermPercentage') }}</span>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>

                    </template>

                    <template v-slot:tgslot-Occurs_Every="{ data }">
                        <Field name="Occurs Every" :rules="{ 'required': true }" v-slot="{ errors }">
                            <div class="input-group">
                                <input type="number"
                                    v-on:keyup="checkQuantity($event, data.fieldInfo, data.fieldInfo.value)"
                                    id="" v-model="data.fieldInfo.value" min="1" maxlength="8"
                                    v-bind:class="{ 'txtitems form-control ': true, }" />
                                <span class="input-group-append">
                                    <span class="input-group-text" id="spnOutOF">Hours</span>
                                </span>
                            </div>
                            <span v-if="errors[0]" name="Occurs Every" class="invalid-feedback d-block">{{
                                errors[0]
                                }}</span>
                        </Field>
                    </template>

