        created: function () {
            var vm = this;
            vm.planId = parseInt(vm.$route.params.id)

            // Set GenerateBill options first
            let options = [{ value: "1", name: this.$t(vm.callfrom === 'UTILITY_BILLING' ? "ByPlan" : "OnFixdate")},{ value: "2", name: this.$t("AsPerRegistrationDate") },{ value: "3", name: this.$t("ByBatch") },];
            if (vm.callfrom === 'UTILITY_BILLING') {
                options = options.filter(opt => opt.value !== "2");
            }else{
                options = options.filter(opt => opt.value !== "3");
            }
            this.$nextTick(() => {
                if (vm.$refs.refAddress) {
                    vm.$refs.refAddress.updateProperty("GenerateBill", "config.options", options);
                    vm.$refs.refAddress.UpdateKeyValue();
                }
            });

            setTimeout(function () {
                vm.BillGenerateDropDown();
                vm.GetBillCycleData(vm.planId);
                vm.GetDunningList(vm.planId);
                vm.$refs.refAddress.updateProperty("AdvanceSettings", "value", true);
            }, 500);
        },
        methods: {
            GetDunningList(id) {
                var vm = this;
                var url = `palneId=${id}`;
                DataService.GetDdlDunningList(url).then((response) => {
                    if (response.data != '') {
                        var selctedValue = [];
                        if (response.data.length > 0) {
                            response.data.forEach((item, index) => {
                                selctedValue.push({
                                    value: item.ID,
                                    name: item.TITLE
                                });
                            });
                        }
                        vm.$refs.refAddress.updateProperty("Dunning", "config.options", selctedValue);
                        // vm.$refs.validatePlan.updateProperty("Dunning", "config.options", selctedValue);
                        vm.$refs.refAddress.UpdateKeyValue();
                    }
                });

            },
            getReadableDateTimeString: async function(timeStr, dateStr) {
                if (!timeStr || typeof timeStr !== 'string') return null;
   
                const today = new Date();
                const datePart = dateStr || today.toISOString().split('T')[0]; // "YYYY-MM-DD"
                const fullDateTimeStr = `${datePart}T${timeStr}`;
   
                const dateObj = new Date(fullDateTimeStr);
   
                return isNaN(dateObj.getTime()) ? null : dateObj.toString(); // full date-time string
            },
   
            extractTimeFromDateTime: function(dateTimeStr) {
                if (!dateTimeStr || typeof dateTimeStr !== 'string') return '';
   
                // If it's already just a time string (e.g., "14:30:00"), return as is
                if (dateTimeStr.match(/^\d{1,2}:\d{2}:\d{2}$/) || dateTimeStr.match(/^\d{1,2}:\d{2}\s(AM|PM)$/i)) {
                    return dateTimeStr;
                }
   
                // Try to extract time from datetime string
                const timeMatch = dateTimeStr.match(/(\d{1,2}:\d{2}:\d{2}|\d{1,2}:\d{2}\s?(AM|PM)?)/i);
                return timeMatch ? timeMatch[1] : dateTimeStr;
            },
            getCycleRange(len, start) {
                if (!start || len <= 0) return "";
                const idx = this.months.indexOf(start);
                if (idx === -1) return "";
                const list = [];
                for (let i = 0; i < len; i++) {
                    list.push(this.months[(idx + i) % 12]);
                }
                return list.join("  ");
            },

            gethelfYearCycleRange() {
                const month = this.form.customQuarterStartMonth;
                const index = this.months.indexOf(month);
                const nextMonths = [
                    this.months[index % 12],
                    this.months[(index + 1) % 12],
                    this.months[(index + 2) % 12],
                ];
                return `${nextMonths.join("  ")}`;
            },
            checkQuantity(event, fieldInfo, value) {
                // Validation method for Occurs_Every field - same as CreateBatch.vue
                var vm = this;
                if (value && value > 99999999) {
                    fieldInfo.value = value.toString().slice(0, 8);
                }
            },
            OnChangeNoOfTerms: function (x, y, z) {
                var vm = this;
                debugger;
                vm.$refs.refAddress.updateProperty("DaysUntilDue", "value", '');
                vm.termRows = [];
                const selectedTerms = y.value;

                if (selectedTerms > "1") {
                    vm.$refs.refAddress.updateProperty("GenerateSingleInvoice", "visibility", true);
                    vm.$refs.refAddress.UpdateKeyValue();
                }
                else {
                    vm.$refs.refAddress.updateProperty("GenerateSingleInvoice", "visibility", false);
                    vm.$refs.refAddress.UpdateKeyValue();
                }

                vm.termRows = [];
                for (let i = 0; i < selectedTerms; i++) {
                    const row = {
                        days: (i === 0) ? "On Registration" : "",
                        percentage: null
                    };
                    if (selectedTerms == 1 && i === 0) {
                        row.percentage = 100;
                    }
                    vm.termRows.push(row);
                }
                vm.$refs.refAddress.updateProperty("PaymentTerm", "visibility", true);
                vm.$refs.refAddress.UpdateKeyValue();
            },
            handleUntilDays: function (index) {
                var vm = this;
                vm.termRows.forEach((row, i) => {
                    vm.$refs.refAddress.updateProperty("DaysUntilDue", "value", row.days);
                });

            },
            handlePercentageChange: function (index) {
                const vm = this;
                let totalPercentage = 0;
                let filledRows = 0;
                vm.termRows.forEach((row, i) => {
                    if (i !== vm.termRows.length - 1) {
                        const percentageValue = parseFloat(row.percentage);
                        if (!isNaN(percentageValue)) {
                            totalPercentage += percentageValue;
                            filledRows++;
                        }
                    }
                });
                if (totalPercentage > 100) {
                    vm.termRows[index].percentage = null;
                    vm.termRows[index].exceedError = true;
                } else {
                    if (totalPercentage < 100) {
                        vm.termRows[index].exceedError = false;
                        vm.termRows[index].daysError = false;
                        vm.termRows[index].inpError = false;
                        const remainingPercentage = 100 - totalPercentage;
                        if (filledRows === 0) {
                            vm.termRows[vm.termRows.length - 1].percentage = 100;
                        } else {
                            vm.termRows[vm.termRows.length - 1].percentage = remainingPercentage;
                        }
                    }
                }
            },
            OnChangeReadingNotAvailable: function (event, field) {
                var vm = this;
                field.value = event.target.getAttribute('data-value');
                let labelValue = field.value;
                if (labelValue == "AverageBill") {
