            OnChangeGenerateBill: function (event, field) {
                var vm = this;
                field.value = event.target.getAttribute('data-value');
                let labelValue = field.value;

                // Preserve original logic for all callfrom types
                if (labelValue == 1) {
                    vm.GenerationDate = true;
                }
                else {
                    vm.GenerationDate = false;
                }

                vm.form.BillGenerationDate="";
                vm.$refs.refAddress.updateProperty("DaysUntilDue", "value", "");
                vm.$refs.refAddress.updateProperty("LateFeesApplicable", "value", 0);
                vm.$refs.refAddress.updateProperty("LateFeeType", "value", "1");
                vm.$refs.refAddress.updateProperty("LateFeePercentage", "value", "");
                vm.$refs.refAddress.updateProperty("Dunning", "value", "");

                vm.$refs.refAddress.updateProperty("LateFeeType", "visibility", false);
                vm.$refs.refAddress.updateProperty("LateFeePercentage", "visibility", false);
                vm.$refs.refAddress.updateProperty("BillingFrequency", "value", "");
                vm.$refs.refAddress.updateProperty("MeterReadingStartDate", "value", "");
                vm.$refs.refAddress.updateProperty("MeterReadingEndDate", "value", "");

                // Handle UTILITY_BILLING specific logic
                if (vm.callfrom === 'UTILITY_BILLING' && labelValue == 1) {
                    // Hide Bill Generation Date field for UTILITY_BILLING with GenerateBill = 1
                    vm.GenerationDate = false;
                    // Show only UTILITY_BILLING specific fields when GenerateBill is "OnFixdate" (value = 1)
                    vm.$refs.refAddress.updateProperty("IsRecurring", "visibility", true);
                    vm.$refs.refAddress.updateProperty("IsRecurring", "value", "1");
                    vm.$refs.refAddress.updateProperty("BillingFrequency", "visibility", true);
                    vm.$refs.refAddress.updateProperty("UtilityBillingFrequencyType", "visibility", false);
                    vm.$refs.refAddress.updateProperty("StartDate", "visibility", true);
                    vm.$refs.refAddress.updateProperty("EndDate", "visibility", true);
                    vm.$refs.refAddress.updateProperty("MeterReadingStartDate", "visibility", true);
                    vm.$refs.refAddress.updateProperty("MeterReadingEndDate", "visibility", true);
                    vm.$refs.refAddress.updateProperty("AdvanceSettings", "visibility", true);
                    vm.$refs.refAddress.updateProperty("AdvanceSettings", "value", true);
                    const selectedValue = vm.$refs.refAddress.getPropertyValue("IsRecurring", "value");
                    if (selectedValue === "1") {
                    vm.$refs.refAddress.updateProperty("MeterReadingStartDate", "visibility", true);
                    vm.$refs.refAddress.updateProperty("MeterReadingEndDate", "visibility", true);
                    if (true === true) {
                        vm.$refs.refAddress.updateProperty("MeterReadingStartDate1", "visibility", true);
                        vm.$refs.refAddress.updateProperty("MeterReadingEndDate2", "visibility", true);
                    }
                    vm.$refs.refAddress.updateProperty("StartDate", "visibility", false);
                    vm.$refs.refAddress.updateProperty("EndDate", "visibility", false);
                    } else if (selectedValue === "2") {
                        // One Time - show date range fields
                        vm.$refs.refAddress.updateProperty("StartDate", "visibility", true);
                        vm.$refs.refAddress.updateProperty("EndDate", "visibility", true);

                        // Show advance fields if AdvanceSettings is enabled
                        if (true === true) {
                            vm.$refs.refAddress.updateProperty("Start_Date1", "visibility", true);
                            vm.$refs.refAddress.updateProperty("End_Date2", "visibility", true);
                        }
                    }

                    // Show Occurs_Every field for UTILITY_BILLING with GenerateBill = 1
                    vm.$refs.refAddress.updateProperty("Occurs_Every", "visibility", true);
                    this.showAdvanceSettings(true);
                    } else if (vm.callfrom === 'UTILITY_BILLING') {
                    // Show Bill Generation Date field for other GenerateBill values in UTILITY_BILLING
                    vm.GenerationDate = true;

                    // Hide UTILITY_BILLING specific fields for other GenerateBill values
                    vm.$refs.refAddress.updateProperty("IsRecurring", "visibility", false);
                    vm.$refs.refAddress.updateProperty("BillingFrequency", "visibility", false);
                    vm.$refs.refAddress.updateProperty("UtilityBillingFrequencyType", "visibility", false);
                    vm.$refs.refAddress.updateProperty("StartDate", "visibility", false);
                    vm.$refs.refAddress.updateProperty("EndDate", "visibility", false);
                    vm.$refs.refAddress.updateProperty("MeterReadingStartDate", "visibility", false);
                    vm.$refs.refAddress.updateProperty("MeterReadingEndDate", "visibility", false);
                    vm.$refs.refAddress.updateProperty("AdvanceSettings", "visibility", false);
                    vm.$refs.refAddress.updateProperty("Occurs_Every", "visibility", false);
                    this.showAdvanceSettings(false);
                } else {
                    // For non-UTILITY_BILLING, preserve original logic
                    if (labelValue == 1) {
                        vm.GenerationDate = true;
                    } else {
                        vm.GenerationDate = false;
                    }
                }

                vm.$refs.refAddress.UpdateKeyValue();
            },

