            GetBillCycleData: async function (planId) {
                var vm = this;
                let param = `planid=${planId}`
                DataService.GetBillingCycleData(param).then(async response => {
                    debugger
                    if (JSON.parse(response.data.result).DATA.length != 0) {
                        vm.BillData = JSON.parse(response.data.result).DATA
                        for (let indx = 0; indx < vm.BillData.length; indx++) {
                            const itm = vm.BillData[indx];
                            if (this.paymentType == 'One Time') {
                                if (itm.no_of_terms > 1) {
                                    vm.$refs.refAddress.updateProperty("GenerateSingleInvoice", "visibility", true);
                                }
                                vm.$refs.refAddress.updateProperty("NoOfTerms", "value", itm.no_of_terms);
                                if (itm.no_of_terms != null && itm.no_of_terms != '') {
                                    if (itm.payment_term != null && itm.payment_term != '') {
                                        vm.$refs.refAddress.updateProperty("PaymentTerm", "visibility", true);
                                        const row = {
                                            days: "",
                                            percentage: null
                                        };
                                        var paymentTerm = JSON.parse(itm.payment_term);
                                        paymentTerm.forEach(item => {
                                            const newRow = { ...row };
                                            newRow.days = item.days;
                                            newRow.percentage = item.percentage;
                                            vm.termRows.push(newRow);
                                        });
                                    }

                                }
                            }
                            else {
                                vm.$refs.refAddress.updateProperty("NoOfTerms", "visibility", false);
