                                    selectedValue === "1"
                                ) {
                                    field.visibility = isEnabled;
                                    field.validationRules = field.validationRules || {};
                                    field.validationRules.required = isEnabled;
                                    if (!isEnabled) field.value = "";
                                }
                                else if (
                                    (field.name === "Start_Date1" || field.name === "End_Date2") &&
                                    selectedValue === "2"
                                ) {
                                    field.visibility = isEnabled;
                                    field.validationRules = field.validationRules || {};
                                    field.validationRules.required = isEnabled;
                                    if (!isEnabled) field.value = "";
                                }
                                else if (
                                    field.name === "Start_Time" ||
                                    field.name === "End_Time" ||
                                    field.name === "Occurs_Every"
                                ) {
                                    field.visibility = isEnabled;
                                    field.validationRules = field.validationRules || {};
                                    field.validationRules.required = isEnabled;
                                    if (!isEnabled) field.value = "";
                                }
                                else {
                                    field.visibility = false;
                                    if (field.validationRules) field.validationRules.required = false;
                                    field.value = "";
                                }
                            }
                        });
                    }
                });

                vm.$refs.refAddress.UpdateKeyValue();
            },
            setEndMinDate: function (event, field) {
                var vm = this;
                // Set minimum date for EndDate based on StartDate
                if (field.value) {
                    vm.$refs.refAddress.updateProperty("EndDate", "config.minDate", field.value);
                }
                vm.$refs.refAddress.UpdateKeyValue();
            },
            setScEndMinDate: function (event, field) {
                var vm = this;
                // Set minimum date for End_Date2 based on Start_Date1
                if (field.value) {
                    vm.$refs.refAddress.updateProperty("End_Date2", "config.minDate", field.value);
                }
                vm.$refs.refAddress.UpdateKeyValue();
            },
            setEndTime: function (event, field) {
                var vm = this;
                // Set minimum time for End_Time based on Start_Time
                if (field.value) {
                    vm.$refs.refAddress.updateProperty("End_Time", "config.minDate", field.value);
                }
                vm.$refs.refAddress.UpdateKeyValue();
            },
            setEndTimeAds: function (event, field) {
                var vm = this;
                // Set minimum time for End_Time based on Start_Time (alternative method)
                if (field.value) {
                    vm.$refs.refAddress.updateProperty("End_Time", "config.minDate", field.value);
                }
                vm.$refs.refAddress.UpdateKeyValue();
            },
            getCycleRange(len, start) {
                if (!start || len <= 0) return "";
                const idx = this.months.indexOf(start);
                if (idx === -1) return "";
                const list = [];
                for (let i = 0; i < len; i++) {
                    list.push(this.months[(idx + i) % 12]);
                }
                return list.join("  ");
            },
            gethelfYearCycleRange() {
                const month = this.form.customQuarterStartMonth;
                const index = this.months.indexOf(month);
                const nextMonths = [
                    this.months[index % 12],
                    this.months[(index + 1) % 12],
                    this.months[(index + 2) % 12],
                ];
                return `${nextMonths.join("  ")}`;
            },
            async onSubmit() {
                var vm = this;
                const result = await vm.$refs.refAddress.validateForm.validate();
                if (result) {
                    let isValid = true;
                    vm.termRows.forEach(row => {
                        if (row.percentage == null || row.percentage == "") {
                            row.inpError = true;
                            isValid = false;
                        }
                        else {
                            row.inpError = false;
                        }
                        if (row.days == null || row.days == "") {
                            row.daysError = true;
                            isValid = false;
                        }
                        else {
                            row.daysError = false;
                        }
                    });
                    if (!isValid) {
                        return;
                    }

                    var getvalue = '';
                    switch (vm.selectedFrequencyTRype) {
                        case "month":
                            getvalue = vm.form.monthlyBillDate;
                            break;
                        case "week":
                            getvalue = vm.form.weeklyDay;
                            break;
                        case "biweek":
                            getvalue = vm.form.biweeklyDay;
                            break;
                        case "quarterly":
                            getvalue = vm.form.quarterCycle;
                            break;
                        case "halfyear":
                            getvalue = vm.form.halfYearCycle;
                            break;
                        case "bymonthly":
                            getvalue = vm.form.byMonthly;
                            break;
                        case "custom":
                            getvalue = "custom";
                            break;
                    }
                    var billdate = '';
                    var custom_month = '';
                    if (vm.form.BillGenerationDate != '') {
                        billdate = vm.form.BillGenerationDate;
                    }
                    if (vm.form.customQuarterStartMonth != '') {
                        custom_month = vm.form.customQuarterStartMonth;
                    }

                    let genrate_single_invoice = vm.$refs.refAddress.getPropertyValue("GenerateSingleInvoice", "value");
                    if (genrate_single_invoice == '1') {
                        genrate_single_invoice = true;
                    } else {
                        genrate_single_invoice = false;
                    }

                    // Handle UTILITY_BILLING specific fields when GenerateBill === '1'
                    let isRecurring = 0;
                    let scStartDate = null;
                    let scEndDate = null;
                    let scStartTime = '';
                    let scEndTime = '';
                    let occursEvery = 0;
                    let scBillingStartDay = null;
                    let scBillingEndDay = null;
                    let readingCycleStartDay = null;
                    let readingCycleEndDay = null;
                    let startDate = null;
                    let endDate = null;

                    if (vm.callfrom === 'UTILITY_BILLING' && vm.$refs.refAddress.getPropertyValue("GenerateBill", "value") === 1) {
                        let isRecurringRaw = vm.$refs.refAddress.getPropertyValue("IsRecurring", "value");
                        if (Array.isArray(isRecurringRaw)) {
                            isRecurringRaw = isRecurringRaw[0];
                        }
                        isRecurring = (isRecurringRaw === "1") ? 1 : 0;

                        if (isRecurring === 1) {
                            // Recurring billing - get meter reading dates
                            readingCycleStartDay = vm.$refs.refAddress.getPropertyValue("MeterReadingStartDate", "value");
                            readingCycleEndDay = vm.$refs.refAddress.getPropertyValue("MeterReadingEndDate", "value");

                            // Handle advance settings if enabled
                            let advanceSettings = true;
                            if (advanceSettings) {
                                scBillingStartDay = vm.$refs.refAddress.getPropertyValue("MeterReadingStartDate1", "value");
                                scBillingEndDay = vm.$refs.refAddress.getPropertyValue("MeterReadingEndDate2", "value");

                                let startTimeValue = vm.$refs.refAddress.getPropertyValue("Start_Time", "value");
                                let endTimeValue = vm.$refs.refAddress.getPropertyValue("End_Time", "value");

                                // Extract only time part from the datetime string returned by time fields - same as CreateBatch.vue
                                scStartTime = startTimeValue ? startTimeValue.toString().match(/(\d{1,2}:\d{2}:\d{2})/) ? startTimeValue.toString().match(/(\d{1,2}:\d{2}:\d{2})/)[1] : startTimeValue : '';
                                scEndTime = endTimeValue ? endTimeValue.toString().match(/(\d{1,2}:\d{2}:\d{2})/) ? endTimeValue.toString().match(/(\d{1,2}:\d{2}:\d{2})/)[1] : endTimeValue : '';
                                occursEvery = vm.$refs.refAddress.getPropertyValue("Occurs_Every", "value");
                            }
                        } else {
                            // One-time billing - get date range
                            startDate = vm.$refs.refAddress.getPropertyValue("StartDate", "value");
                            endDate = vm.$refs.refAddress.getPropertyValue("EndDate", "value");

                            // Handle advance settings if enabled
                            let advanceSettings = true;
                            if (advanceSettings) {
                                scStartDate = vm.$refs.refAddress.getPropertyValue("Start_Date1", "value");
                                scEndDate = vm.$refs.refAddress.getPropertyValue("End_Date2", "value");

                                let startTimeValue = vm.$refs.refAddress.getPropertyValue("Start_Time", "value");
                                let endTimeValue = vm.$refs.refAddress.getPropertyValue("End_Time", "value");

                                // Extract only time part from the datetime string returned by time fields - same as CreateBatch.vue
                                scStartTime = startTimeValue ? startTimeValue.toString().match(/(\d{1,2}:\d{2}:\d{2})/) ? startTimeValue.toString().match(/(\d{1,2}:\d{2}:\d{2})/)[1] : startTimeValue : '';
                                scEndTime = endTimeValue ? endTimeValue.toString().match(/(\d{1,2}:\d{2}:\d{2})/) ? endTimeValue.toString().match(/(\d{1,2}:\d{2}:\d{2})/)[1] : endTimeValue : '';
                                occursEvery = vm.$refs.refAddress.getPropertyValue("Occurs_Every", "value");
                            }
                        }
                    }

                    const dataPaymentTerm = [];
                    if (this.paymentType == 'One Time') {
                        if (vm.termRows.length > 0) {
                            vm.termRows.forEach(row => {
                                dataPaymentTerm.push({
                                    days: row.days,
                                    percentage: row.percentage
                                });
                            });
                        }
                    }

                    var obj = {
                        id: this.BillData.length > 0 ? this.BillData[0].id : 0,
                        planId: this.planId,
                        invoice_generation_date: billdate,
                        days_until_due: vm.$refs.refAddress.getPropertyValue("DaysUntilDue", "value"),
                        late_fees_applicable: vm.$refs.refAddress.getPropertyValue("LateFeesApplicable", "value"),
                        late_fee_type: vm.$refs.refAddress.getPropertyValue("LateFeeType", "value"),
                        late_fee_amount: vm.$refs.refAddress.getPropertyValue("LateFeePercentage", "value"),
                        bill_generate_on: vm.$refs.refAddress.getPropertyValue("GenerateBill", "value"),
                        when_reading_not_available: vm.$refs.refAddress.getPropertyValue("ReadingNotAvailable", "value"),
                        billing_month_depend_on: vm.$refs.refAddress.getPropertyValue("BillingMonth", "value"),
                        average_billing_month: vm.$refs.refAddress.getPropertyValue("AverageNumberOfMonth", "value"),
                        billing_frequency: vm.$refs.refAddress.getPropertyValue("BillingFrequency", "value"),
                        payment_term: this.paymentType == 'One Time' ? JSON.stringify(dataPaymentTerm) : null,
                        no_of_terms: this.paymentType == 'One Time' ? vm.$refs.refAddress.getPropertyValue("NoOfTerms", "value") : null,
                        billing_frequency_type: vm.callfrom === 'UTILITY_BILLING' ?vm.$refs.refAddress.getPropertyValue("UtilityBillingFrequencyType", "value"):vm.$refs.refAddress.getPropertyValue("BillingFrequencyType", "value"),
                        billing_dunning: vm.$refs.refAddress.getPropertyValue("Dunning", "value"),
                        Custom_range: getvalue,
                        custom_start_month: custom_month,
                        custom_months_count: vm.form.customMonthsCount,
                        genrate_single_invoice: genrate_single_invoice,
                        // UTILITY_BILLING specific fields
                        is_recurring: isRecurring,
                        sc_start_date: scStartDate,
                        sc_end_date: scEndDate,
                        sc_start_time: scStartTime,
                        sc_end_time: scEndTime,
                        occurs_every: occursEvery,
                        sc_billing_start_day: scBillingStartDay,
                        sc_billing_end_day: scBillingEndDay,
                        reading_cycle_start_day: readingCycleStartDay,
                        reading_cycle_end_day: readingCycleEndDay,
                        start_date: startDate,
                        end_date: endDate,
                        billing_frequency_value: getvalue
                    }
                    var obj1 = JSON.stringify(obj);
                    let param = `objData=${obj1}`

                    await DataService.SaveBillingCycle(param).then(response => {
                        debugger;
                        if (response.data != null) {
                            if (response.data.Code == 1) {
                                vm.ShowAlert(vm.$t('Bill cycle saved successfully.'), "success");
                                vm.resetFrom();
                                var palne = parseInt(vm.$route.params.id)
                                vm.GetBillCycleDatapalne(palne);
                            }
                            else if (response.data.Code == 2) {
                                vm.ShowAlert(vm.$t('Bill cycle updated successfully.'), "success");
                            }
                            else {
                                vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                            }
                        }
                    })
                }
            },
            resetFrom() {
                return {
                    BillingFrequency: "",
                    monthlyBillDate: "",
                    weeklyDay: "",
                    biweeklyDay: "",
                    quarterCycle: "",
                    quarterlyBillDate: "",
                    halfYearlyBillDate: "",
                    customQuarterStartMonth: "",
                };
            },

        }
    }
</script>

<style>
#BillingFrequencyType {
    -ms-flex: 0 0 100%;
    flex: 0 0 100%;
    max-width: 100%;
}
</style>

