<template>
  <section>
    <div class="my-popups">
      <loader :is-visible="isLoading"></loader>
      <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
          <div class="modal-content">
            <div class="modal-header theme-primary partition-full d-flex">
              <h5 class="modal-title">Peak Hours</h5>
              <button type="button" class="close" v-on:click="Close">
                <em class="fa fa-times" aria-hidden="true"></em>
              </button>
            </div>

            <div class="modal-body">
              <dynamic-form lang="en" ref="validatePeakHours" :buttons="buttons" :schema="FormSchema">
                <template v-slot:tgslot-FromDateMonth="{ data }">
                  <div class="form-group mb-0">
                    <select v-model="selectedDay" class="form-control" style="display:inline-block;width: 120px;">
                      <option v-for="day in daysInMonth" :key="day" :value="day">{{ day }}</option>
                    </select>
                    <select v-model="selectedMonth" class="form-control ml-3" style="display:inline-block;width: 120px;"
                      @change="adjustDays">
                      <option v-for="(month, index) in monthNames" :key="index" :value="index + 1">{{ month }}</option>
                    </select>
                  </div>
                </template>
                <template v-slot:tgslot-TillDateMonth="{ data }">
                  <div class="form-group mb-0">
                    <select v-model="selectedEndDay" class="form-control" style="display:inline-block;width: 120px;">
                      <option v-for="day in daysEndInMonth" :key="day" :value="day">{{ day }}</option>
                    </select>
                    <select v-model="selectedEndMonth" class="form-control ml-3"
                      style="display:inline-block;width: 120px;" @change="adjustEndDays">
                      <option v-for="(month, index) in monthEndNames" :key="index" :value="index + 1">{{ month }}</option>
                    </select>

                  </div>
                </template>
                <template v-slot:tgslot-DurationLayout="{ data }">
                  <div class="row" v-for="(item, index) in Mainleveldiv" :key="index">
                    <div class="col-md-2">
                      <label for="start-time">Start Time:</label>
                      <div class="input-group">
                        <input type="time" class="form-control" v-model="item.startTime"
                          @change="calculateDuration(item)" />
                      </div>
                    </div>
                    <div class="col-md-2">
                      <label for="end-time">End Time:</label>
                      <div class="input-group">
                        <input type="time" class="form-control" v-model="item.endTime"
                          @change="calculateDuration(item)" />
                        <span style="color:red;font-size: 14px;" v-if="item.isErrorDurationValid">{{ item.durationError
                          }}</span>
                      </div>
                    </div>
                    <div class="col-md-2">
                      <label for="duration">Duration:</label>
                      <input type="text" class="form-control" id="duration" v-model="item.duration" readonly />
                      <input type="text" class="form-control d-none" id="durationInSeconds"
                        v-model="item.durationInSeconds" readonly />
                    </div>
                    <div class="col-md-2">
                      <label for="is-per-unit">Is Per Unit:</label>
                      <div class="form-group mb-0">
                        <label class="switch">
                          <input class="form-check-input custom-control-input dynamic" :checked="item.isPerUnit"
                            type="checkbox" @change="checkIsPerUnit($event, index)" />
                          <span class="slider round" style="cursor:default;">
                            <span class="slider-yes">{{ $t('Yes') }}</span>
                            <span class="slider-no">{{ $t('No') }}</span>
                          </span>
                        </label>
                      </div>
                    </div>

                    <div class="col-md-2" v-if="item.isPerUnit">
                      <label for="unit-rate">Unit Rate:</label>
                      <div class="input-group">
                        <input type="number" class="form-control" id="unit-rate" v-model="item.unitRate" />
                      </div>
                    </div>

                    <div class="col-md-2" v-else>
                      <label for="fixed-rate">Fixed Rate:</label>
                      <div class="input-group">
                        <input type="number" class="form-control" id="fixed-rate" v-model="item.fixedRate" />
                      </div>
                    </div>
                    <div class="col-md-2">
                      <label for="action"></label>
                      <div class="input-group">
                        <em class="round-icon-small fa fa-plus bg-success float-none" v-if="index == 0"
                          @click="addRow"></em>
                        <em class="round-icon-small fa fa-minus bg-danger float-none" v-if="index != 0"
                          @click="deleteRow(index)"></em>
                      </div>
                    </div>
                  </div>
                </template>
              </dynamic-form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import DataService from "../../services/DataService";

export default {
  props: {
    peakRateData: {
      type: Function,
      required: false
    },
    refCode: {
      type: String,
      required: false
    },
    currentEditIndex: {
      type: Number,
      required: false
    }
  },
  data() {
    return {
      isLoading: false,
      monthNames: ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"],
      monthEndNames: ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"],
      weekDaysArry: [{ name: "Sun", value: "0" }, { name: "Mon", value: "1" }, { name: "Tue", value: "2" }, { name: "Wed", value: "3" }, { name: "Thu", value: "4" }, { name: "Fri", value: "5" },
      { name: "Sat", value: "6" }],
      monthsArray: [{ name: "Jan", value: "1" }, { name: "Feb", value: "2" }, { name: "Mar", value: "3" }, { name: "Apr", value: "4" },
      { name: "May", value: "5" }, { name: "Jun", value: "6" }, { name: "Jul", value: "7" }, { name: "Aug", value: "8" },
      { name: "Sep", value: "9" }, { name: "Oct", value: "10" }, { name: "Nov", value: "11" }, { name: "Dec", value: "12" }],
      selectedMonth: new Date().getMonth(),
      selectedDay: new Date().getDate(),
      selectedEndMonth: new Date().getMonth(),
      selectedEndDay: new Date().getDate(),
      Mainleveldiv: [
        {
          startTime: '',
          endTime: '',
          duration: '',
          unitRate: '',
          fixedRate: '',
          isPerUnit: false,
          durationError: '',
          durationInSeconds: '',
          isErrorDurationValid: false
        }
      ],
      // isErrorDurationValid: false,
      daysInMonth: [],
      daysEndInMonth: [],
      FormSchema: [
        {
          layoutType: "single",
          Data: [
            {
              astype: "RadioField",
              label: this.$t("Select Type"),
              name: "Select",
              value: "1",
              placeholder: "",
              config: {
                options: [
                  {
                    name: "Day",
                    value: "1",
                  },
                  {
                    name: "Month",
                    value: "2",
                  },
                  {
                    name: "Dates",
                    value: "3",
                  },
                  {
                    name: "Month with dates",
                    value: "4",
                  },
                ],
                onChange: this.changeRadioFieldValue,
              },
              validationRules: { required: true },
            },
          ],
        },
        {
          layoutType: "single",
          Data: [
            {
              astype: "CheckBoxField",
              label: "Select Options",
              name: "SelectedOption",
              value: "",
              mode: "tag",
              placeholder: "",
              visibility: true,
              config: {
                options: [],
              },
              validationRules: "",
            },
          ],
        },
        {
          layoutType: "four",
          Data: [
            {
              astype: "SlotField",
              label: "From Date Month",
              name: "FromDateMonth",
              value: "",
              placeholder: "",
              visibility: false,
              validationRules: "",
              settings: {
                isInSlot: true
              }
            },
            {
              astype: "SlotField",
              label: "Till Date Month",
              name: "TillDateMonth",
              value: "",
              placeholder: "",
              visibility: false,
              validationRules: "",
              settings: {
                isInSlot: true
              }
            }
          ],
        },
        {
          layoutType: "single",
          group_name: 'Duration',
          visibility: false,
          Data: [
            {
              astype: "SlotField",
              label: "",
              name: "DurationLayout",
              value: "",
              visibility: false,
              placeholder: "",
              config: {
                options: [],
              },
              validationRules: "",
            },
          ],
        },
      ],
      buttons: [
        {
          type: "submit",
          class: "btn btn-success save",
          value: "Y",
          text: "<i class='fa fa-save pr-2'></i>Save",
          onClick: this.SaveData,
        },
        {
          type: "button",
          class: "btn btn-danger",
          text: "<i class='fa fa-close pr-2'></i>Cancel",
          onClick: this.Close,
        },
      ],
    };
  },
  created: async function () {
    var vm = this;
    vm.adjustDays();
    vm.adjustEndDays();
    if (vm.refCode == 'Update') {
      setTimeout(() => { this.BindPeakRateDetail(vm.peakRateData); }, 500);
    }
    vm.FormSchema[3].Data[0].visibility = true;
    vm.FormSchema[1].Data[0].config.options = vm.weekDaysArry;
  },
  methods: {
    BindPeakRateDetail(editDetail) {
      var vm = this;
      if (editDetail.fromStartDay !== null && editDetail.fromStartMonth !== null) {
        vm.selectedDay = editDetail.fromStartDay;
        vm.selectedMonth = editDetail.fromStartMonth;
      }
      if (editDetail.tillEndDay !== null && editDetail.tillEndMonth !== null) {
        vm.selectedEndDay = editDetail.tillEndDay;
        vm.selectedEndMonth = editDetail.tillEndMonth;
      }
      vm.Mainleveldiv = editDetail.peakHours.map(item => ({
        startTime: item.startTime,
        endTime: item.endTime,
        duration: item.durationInSeconds == null ? "" : vm.formattedDuration(item.durationInSeconds),
        durationInSeconds: item.durationInSeconds,
        unitRate: item.isPerUnit ? item.peakRate : null, // Set unit rate if per unit
        fixedRate: item.isPerUnit ? null : item.peakRate, // Set fixed rate if not per unit
        isPerUnit: item.isPerUnit,
        durationError: '',
        isErrorDurationValid: false
      }));
      const selectValueData = editDetail.selectValue;
      const selectedOptions = editDetail.selectedOptions ? editDetail.selectedOptions.split(',') : [];
      vm.$refs.validatePeakHours.updateProperty("Select", "value", selectValueData);
      if (selectValueData === "3") {
        vm.$refs.validatePeakHours.updateProperty("SelectedOption", "astype", "MultiSelectField");
        var datesArrayList = Array.from({ length: 30 }, (_, i) => ({ name: (i + 1).toString(), value: (i + 1).toString(), }));
        vm.$refs.validatePeakHours.updateProperty("SelectedOption", "config.options", datesArrayList);
        var bindDatesValue = [];
        selectedOptions.forEach((item, index) => {
          bindDatesValue.push({
            value: item,
            label: item
          })
        });
        vm.$refs.validatePeakHours.updateProperty("SelectedOption", "value", bindDatesValue);
      }
      else if (selectValueData === "2") {
        vm.$refs.validatePeakHours.updateProperty("SelectedOption", "config.options", vm.monthsArray);
        vm.$refs.validatePeakHours.updateProperty("SelectedOption", "value", selectedOptions);
      }
      else if (selectValueData === "4") {
        vm.$refs.validatePeakHours.updateProperty("SelectedOption", "visibility", false);
        vm.$refs.validatePeakHours.updateProperty("FromDateMonth", "visibility", true);
        vm.$refs.validatePeakHours.updateProperty("TillDateMonth", "visibility", true);
      }
      else {
        vm.$refs.validatePeakHours.updateProperty("SelectedOption", "value", selectedOptions);
      }
      vm.$refs.validatePeakHours.UpdateKeyValue();
    },

    addRow() {
      this.Mainleveldiv.push({
        startTime: '',
        endTime: '',
        duration: '',
        durationInSeconds: '',
        unitRate: '',
        fixedRate: '',
        isPerUnit: false, // or whatever default values you need
        durationError: '',
        isErrorDurationValid: false
      });
    },
    deleteRow(index) {
      this.Mainleveldiv.splice(index, 1);
    },
    SaveData() {
      const selectValueData = this.$refs.validatePeakHours.getPropertyValue("Select", "value");
      let selectedOptionValue = this.$refs.validatePeakHours.getPropertyValue("SelectedOption", "value");
      if (selectValueData == "3") {
        selectedOptionValue = selectedOptionValue.map(option => option.label).join(', ');
      }
      else if (selectValueData != "4") {
        selectedOptionValue = selectedOptionValue.join(",");
      }
      else {
        selectedOptionValue = null;
      }
      const formData = {
        serviceId: 0,
        selectValue: this.$refs.validatePeakHours.getPropertyValue("Select", "value"),
        selectedOptions: selectedOptionValue,
        fromStartDay: selectValueData == "4" ? this.selectedDay : null,
        fromStartMonth: selectValueData == "4" ? this.selectedMonth : null,
        tillEndDay: selectValueData == "4" ? this.selectedEndDay : null,
        tillEndMonth: selectValueData == "4" ? this.selectedEndMonth : null,
        peakHours: this.Mainleveldiv.map(item => ({
          startTime: item.startTime,
          endTime: item.endTime,
          duration: item.duration,
          durationInSeconds: item.durationInSeconds,
          peakRate: item.isPerUnit == false ? item.fixedRate : item.unitRate,
          isPerUnit: item.isPerUnit,
        })),
      };
      if (this.currentEditIndex !== null && this.refCode == 'Update') {
        this.$emit('updatePeakHoursDetail', this.currentEditIndex, formData);
      } else {
        this.$emit('peakHoursDetail', formData);
      }
    },
    checkIsPerUnit(event, index) {
      this.Mainleveldiv[index].isPerUnit = event.target.checked;
    },

    adjustDays() {
      const month = this.selectedMonth + 1;
      const year = new Date().getFullYear();
      const days = new Date(year, month, 0).getDate();
      this.daysInMonth = Array.from({ length: days }, (_, i) => i + 1);

      if (this.selectedDay > days) {
        this.selectedDay = days;
      }
    },
    adjustEndDays() {
      const month = this.selectedEndMonth + 1;
      const year = new Date().getFullYear();
      const days = new Date(year, month, 0).getDate();
      this.daysEndInMonth = Array.from({ length: days }, (_, i) => i + 1);

      if (this.selectedEndDay > days) {
        this.selectedEndDay = days;
      }
    },
    changeRadioFieldValue(event, y) {
      var vm = this;
      const selectedValue = y.value;
      vm.$refs.validatePeakHours.updateProperty("SelectedOption", "visibility", true);
      vm.$refs.validatePeakHours.updateProperty("FromDateMonth", "visibility", false);
      vm.$refs.validatePeakHours.updateProperty("TillDateMonth", "visibility", false);
      vm.$refs.validatePeakHours.updateProperty("SelectedOption", "value", "");
      vm.$refs.validatePeakHours.updateProperty("SelectedOption", "astype", "CheckBoxField");
      if (selectedValue === "1") {
        vm.$refs.validatePeakHours.updateProperty("SelectedOption", "config.options", vm.weekDaysArry);
        vm.$refs.validatePeakHours.UpdateKeyValue();
      } else if (selectedValue === "2") {
        vm.$refs.validatePeakHours.updateProperty("SelectedOption", "config.options", vm.monthsArray);
        vm.$refs.validatePeakHours.UpdateKeyValue();
      } else if (selectedValue === "3") {
        vm.$refs.validatePeakHours.updateProperty("SelectedOption", "astype", "MultiSelectField");
        var datesArrayList = Array.from({ length: 30 }, (_, i) => ({ name: (i + 1).toString(), value: (i + 1).toString(), }));
        vm.$refs.validatePeakHours.updateProperty("SelectedOption", "config.options", datesArrayList);
        vm.$refs.validatePeakHours.UpdateKeyValue();

      }
      else if (selectedValue === "4") {
        vm.$refs.validatePeakHours.updateProperty("SelectedOption", "visibility", false);
        vm.$refs.validatePeakHours.updateProperty("FromDateMonth", "visibility", true);
        vm.$refs.validatePeakHours.updateProperty("TillDateMonth", "visibility", true);
        vm.$refs.validatePeakHours.UpdateKeyValue();
      }
    },
    Close: function () {
      var vueObj = this;
      vueObj.$parent.isAddPeakHours = false;
    },
    formattedDuration(durationInSecond) {
      const totalMinutes = Math.floor(durationInSecond / 60);
      const hours = Math.floor(totalMinutes / 60);
      const minutes = totalMinutes % 60;
      return `${hours} hours and ${minutes} minutes`;
    },
    calculateDuration(item) {
      if (item.startTime && item.endTime) {
        const start = this.parseTime(item.startTime);
        const end = this.parseTime(item.endTime);
        if (end >= start) {
          item.durationInSeconds = (end - start) / 1000;
          const durationInMinutes = (end - start) / (1000 * 60);
          const hours = Math.floor(durationInMinutes / 60);
          const minutes = durationInMinutes % 60;
          item.duration = `${hours} hours and ${minutes} minutes`;
          item.isErrorDurationValid = false;
        } else {
          item.isErrorDurationValid = true;
          item.durationError = 'End time must be after start time';
          item.duration = '';
          item.durationInSeconds = '';
        }
      } else {
        item.isErrorDurationValid = false;
        item.duration = '';
        item.durationInSeconds = '';
        item.durationError = '';
      }
    },
    parseTime(time) {
      const [hours, minutes] = time.split(':').map(Number);
      const date = new Date();
      date.setHours(hours);
      date.setMinutes(minutes);
      date.setSeconds(0);
      return date;
    }
  },
};
</script>