<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <!--  Modal Header -->
                        <div class="modal-header ui-draggable-handle py-1">
                            <h5 name="header" class="pt-2">
                                Select Conditions
                            </h5>
                            <button class="close" @click="ClosePopUp">
                                <em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                        </div>

                        <!--  Modal Body -->
                        <div class="modal-body">
                            <!--  Search Bar -->
                            <div class="mb-3 position-relative">
                                <em class="fa fa-search position-absolute text-muted"
                                    style="left: 10px; top: 50%; transform: translateY(-50%);"></em>
                                <input type="text" class="form-control pl-4" placeholder="Search conditions"
                                    v-model="searchQuery" />
                            </div>

                            <!--  Scrollable List -->
                            <ul class="list-group conditions-list mb-3">
                                <li v-for="(condition, index) in filteredConditions" :key="index"
                                    class="list-group-item condition-item" :class="{
                                        active: selectedCondition && selectedCondition.SERVICEID === condition.SERVICEID,
                                        disabled: isAlreadyAdded(condition.SERVICEID)
                                    }" @click="!isAlreadyAdded(condition.SERVICEID) && selectCondition(condition)">
                                    <strong>{{ condition.SERVICENAME }}</strong>
                                    <br />
                                    <small class="text-muted">{{ condition.CONDITION }}</small>
                                    <span v-if="isAlreadyAdded(condition.SERVICEID)"
                                        class="badge badge-light float-right font-weight-medium p-2">
                                        Added
                                    </span>
                                </li>

                                <li v-if="filteredConditions.length === 0"
                                    class="list-group-item text-muted text-center">
                                    No conditions found
                                </li>
                            </ul>
                        </div>

                        <!--  Modal Footer (Buttons) -->
                        <div class="modal-footer py-2">
                            <button type="button" class="btn btn-secondary btn-sm" @click="ClosePopUp">
                                Cancel
                            </button>

                            <button type="button" class="btn btn-primary btn-sm" :disabled="!selectedCondition"
                                @click="applySelectedCondition">
                                Apply Selected Condition
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from "../../services/DataService";

export default {
    props: {
        existingConditions: {
            type: Array,
            default: () => []
        }
    },
    data() {
        return {
            Searchvalue: "",
            ConditionsData: [],
            PageSize: 1000,
            PageNumber: 1,
            SortBy: "",
            SortExp: "",
            TotalRecords: 0,
            searchQuery: "",
            selectedCondition: null
        };
    },
    computed: {
        // filteredConditions() {
        //     if (!this.searchQuery) return this.ConditionsData;
        //     const query = this.searchQuery.toLowerCase();
        //     return this.ConditionsData.filter(
        //         item =>
        //             item.SERVICENAME &&
        //             item.SERVICENAME.toLowerCase().includes(query)
        //     );
        // }
        filteredConditions() {
            const query = this.searchQuery.toLowerCase();
            return this.ConditionsData
                //  Exclude already added conditions for this subsidy
                .filter(
                    item =>
                        !this.existingConditions.some(
                            cond => cond.conditionId === item.SERVICEID
                        )
                )
                //  Apply search filter
                .filter(
                    item =>
                        !this.searchQuery ||
                        (item.SERVICENAME &&
                            item.SERVICENAME.toLowerCase().includes(query))
                );
        }
    },
    created() {
        this.getConditionRecords();
    },
    methods: {
        ClosePopUp() {
            this.$parent.IsShowCondition = false;
        },

        selectCondition(condition) {
            this.selectedCondition = condition;
        },
        isAlreadyAdded(serviceId) {
            return this.existingConditions.some(cond => cond.conditionId === serviceId);
        },
        applySelectedCondition() {
            if (this.selectedCondition) {
                // Emit the selected condition to parent
                this.$emit('condition-selected', this.selectedCondition);
                this.ClosePopUp(); // close modal after selection
            }
        },

        getConditionRecords() {
            debugger
            const vm = this;

            const url = `serviceName=&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&setupId=`
            //const url = `pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&servicename=${vm.Searchvalue}`;
            DataService.GetBillServiceListing(url).then(response => {
                if (response && response.data && response.data.length > 0) {
                    vm.ConditionsData = response.data;
                    vm.TotalRecords =
                        response.data[0].TOTALRECORDS || response.data.length;
                } else {
                    vm.ConditionsData = [];
                    vm.TotalRecords = 0;
                }
            });
        }
    }
};
</script>

<style scoped>
.form-control.pl-4 {
    padding-left: 2rem !important;
}

/* Scrollable list */
.conditions-list {
    max-height: 500px;
    overflow-y: auto;
    border: 1px solid #ddd;
    border-radius: 4px;
}

/* Scrollbar styling */
.conditions-list::-webkit-scrollbar {
    width: 6px;
}

.conditions-list::-webkit-scrollbar-thumb {
    background-color: #bbb;
    border-radius: 3px;
}

.conditions-list::-webkit-scrollbar-thumb:hover {
    background-color: #888;
}

/* Highlight selected item */
.condition-item {
    cursor: pointer;
}

.condition-item.active {
    background-color: #007bff;
    color: #fff;
}

.condition-item.active small {
    color: #e9ecef;
}

/* Footer button spacing */
.modal-footer {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
}
.condition-item.disabled {
    background-color: #f8f9fa;
    color: #999;
    pointer-events: none;
    opacity: 0.6;
}
.condition-item.active small.text-muted{color:#fff!important;}
</style>
