<template>
    <section>
        <div class="col-md-12 border p-3">
            <div class="col-md-12 p-0">
                <dynamic-form lang="en" ref="refAddress" :buttons="false" :schema="FormSchema">

                    <!-- <template v-slot:tgslot-EMIDurationOptions="{ data }">

                      

                        <input type="checkbox" id="3Months" @change="OnChangeEMIDurationOptions($event)" value="1" v-model="checkedNames" /> &nbsp;
                        <label for="jack"> 3 Months</label>
                        &nbsp;
                        <input type="checkbox" id="6Months" @change="OnChangeEMIDurationOptions($event)" value="2" v-model="checkedNames" />  &nbsp;
                        <label for="john"> 6 Months</label>
                        &nbsp;
                        <input type="checkbox" id="12Months" @change="OnChangeEMIDurationOptions($event)" value="3" v-model="checkedNames" /> &nbsp;
                        <label for="mike"> 12 Months</label>
                        </template> -->
                    <template v-slot:tgslot-NextBillCycle="{ data }">



                        <label for="one" class="mr-2"><input type="radio"
                                @change="OnChangeIncludeUnpaidEMIinNextBill($event)" id="1" value="1"
                                v-model="NextBillpicked" /> {{ $t('Yes') }}</label>
                        <label for="two" class="mr-2"><input type="radio"
                                @change="OnChangeIncludeUnpaidEMIinNextBill($event)" id="0" value="0"
                                v-model="NextBillpicked" /> {{ $t('No') }}</label>


                        <div id="impactYes" class="impact" style="display: block;">
                            <strong>{{ $t('ImpactofSelecting') }} {{ $t(isUnpaidEMINextBill) }}:</strong>
                            <ul>
                                <li>{{ isUnpaidEMINextBill == 'Yes' ? $t('Theunpaidinstallmenamount') :
                                    $t('Theunpaidinstallmentamounwillnotbeincludedinyounextbill') }}</li>
                                <li>{{ isUnpaidEMINextBill == 'Yes' ?
                                    $t('Thismayincreaseyourtotalbillamountsignificantly') :
                                    $t('Youwillneedtopayanyunpaidinstallmentsseparately') }}</li>
                                <li>{{ isUnpaidEMINextBill == 'Yes' ?
                                    $t('Youwillreceivenotificationsabouttheupcomingpaymentdue') :
                                    $t('Thishelpsyoukeeptrackofyourcurrentchargesmoreclearly') }}
                                </li>
                            </ul>
                        </div>
                    </template>


                </dynamic-form>

            </div>
            <div class="row">
                <div class="col-md-12">
                    <div class="row flex-row-reverse my-3 ">
                        <div class="col-lg-6 text-right">
                            <a @click="onSubmit" class="btn btn-success" title="" id="addAddress"
                                data-toggle-tooltip="tooltip" data-original-title="Save">
                                <i class="fa fa-save mr-2"></i>
                                <span id="btnAddressText"> {{ $t("Save") }}</span></a>


                        </div>
                        <div class="col-lg-6">
                            <div class="py-2">
                                <span class="mandatory">{{ $t("Mandatory") }}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
export default
    {
        data() {
            return {
                isUnpaidEMINextBill: 'No',
                NextBillpicked: 0,
                checkedNames: [],
                FormSchema: [
                    {
                        layoutType: "double",
                        group_name: "installment Conversion Options",
                        Data: [
                            {
                                astype: "NumericField",
                                label: this.$t('MaximumConversionsPerYear'),
                                name: "Maximum Conversions per Year:",
                                value: null,
                                placeholder: "",
                                validationRules: {
                                    "required": true
                                },
                            },
                            {
                                astype: "NumericField",
                                label: this.$t('MinimumAmountforEMI'),
                                name: "Minimum Amount for installment ($):",
                                value: null,
                                placeholder: "",
                                validationRules: {
                                    "required": true
                                },
                            },
                            {
                                astype: "NumericField",
                                label: this.$t('MaximumAmountforEMI'),
                                name: "Maximum Amount for installment ($):",
                                value: "",
                                placeholder: "",

                            },
                            {
                                astype: "CheckBoxField",
                                label: this.$t('InstallmentDurationOptions'),
                                name: "EMIDurationOptions:",
                                value: "",
                                mode: "tag",
                                placeholder: "",
                                visibility: true,
                                config: {
                                    options: [{
                                        name: "3 " + this.$t('months'),
                                        value: "1",
                                    },
                                    {
                                        name: "6 " + this.$t('months'),
                                        value: "2",
                                    },
                                    {
                                        name: "12 " + this.$t('months'),
                                        value: "3",
                                    },
                                    ],
                                    onChange: this.OnChangeEMIDurationOptions
                                },
                                validationRules: "",
                            },
                            // {
                            //     astype: "SlotField",
                            //     label: "EMI Duration Options",
                            //     name: "EMIDurationOptions",
                            //     value: null,
                            //     placeholder: "",
                            //     visibility: true,

                            // },
                            {
                                astype: "NumericField",
                                label: 'Interest Rate for 3 Months (%)',
                                name: "Interest Rate for 3 Months (%)",
                                value: null,
                                placeholder: "",
                                visibility: false,

                            },
                            {
                                astype: "NumericField",
                                label: 'Interest Rate for 6 Months (%)',
                                name: "Interest Rate for 6 Months (%)",
                                value: null,
                                placeholder: "",
                                visibility: false,

                            },
                            {
                                astype: "NumericField",
                                label: 'Interest Rate for 12 Months (%)',
                                name: "Interest Rate for 12 Months (%)",
                                value: null,
                                placeholder: "",
                                visibility: false,

                            },

                            {
                                astype: "NumericField",
                                label: this.$t('ProcessingFee'),
                                name: "Processing Fee ($):",
                                value: null,
                                placeholder: "",

                            },
                        ]
                    },
                    {
                        layoutType: "single",
                        group_name: "NextBillingCycleIntegration",
                        Data: [

                            {
                                astype: "SlotField",
                                label: this.$t("IncludeUnpaidinstallmentinNextBill"),
                                name: "NextBillCycle",
                                value: "",
                                placeholder: "",
                                settings: {
                                    isInSlot: true
                                }

                            },

                        ]
                    },
                    /*{
                        layoutType: "double",
                        Data: [
                        
                       
                        {
                            astype: "CheckBoxField",
                            label: this.$t('NotificationSettings'),
                            name: "Notification Settings:",
                            value: "",
                            mode: "tag",
                            placeholder: "",
                            visibility: true,
                            config: {
                                options: [{
                                    name: "Upcoming Payment Reminders",
                                    value: "1",
                                },
                                {
                                    name: "Due Date Alerts",
                                    value: "2",
                                },
                               ],
                            },
                            validationRules: "",
                        },
                        {
                            astype: "CheckBoxField",
                            label: this.$t('UserDashboardOptions'),
                            name: "User Dashboard Options:",
                            value: "",
                            mode: "tag",
                            placeholder: "",
                            visibility: true,
                            config: {
                                options: [{
                                    name: "Show EMI Status",
                                    value: "1",
                                },
                                {
                                    name: "Show Remaining Balance",
                                    value: "2",
                                },
                               ],
                            },
                            validationRules: "",
                        },
                        ]
                    },
                    {
                        layoutType: "double",
                        group_name:"ACH Payment Option",
                        Data: [
                        {
                            astype: "RadioField",
                            label: this.$t('AutomaticPayments'),
                            name: "Automatic Payments:",
                            value: "0",
                            placeholder: "",
                            visibility: true,
                            config: {
                                    options: [{
                                        value: "1",
                                        name: "Yes"
                                    }, {
                                        value: "0",
                                        name: "No"
                                    }],
                                    onChange: this.onChangeAutomaticPayments
                                },
                             
                            
                        },
                        { 
                            astype: "SelectField",
                            label: this.$t('AccountType'),
                            name: "Account Type:",
                            value: "",
                            placeholder: "",
                            visibility: false,
                            config: {
                                options: [{name : 'checking', value:'1'}, {name : 'savings', value:'2'}, ],
                            },
                        },
                        { 
                            astype: "TextField",
                            label: this.$t('Bank Account Number'),
                            name: "Bank Account Number:",
                            value: "",
                            placeholder: "",
                            visibility: false,
                        },
                        { 
                            astype: "TextField",
                            label: this.$t('Routing Number'),
                            name: "Routing Number:",
                            value: "",
                            placeholder: "",
                            visibility: false,
                        },
                        {
                            astype: "CheckBoxField",
                            label: this.$t('Auto Pay for EMI'),
                            name: "Auto Pay for EMI:",
                            value: "",
                            mode: "tag",
                            placeholder: "",
                            visibility: false,
                            config: {
                                options: [{
                                    name: "Enable automatic payments for EMI",
                                    value: "1",
                                },
                                {
                                    name: "I accept the terms and conditions.",
                                    value: "2",
                                },
                               ],
                            },
                            validationRules: "",
                        },
                        ]
                    },*/

                ],


            }
        },
        created: function () {
            debugger
            this.planId = parseInt(this.$route.params.id)
            this.GetEMIConfigData(this.planId);
        },
        methods: {
            GetEMIConfigData: function (planId) {
                debugger
                const vm = this;
                const param = `planid=${planId}`;

                DataService.GetEMIOptionSettingData(param).then(response => {
                    debugger
                    const data = JSON.parse(response.data.result).DATA[0];
                    if (data) {
                        if (data.emi_duration_options.length > 0) {
                            // vm.checkedNames = [];
                            // Populate checkedNames based on EMI duration options
                            const emiDurationOptions = JSON.parse(data.emi_duration_options);
                            vm.FormSchema[0].Data[3].value = emiDurationOptions
                            //vm.checkedNames = emiDurationOptions; // Assuming emi_duration_options is an array of values (1, 2, 3)
                            // Update visibility and values based on EMI duration options
                            JSON.parse(data.emi_duration_options).forEach(itm => {
                                switch (itm) {
                                    case '1':
                                        vm.FormSchema[0].Data[4].visibility = true;
                                        vm.FormSchema[0].Data[4].value = data.interest_rate_for_three_months;
                                        break;
                                    case '2':
                                        vm.FormSchema[0].Data[5].visibility = true;
                                        vm.FormSchema[0].Data[5].value = data.interest_rate_for_six_months;
                                        break;
                                    case '3':
                                        vm.FormSchema[0].Data[6].visibility = true;
                                        vm.FormSchema[0].Data[6].value = data.interest_rate_for_twelve_months;
                                        break;
                                }
                            });
                        }
                        else {
                            vm.FormSchema[0].Data[4].visibility = false;
                            vm.FormSchema[0].Data[5].visibility = false;
                            vm.FormSchema[0].Data[6].visibility = false;
                        }
                        // Clear and set array fields to avoid pushing duplicates
                        vm.FormSchema[2].Data[0].value = [];
                        const upcomingReminders = data.upcoming_payment_reminders != null ? data.upcoming_payment_reminders.toString() : '';
                        const dueAlerts = data.due_date_alerts != null ? data.due_date_alerts.toString() : '';
                        vm.FormSchema[2].Data[0].value = [upcomingReminders, dueAlerts];

                        vm.FormSchema[2].Data[1].value = [];
                        const emiStatus = data.show_emi_status != null ? data.show_emi_status.toString() : '';
                        const remainingBalance = data.show_remaining_balance != null ? data.show_remaining_balance.toString() : '';
                        vm.FormSchema[2].Data[1].value = [emiStatus, remainingBalance];

                        vm.FormSchema[3].Data[4].value = [];
                        const automaticPayments = data.Enable_automatic_payments_for_emi != null ? data.Enable_automatic_payments_for_emi.toString() : '';
                        const termsConditions = data.is_terms_conditions != null ? data.is_terms_conditions.toString() : '';
                        vm.FormSchema[3].Data[4].value.push(automaticPayments, termsConditions);

                        // Update primary fields
                        vm.FormSchema[0].Data[0].value = data.maximum_conversions_per_year.toString();
                        vm.FormSchema[0].Data[1].value = data.minimum_Amount_For_EMI;
                        vm.FormSchema[0].Data[2].value = data.maximum_amount_for_eMI;
                        vm.FormSchema[0].Data[4].value = data.interest_rate_for_three_months;
                        vm.FormSchema[0].Data[5].value = data.interest_rate_for_six_months;
                        vm.FormSchema[0].Data[6].value = data.interest_rate_for_twelve_months;
                        vm.FormSchema[0].Data[7].value = data.processing_fee;
                        debugger
                        vm.NextBillpicked = data.include_unpaid_emi_in_next_bill ? 1 : 0;
                        if (vm.NextBillpicked == 0) {
                            vm.isUnpaidEMINextBill = 'No'
                        }
                        else {
                            vm.isUnpaidEMINextBill = 'Yes'
                        }
                        //vm.FormSchema[2].Data[0].value=data.upcoming_payment_reminders
                        if (data.automatic_payments == 1) {
                            vm.FormSchema[3].Data[1].visibility = true;
                            vm.FormSchema[3].Data[2].visibility = true;
                            vm.FormSchema[3].Data[3].visibility = true;
                            vm.FormSchema[3].Data[4].visibility = true;

                        }
                        // Update account details
                        vm.FormSchema[3].Data[0].value = data.automatic_payments;
                        vm.FormSchema[3].Data[1].value = data.account_type;
                        vm.FormSchema[3].Data[2].value = data.bank_account_number;
                        vm.FormSchema[3].Data[3].value = data.routing_number;
                    }
                });
            },
            OnChangeIncludeUnpaidEMIinNextBill: function (event, field) {
                debugger
                var vm = this;
                //field.value = event.target.getAttribute('data-value');
                let labelValue = event.target.value
                if (labelValue == 0) {
                    vm.isUnpaidEMINextBill = 'No'
                }
                else {
                    vm.isUnpaidEMINextBill = 'Yes'
                }
            },
            OnChangeEMIDurationOptions: function (event, field) {
                debugger;
                var vm = this;
                const isVisible = event.target.getAttribute('data-value');

                // Check if the value is already in checkedNames
                // const index = vm.FormSchema[0].Data[3].value.indexOf(isVisible);
                // if (index === -1) {
                //     // If not present, add it
                //     vm.FormSchema[0].Data[3].value.push(isVisible);
                // } else {
                //     // If present, remove it (unchecking)
                //     vm.FormSchema[0].Data[3].value.splice(index, 1);
                // }

                // Reset visibility for all relevant fields
                vm.FormSchema[0].Data[4].visibility = false; // For '1'
                vm.FormSchema[0].Data[5].visibility = false; // For '2'
                vm.FormSchema[0].Data[6].visibility = false; // For '3'

                // Iterate through checkedNames and set visibility accordingly
                vm.FormSchema[0].Data[3].value.forEach(element => {
                    switch (element) {
                        case '1':
                            vm.FormSchema[0].Data[4].visibility = true;
                            break;
                        case '2':
                            vm.FormSchema[0].Data[5].visibility = true;
                            break;
                        case '3':
                            vm.FormSchema[0].Data[6].visibility = true;
                            break;
                    }
                });
            },


            onChangeAutomaticPayments: function (event, field) {
                debugger;
                const vm = this;
                const isVisible = event.target.getAttribute('data-value') == '1';

                // Update visibility for Data elements based on isVisible
                for (let i = 1; i <= 4; i++) {
                    vm.FormSchema[3].Data[i].visibility = isVisible;
                }
            },

            onSubmit() {
                debugger
                var vm = this;
                vm.$refs.refAddress.validateForm.validate().then(async valid => {
                    if (valid) {
                        if (vm.FormSchema[0].Data[3].value.length != 0) {


                            var obj = {
                                planId: this.planId,
                                maximum_conversions_per_year: vm.FormSchema[0].Data[0].value,
                                minimum_Amount_For_EMI: vm.FormSchema[0].Data[1].value,
                                maximum_amount_for_eMI: vm.FormSchema[0].Data[2].value,
                                emi_duration_options: JSON.stringify(vm.FormSchema[0].Data[3].value),
                                interest_rate_for_three_months: vm.FormSchema[0].Data[4].value,
                                interest_rate_for_six_months: vm.FormSchema[0].Data[5].value,
                                interest_rate_for_twelve_months: vm.FormSchema[0].Data[6].value,
                                processing_fee: vm.FormSchema[0].Data[7].value,

                                include_unpaid_emi_in_next_bill: vm.NextBillpicked,

                                automatic_payments: vm.FormSchema[3].Data[1].value,
                                account_type: vm.FormSchema[3].Data[1].value,
                                bank_account_number: vm.FormSchema[3].Data[2].value,
                                routing_number: vm.FormSchema[3].Data[3].value,
                                auto_pay_for_EMI: '',

                                upcoming_payment_reminders: vm.FormSchema[2].Data[0].value[0],
                                due_date_alerts: vm.FormSchema[2].Data[0].value[1],
                                show_remaining_balance: vm.FormSchema[2].Data[1].value[0],
                                show_emi_status: vm.FormSchema[2].Data[1].value[1],
                                Enable_automatic_payments_for_emi: vm.FormSchema[3].Data[4].value[0],
                                is_terms_conditions: vm.FormSchema[3].Data[4].value[1]
                            }
                            debugger
                            var obj1 = JSON.stringify(obj);
                            let param = `objData=${obj1}`
                            DataService.SaveEMIConfiguration(param).then(response => {
                                debugger
                                if (response.data != null) {
                                    if (response.data.Code == 1) {
                                        vm.ShowAlert(vm.$t('installment Configuration saved successfully.'), "success");

                                    }
                                    else if (response.data.Code == 2) {
                                        vm.ShowAlert(vm.$t('installment Configuration updated successfully.'), "success");

                                    }
                                    else {
                                        vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                                    }
                                }

                            })
                        }
                        else {
                            vm.ShowAlert(vm.$t("Select at least one installment Duration."), "failure", vm.$t('Alert'));
                        }
                    }
                })
            },
        }

    }
</script>
