<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle py-1">
                            <h5 name="header" class="pt-2">
                                Map Subsidy Records with Plan
                            </h5>
                            <button class="close" v-on:click="ClosePopUp">
                                <em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <div class="heading-border mb-2">
                                <h5 class="h5">
                                    <span>{{ $t("Billing Conditions") }}</span>

                                </h5>
                            </div>
                            <div class="listing">


                                <div class="table-responsive table-fix-header">
                                    <table class="table table-bordered">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th>Subsidy</th>
                                                <th>Type</th>
                                                <th>Value</th>
                                                <th>Status</th>
                                                <th class="text-center">Conditions</th>

                                            </tr>
                                        </thead>
                                        <tbody>

                                            <template v-for="itm in SubsidyList">
                                                <!-- Main Subsidy Row -->
                                                <tr :class="{ selected_tr: isSubsidySelected(itm.Id) }">
                                                    <td>{{ itm.Subsidy_Name }}</td>
                                                    <td>{{ itm.Subsidy_Type }}</td>
                                                    <td>{{ itm.Subsidy_Value }}</td>
                                                    <td><span>{{ itm.status_name }}</span></td>
                                                    <td class="text-center">
                                                        <!-- <button type="button" :class="[
                                                            'btn',
                                                            isSubsidySelected(itm.Id)
                                                                ? 'btn-outline-primary'
                                                                : 'btn-primary'
                                                        ]" @click="AddCondition(itm)">
                                                            <i class="fa fa-plus" aria-hidden="true"></i>
                                                            Add Conditions
                                                        </button> -->

                                                        <button type="button"
                                                            :disabled="isTypeAlreadySelected(itm.Subsidy_Type) && !isSubsidySelected(itm.Id)"
                                                            :class="[
                                                                'btn',
                                                                (isTypeAlreadySelected(itm.Subsidy_Type) && !isSubsidySelected(itm.Id))
                                                                    ? 'btn-secondary'
                                                                    : (isSubsidySelected(itm.Id) ? 'btn-outline-primary' : 'btn-primary')
                                                            ]" @click="AddCondition(itm)">
                                                            <i class="fa fa-plus"></i> Add Conditions
                                                        </button>
                                                    </td>
                                                </tr>

                                                <!-- Conditions Row -->
                                                <tr v-if="getConditionsBySubsidy(itm.Id).length > 0" class="bg-light">
                                                    <td colspan="5">
                                                        <div class="conditions-container">
                                                            <div class="conditionbase"
                                                                v-for="(cond, index) in getConditionsBySubsidy(itm.Id)"
                                                                :key="index">
                                                                <strong>Condition:</strong>
                                                                {{ cond.expression }}
                                                                <em class="fa fa-times text-danger ml-3" title="Remove"
                                                                    @click="removeCondition(itm.Id, cond.conditionId)"></em>
                                                            </div>
                                                        </div>
                                                    </td>
                                                </tr>
                                            </template>

                                            <tr v-if="SubsidyList.length == 0">
                                                <td colspan="5" class="text-danger text-center border-0">
                                                    {{ $t("NorecordfoundText") }}</td>
                                            </tr>

                                        </tbody>
                                    </table>
                                </div>

                            </div>

                        </div>
                        <div class="modal-footer">
                            <div>
                                <button class="btn btn-success mr-2" @click="SavePlanMappedSubsidy()"><em
                                        class="fa fa-save"></em> {{ $t("Save")
                                        }}</button>
                                <button class="btn btn-danger" @click="ClosePopUp"><em class="fa fa-times"></em>
                                    {{ $t("Cancel") }}</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <AddConditionsWithPlan v-if="IsShowCondition" @condition-selected="onConditionSelected"
            :existingConditions="currentSubsidyConditions"></AddConditionsWithPlan>
    </section>
</template>
<script>

import DataService from '../../services/DataService';
import AddConditionsWithPlan from '../../components/Modals/AddConditionsWithPlan.vue';


export default {
    components: {
        AddConditionsWithPlan
    },

    props: {
        PlanId: {
            type: Number,
            required: true
        }
    },

    data: function () {
        return {
            isModalAddRule: false,
            searchvalue: '',
            PageSize: 10,
            PageNumber: 1,
            SortBy: "",
            SortExp: "",
            TotalRecords: 0,
            SubsidyList: [],
            selectedConditions: [],
            IsShowCondition: false,
            selectedSubsidyIds: [],
            currentSubsidy: null,
        }
    },
    computed: {
        currentSubsidyConditions() {
            return this.currentSubsidy ? this.getConditionsBySubsidy(this.currentSubsidy.Id) : [];
        }
    },
    created: function () {

        var vm = this;
        vm.PlanId;
        vm.GetSubsidyMappedByPlan();

    },
    methods: {
        ClosePopUp() {

            var vm = this;
            vm.selectedSubsidyId = null;
            vm.$parent.IsShowPlanSubsidy = false;
        },
        GetSubsidyMappedByPlan: function () {

            var vm = this;

            var Url = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&search=${vm.searchvalue}&planId=${vm.PlanId}`;
            DataService.GetSubsidyMappedByPlan(Url).then((response) => {
                if (response.data) {
                    const parsed = JSON.parse(response.data[0]); // parse string to object
                    vm.SubsidyList = parsed.Data; // assign the array inside Data property                  

                }

                vm.TotalRecords = vm.SubsidyList[0].total_records;
            });
        },
        AddCondition(itm) {

            var vm = this;
            vm.currentSubsidy = itm;
            vm.IsShowCondition = true;


        },
        isTypeAlreadySelected(type) {
            
            return this.selectedConditions.some(cond => {
                const subsidy = this.SubsidyList.find(s => s.Id === cond.subsidyId);
                return subsidy && subsidy.Subsidy_Type === type;
            });
        },
        isSubsidySelected(subsidyId) {
            // Returns true if this subsidy has at least one condition selected
            return this.selectedConditions.some(c => c.subsidyId === subsidyId);
        },
        getConditionsBySubsidy(subsidyId) {

            return this.selectedConditions.filter(c => c.subsidyId === subsidyId);
        },
        onConditionSelected(condition) {
            debugger
            if (!this.currentSubsidy) return;

            // Create mapping object
            const mapping = {
                planId: this.PlanId,
                subsidyId: this.currentSubsidy.Id, // map with current subsidy
                conditionId: condition.SERVICEID,
                expression: condition.CONDITION,
            };

            // Optional: avoid duplicates
            const exists = this.selectedConditions.some(
                x => x.subsidyId === mapping.subsidyId && x.conditionId === mapping.conditionId
            );

            if (!exists) {
                this.selectedConditions.push(mapping);
            }

            // Close child modal
            this.IsShowCondition = false;
        },
        // removeSelectedCondition(index) {
        //     
        //     this.selectedConditions.splice(index, 1);
        // },
        removeCondition(subsidyId, conditionId) {
            // Remove condition specific to this subsidy
            this.selectedConditions = this.selectedConditions.filter(
                x => !(x.subsidyId === subsidyId && x.conditionId === conditionId)
            );
        },

        async SavePlanMappedSubsidy() {
            debugger
            var vm = this;

            // Create payload for API
            const payload = vm.selectedConditions.map(item => ({
                planId: vm.PlanId,
                conditionId: item.conditionId,
                subsidyId: item.subsidyId,

            }));

            // Wrap as postString if your API expects JSON string
            const postData = {
                postString: JSON.stringify(payload)
            };

            try {
                const response = await DataService.AddSubsidyPlanData(postData);

                if (response.data != null && response.data[0] === "Success") {
                    vm.ShowAlert(vm.$t("DataSavedSuccessfully"), "success", vm.$t("Alert"));
                    vm.ClosePopUp();
                    vm.$parent.GetSubsidyPlanData();
                } else {
                    vm.ShowAlert(vm.$t("ErrorWhileSaving", [vm.$t("Service")]), "danger", true, vm.$t("Alert"));
                }
            } catch (error) {
                console.error("Error saving subsidy plan:", error);
                vm.ShowAlert(vm.$t("ErrorWhileSaving", [vm.$t("Service")]), "danger", true, vm.$t("Alert"));
            } finally {
                vm.isLoading = false;
            }


        }


    }

}
</script>
<style scoped>
AddConditionsWithPlan {
    position: relative;
    z-index: 9999;
}

.conditionbase {
    background-color: #f8f9fa;
    border: 1px solid #dee2e6;
    border-radius: 20px;
    padding: 8px 15px;
    color: #495057;
    margin-right: 10px;
    margin-bottom: 10px;
    float: left;
}

tr.selected_tr {
    background: #e8eaea !important;
}
</style>