<template>
    <section>
      <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
          <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
              <div class="modal-header theme-primary partition-full d-flex">
                <h5 class="modal-title">Enable Minimum Charge</h5>
                <button type="button" class="close" v-on:click="Close">
                  <em class="fa fa-times" aria-hidden="true"></em>
                </button>
              </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" ref="validateEnableMinimumCharge" :buttons="buttons" :schema="FormSchema">
                            <template v-slot:tgslot-LimitLayout="{ data }">
                                <div class="row" v-for="(item, index) in Mainleveldiv" :key="index">
                                    <div class="col-md-2">
                                        <label>Service Name:</label>
                                        <Field :name="'ServiceName'" v-slot="{ errors }" :rules="{ required: true }">
                                            <select :class="{ 'form-control': true, 'is-invalid': errors[0] }" class="form-control level" v-model="item.serviceId" name="ServiceName">
                                                <option value="">Select Service</option>
                                                <option v-for="gitem in ServiceNameData" :value="gitem.value" :key="gitem.value">{{ gitem.name }}</option>
                                            </select>
                                            <span v-if="errors[0]" :name="'ServiceName' + index" class="invalid-feedback d-block">
                                                {{ errors[0] }}
                                            </span>
                                        </Field>
                                    </div>
                                    <div class="col-md-2">
                                        <div class="form-group">
                                        <label>Limit type:</label>
                                        <select class="form-control" v-model="item.limitType">
                                            <option value="HM3">HM3</option>
                                            <option value="MMBTU">MMBTU</option>
                                        </select>
                                        </div>
                                    </div>
                                    <div class="col-md-2">
                                        <div class="form-group">
                                        <label>Limit from:</label>
                                        <input type="number" class="form-control" v-model="item.limitFrom" />
                                        </div>
                                    </div>
                                    <div class="col-md-2">
                                        <div class="form-group">
                                        <label>Limit to:</label>
                                        <input type="number" class="form-control" v-model="item.limitTo" />
                                        </div>
                                    </div>
                                    <div class="col-md-2">
                                        <div class="form-group">
                                        <label>Rate:</label>
                                        <input type="number" class="form-control" v-model="item.rate" />
                                        </div>
                                    </div>
                                    <div class="col-md-2">
                                        <label for="action"></label>
                                        <div class="input-group">
                                            <em class="round-icon-small fa fa-plus bg-success float-none" v-if="index == 0" @click="addRow"></em>
                                            <em class="round-icon-small fa fa-minus bg-danger float-none" v-if="index != 0" @click="deleteRow(index)"></em>
                                        </div>
                                    </div>
                                </div>
                            </template>
                        </dynamic-form>
                    </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </template>
  
  <script>
  import DataService from "../../services/DataService";
  
  export default {
    props: {
      editEnableLimitData: {
            type: Function,
            required: false
        },
        refCodeCharge: {
            type: String,
            required: false
        },
        MonthApplicable :{
            type: Array,
            required: false
        }
    },
    data() {
      return {
        isLoading: false,
        Mainleveldiv: [{ serviceId: "", limitType: "", limitFrom: "", limitTo: "",rate : "" }],
        ServiceNameData: [],
       FormSchema: [
        {
          layoutType: "single",
          Data: [
            {
              astype: "CheckBoxField",
              label: "Applicable From Month",
              name: "ApplicableFromMonth",
              value: "",
              mode: "tag",
              placeholder: "",
              visibility: true,
              config: {
                options: [{ name: "Jan", value: "1" },{ name: "Feb", value: "2" },{ name: "Mar", value: "3" },{ name: "Apr", value: "4" },
                  { name: "May", value: "5" },{ name: "Jun", value: "6" },{ name: "Jul", value: "7" },{ name: "Aug", value: "8" },
                  { name: "Sep", value: "9" },{ name: "Oct", value: "10" },{ name: "Nov", value: "11" },{ name: "Dec", value: "12" }],
              },
              validationRules: { required: true },
            },
          ],
        },
        {
          layoutType: "single",
          visibility:true,
          Data: [
            {
              astype: "SlotField",
              label: "",
              name: "LimitLayout",
              value: "",
              visibility: true,
              placeholder: "",
              config: {
                options: [],
              },
              validationRules: "",
            },
          ],
        },
      ],
      buttons: [
        {
          type: "submit",
          class: "btn btn-success save",
          value: "Y",
          text: "<i class='fa fa-save pr-2'></i>Save",
          onClick: this.SaveData,
        },
        {
          type: "button",
          class: "btn btn-danger",
          text: "<i class='fa fa-close pr-2'></i>Cancel",
          onClick: this.Close,
        },
      ]
      };
    },
    created() {
        var vm =this;
        vm.GetDDLServiceName();
        if (vm.refCodeCharge == 'Update') 
        {
            debugger;
          setTimeout(() => { this.BindEnableChargeDetail(vm.editEnableLimitData); }, 500);
        }
    },
    methods: {
        BindEnableChargeDetail(editDetail) 
        {
              var vm = this;
              debugger;
              vm.$refs.validateEnableMinimumCharge.updateProperty("ApplicableFromMonth", "value", vm.MonthApplicable);
              vm.$refs.validateEnableMinimumCharge.UpdateKeyValue();
              vm.Mainleveldiv = editDetail.map(item => ({
                serviceId: item.serviceId,
                limitType: item.limitType,
                limitFrom: item.limitFrom,
                limitTo: item.limitTo,
                rate: item.rate
              }));
          },
      SaveData() {
       const fromMonth = this.$refs.validateEnableMinimumCharge.getPropertyValue("ApplicableFromMonth", "value");
          const formData = {
            applicableForMonth: fromMonth,
            limitData: this.Mainleveldiv.map(item => {
              const selectedService = this.ServiceNameData.find(service => service.value === String(item.serviceId));
              const serviceName = selectedService ? selectedService.name : '';  // If not found, use an empty string
              return {
                serviceId: item.serviceId,
                serviceName: serviceName,  // Add the service name to the data
                limitType: item.limitType,
                limitFrom: item.limitFrom,
                limitTo: item.limitTo,
                rate: item.rate
              };
            })
          };
          const serviceNames = formData.limitData.map(item => item.serviceName).join(", ");
          formData.serviceNamesCSV = serviceNames;
          this.$emit('minimumChargesDetail', formData);
        },
      addRow() {
        this.Mainleveldiv.push({ serviceId: "", limitType: "", limitFrom: "", limitTo: "",rate : "" });
      },
      
      deleteRow(index) {
        if (this.Mainleveldiv.length > 1) {
          this.Mainleveldiv.splice(index, 1);
        }
      },
      Close() {
        this.$parent.isEnableMinimiumCharge = false;
      },
      GetDDLServiceName: function () {
            var vm = this;
            var url = `id=&moduleName=BILLING&type=BILLING_SERVICE_NAME&search=&refId=`;
            DataService.GetDDLData(url).then(response => {
                debugger;
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        response.data.DATA.forEach((item) => {
                            vm.ServiceNameData.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            })
        }
    },
  };
  </script>
  