<template>
  <section>

    <tg-list
      :IsShowAction="true"
      ref="tgList"
      :showCheckBox="false"
      ModuleName="Billing"
      SubModuleCode="SubsidyPlan"
      IdentityColumn="Id"
      HeaderText="Plan Subsidy"
      :ListData="SubsidyData"
      :listType="['List']"
      :HeaderData="Headers"
      :NorecordfoundText="$t('NoRecordfound')"
      :TotalRecords="TotalRecords"
      :SearchFields="leftsearchSchema"
      :RenderRowActionMethod="renderActions"
      :ListDataCallBackFunction="GetSubsidyPlanData"
      :LegendArray="LegendArray"
      :SortExp="SortExp"
      :SortBy="SortBy"
      :HeaderButtons="listheaderbuttons"
      :CurrentPageNumber="pageNum"
      :widgets="[]"
      v-on:LeftsearchButtonClick="false"
      v-on:PagerButtonClick="pagerMethod"
      v-on:HeaderButtonClick="commonHeaderButtonClick"
      v-on:ActionButtonClick="actionButtonClick"
      v-on:SortdataButtonClick="sortdata"
    >

      <!-- Slot to show increase/decrease arrows -->
 <template v-slot:slotdata="{ data }">
  <template v-if="data.column.COLUMN_NAME === 'increasedecrease'">

    <div class="buttonsdlt ml-2" style="float: right; min-width: 50px;">
  
        <!-- First row: only down -->
        <div v-if="data.row.ROWNUMBER === 1 && data.row.parent == null">
            <a class="aDelete enable deleteNodeConf ml-2" href="javascript:;" 
            v-on:click="MoveRow(data.row, data.row.ROWNUMBER - 1, 'down')" 
            data-original-title="Move down">
            <em class="fa fa-arrow-circle-down text-dark action-icon DownDisplayOrder" style="font-size: 18px;"></em>
            </a>
        </div>

        <!-- Middle rows: both up and down -->
        <div v-else-if="data.row.ROWNUMBER > 1 && data.row.ROWNUMBER < data.row.total_records && data.row.parent == null">
            <a class="aDelete enable deleteNodeConf" href="javascript:;" 
            v-on:click="MoveRow(data.row, data.row.ROWNUMBER - 1, 'up')" 
            data-original-title="Move up">
            <em class="fa fa-arrow-circle-up text-dark action-icon UpDisplayOrder" style="font-size: 18px;"></em>
            </a>
            <a class="aDelete enable deleteNodeConf ml-2" href="javascript:;" 
            v-on:click="MoveRow(data.row, data.row.ROWNUMBER - 1, 'down')" 
            data-original-title="Move down">
            <em class="fa fa-arrow-circle-down text-dark action-icon DownDisplayOrder" style="font-size: 18px;"></em>
            </a>
        </div>

        <!-- Last row: only up -->
        <div v-else-if="data.row.ROWNUMBER === data.row.total_records && data.row.parent == null">
            <a class="aDelete enable deleteNodeConf ml-2" href="javascript:;" 
            v-on:click="MoveRow(data.row, data.row.ROWNUMBER - 1, 'up')" 
            data-original-title="Move up">
            <em class="fa fa-arrow-circle-up text-dark action-icon UpDisplayOrder" style="font-size: 18px;"></em>
            </a>
        </div>
        </div>
  </template>
</template>



    </tg-list>

    <!-- Conditional modals/components -->
    <AddPlanMappedSubsidy v-if="IsShowPlanSubsidy" :PlanId="PlanId"></AddPlanMappedSubsidy>

    <RuleServiceUpdate
      v-if="isModalAddRule"
      :configurationId="configurationId"
      :billingServiceId="billingServiceId"
      :conditionId="conditionId"
      :nodeName="nodeName"
      reqFrom="ViewBillingConditionForService"
      closeModalType="isModalAddRule"
    />

    <AddConditionWithSubsidy
      v-if="IsShowCondition"
      :selectedServiceIds="selectedServiceIds"
      @conditions-selected="onConditionsSelected"
    ></AddConditionWithSubsidy>

  </section>
</template>

<script>

import DataService from "../../services/DataService";
import AddPlanMappedSubsidy from "../../components/Modals/AddPlanMappedSubsidy.vue";
import RuleServiceUpdate from '../../../src/views/BillingRule/BillingRuleHistory/RuleServiceUpdate.vue';
// import modalAddBillingRule from "../../components/Modals/modalAddBillingRule.vue";
import AddConditionWithSubsidy from "../../components/Modals/AddConditionWithSubsidy.vue";;

export default {
    components: {
        AddPlanMappedSubsidy,
        RuleServiceUpdate,
        AddConditionWithSubsidy
    },

    data() {
        return {
            noRecords: false,
            IsRuleListing: false,
            TotalRecords: 0,
            serviceId: 0,
            CurrentPage: 1,
            PlanId: 0,
            paymentType: '',
            isForService: true,
            TotalPages: 0,
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            GroupStart: 1,
            GroupEnd: 3,
            Headers: [
                {
                    "COLUMN_NAME": "Subsidy_Name",
                    "DATA_TYPE": "string",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Subsidy Name',
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "service_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Condition Name',
                    "DISPLAY_ORDER": 2
                }
                ,
                {
                    "COLUMN_NAME": "Subsidy_Type",
                    "DATA_TYPE": "string",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Subsidy Type',
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "Subsidy_Value",
                    "DATA_TYPE": "string",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Subsidy Value',
                    "DISPLAY_ORDER": 4
                },
                {
                    "COLUMN_NAME": "Effective_From",
                    "DATA_TYPE": "string",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Effective From',
                    "DISPLAY_ORDER": 5
                },
                {
                    "COLUMN_NAME": "Effective_To",
                    "DATA_TYPE": "string",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Effective To',
                    "DISPLAY_ORDER": 6
                },

                {
                    "COLUMN_NAME": "increasedecrease",
                    "DATA_TYPE": "string",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Move Record',
                    "DISPLAY_ORDER": 6,
                    settings: {
                        isInSlot: true
                    }
                },


            ],
            listActions: [

                {
                    title: 'Delete',
                    href: 'javascript:void(0)',
                    icon: 'fa fa-trash',
                    isDisabled: false,
                    callbackfunction: 'deleteItem',
                    additionalSpan: ''
                }

            ],
            listheaderbuttons: [

                {
                    title: 'Map Subsidy',
                    iconClass: 'fa fa-plus',
                    callbackfunction: 'AddPlanSubsidy',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                    checkPrivilege: false
                },
                {
                    title: 'Back',
                    iconClass: 'fa fa-arrow-left',
                    href: '/Plan/PlanListing',
                    //callbackfunction: 'DeleteSubsidy',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                    checkPrivilege: false
                },

            ],
            ServicePlanListing: [],
            isExistingService: false,
            isCreateService: false,
            planCode: '',
            serviceDetailForBillingRule: [],
            serviceDetailConditions: false,
            serviceRuleItem: null,
            servicePlanId: null,

            IsShowPlanSubsidy: false,
            searchCondition: '',
            SubsidyData: [],
            reqFrom: 'fromBillingService',
            conditionId: 0,
            isModalAddRule: false,
            serviceDetailForBillingRule: [],
            nodeName: '',
            configurationId: '',
            IsShowCondition: false,
            CurrentSubsidyId: null,
            selectedServiceIds: [],
            PlanMappedConditionIds: "",
            PlanSubsidyList:[],

        }
    },
    created: async function () {

        var vm = this;
        vm.PlanId = parseInt(this.$route.params.id);
        vm.GetSubsidyPlanData();
    },
    methods: {
        AddPlanSubsidy() {

            var vm = this;
            vm.IsShowPlanSubsidy = true;
        },
        renderActions(row) {

            var self = this;

            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Delete':
                        if (row.Subsidy_Name != null) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }

                        break;


                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
       GetSubsidyPlanData: function () {
  debugger;
  var vm = this;

  // Build API URL with all filters and paging
  var Url = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&search=${vm.searchCondition}&PlanId=${vm.PlanId}`;

  // Call API
  DataService.GetSubsidyPlanData(Url).then((response) => {
    if (response.data) {
      try {
        // Parse JSON safely
        const parsed = JSON.parse(response.data[0]);
        vm.SubsidyData = parsed.Data || [];

        // Handle headers and custom column actions
        vm.Headers.forEach((column) => {
          column.settings = null;

          // for up/down buttons
          if (column.COLUMN_NAME === "increasedecrease") {
            column.settings = {
              isInSlot: true,
            };
          }

          // for service name clickable column
          if (column.COLUMN_NAME === "service_name") {
            column.settings = {
              clickEvent: vm.UpdateCondition,
            };
          }
        });

        // handle pagination and totals
        if (vm.SubsidyData.length > 0) {
          vm.TotalRecords = vm.SubsidyData[0].total_records || 0;
          vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
          vm.CurrentPage = vm.PageNumber;
        } else {
          vm.TotalRecords = 0;
          vm.TotalPages = 0;
          vm.CurrentPage = 1;
        }
      } catch (error) {
        console.error("Error parsing subsidy data:", error);
        vm.SubsidyData = [];
      }
    } else {
      // fallback in case no data
      vm.SubsidyData = [];
      vm.TotalRecords = 0;
      vm.TotalPages = 0;
    }
  })
  .catch((error) => {
    console.error("Error fetching subsidy plan data:", error);
    vm.SubsidyData = [];
    vm.TotalRecords = 0;
    vm.TotalPages = 0;
  });
},

        sortdata: function (s) {
            debugger
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.GetSubsidyPlanData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        pagerMethod: function (value) {
            this.pageSize = value.pageSize;
            this.pageNum = value.pageNum;
            this.GetSubsidyPlanData();
        },
        commonHeaderButtonClick: function (event) {

            switch (event.callbackfunction) {
                case "AddPlanSubsidy":
                    this.AddPlanSubsidy();
                    break;

            }
        },
        actionButtonClick(callbackfunction, item, index, event) {

            switch (callbackfunction) {
                case 'deleteItem':
                    this.deleteItem(item);
                    break;

            }
        },
         deleteItem(Obj) {
             debugger
            var vm = this;           

            if (Obj.Id != null && Obj.Id != undefined) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Plan")), function () {
                    var objData =
                    {
                        PlanId: vm.PlanId,
                        SubsidyId: Obj.Id,
                        ConditionId: "",

                    }

                    var obj = JSON.stringify(objData);

                     DataService.UpdatePlanMappedCondition(obj).then((response) => {
                        if (response.data) {
                            vm.GetSubsidyPlanData();
                            vm.ShowAlert('Deleted Data Successfully!', "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t("DeleteingRecordMsg"), "danger", vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("DeleteingRecordMsg"), "danger", vm.$t('Alert'));
            }

        },
        // handleServiceClick(Itm) {

        //     var vm = this;
        //     debugger;
        //     vm.isModalAddRule = true;
        //     vm.conditionId = Itm.service_id;
        // },
        UpdateCondition(Itm) {
           debugger
            var vm = this;
            debugger;
            vm.IsShowCondition = true;
            vm.CurrentSubsidyId = Itm.Id;

            vm.selectedServiceIds = Itm.service_id
                ? Itm.service_id.split(",").map(id => parseInt(id.trim()))
                : [];

        },       

        async onConditionsSelected(conditions) {
            debugger
            var vm = this
            //  Extract SERVICEIDs and make a comma-separated string
            const conditionIds = conditions
                .map(c => c.SERVICEID)      // extract each SERVICEID
                .filter(id => id != null)   // remove null/undefined
                .join(",");                 // join as "1,2,3"

            //  Save it in your variable
            vm.PlanMappedConditionIds = conditionIds;

            var objData =
            {
                PlanId: vm.PlanId,
                SubsidyId: vm.CurrentSubsidyId,
                ConditionId: vm.PlanMappedConditionIds

            }

            var obj = JSON.stringify(objData);

            //var Url = `PlanId=${vm.PlanId}&ConditionId=${vm.PlanMappedConditionIds}&SubsidyId=${vm.CurrentSubsidyId}`;

            await DataService.UpdatePlanMappedCondition(obj).then((response) => {
                if (response.data) {

                    vm.IsShowCondition = false;
                    vm.ShowAlert('Update Successfully', "success", vm.$t("Alert"));
                    vm.GetSubsidyPlanData();

                }
                else {
                    vm.ShowAlert(vm.$t("ErrorWhileSaving", [vm.$t("Service")]), "danger", true, vm.$t("Alert"));
                }


            });

            vm.IsShowCondition = false;
        },

         // onConditionSelected(condition) {
        //     debugger
        //     var vm = this;
        //     vm.IsShowCondition = false;

        //     var Url = `Id=${vm.PlanMappedId}&ConditionId=${condition.SERVICEID}`;
        //     DataService.UpdatePlanMappedCondition(Url).then((response) => {
        //         if (response.data) {

        //             vm.IsShowCondition = false;
        //             vm.ShowAlert('Update Successfully', "success", vm.$t("Alert"));
        //             vm.GetSubsidyPlanData();

        //         }
        //         else{
        //             vm.ShowAlert(vm.$t("ErrorWhileSaving", [vm.$t("Service")]), "danger", true, vm.$t("Alert"));
        //         }


        //     });
        // },
        

        MoveRow(row, index, direction) {
            const vm = this;
            const thisItem = vm.SubsidyData[index];
            debugger;

            if (direction === "up" && index > 0) {
                const targetItem = vm.SubsidyData[index - 1];

                // Swap DISPLAY_ORDER
                [thisItem.DISPLAY_ORDER, targetItem.DISPLAY_ORDER] = [targetItem.DISPLAY_ORDER, thisItem.DISPLAY_ORDER];

                // Swap in array visually
                vm.SubsidyData.splice(index, 1);
                vm.SubsidyData.splice(index - 1, 0, thisItem);

                sendMoveRequest(thisItem, targetItem, "up");

            } else if (direction === "down" && index < vm.SubsidyData.length - 1) {
                const targetItem = vm.SubsidyData[index + 1];

                // Swap DISPLAY_ORDER
                [thisItem.DISPLAY_ORDER, targetItem.DISPLAY_ORDER] = [targetItem.DISPLAY_ORDER, thisItem.DISPLAY_ORDER];

                // Swap in array visually
                vm.SubsidyData.splice(index, 1);
                vm.SubsidyData.splice(index + 1, 0, thisItem);

                sendMoveRequest(thisItem, targetItem, "down");
            }

            //Helper function to call backend
              function sendMoveRequest(item, targetItem, moveType) {
                debugger;
                const postJSON = {
                  COMPANY_ID: vm.CompanyId,
                  ID: item.Id,
                  IDC: targetItem.Id,
                  DISPLAY_ORDER: item.DISPLAY_ORDER ,
                  DISPLAY_ORDER_CHANGE: targetItem.DISPLAY_ORDER,
                  TYPE: moveType
                };

                DataService.MovePlanSubsidyDisplayOrder(postJSON).then(function (response) {
                  if (response.data.status === "Success") {
                    vm.ShowAlert(vm.$t("RecordMovedSuccessfully"), "success", true, vm.$t("Alert"));
                    vm.GetSubsidyPlanData();
                  } else {
                    vm.ShowAlert(response.data.msg || "Something went wrong!", "error", true, vm.$t("Alert"));
                  }
                });
              }
            }
    }

}
</script>