<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle py-1">
                            <h5 name="header" class="pt-2">
                                {{ $t('Inventory') }}
                            </h5>
                            <button class="close" v-on:click="ClosePopUp">
                                <em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <div class="listing">
                                <div class="table-responsive table-fix-header" style="max-height:500px">
                                    <table class="table table-bordered dt-responsive nowrap mt-0">
                                        <thead class="thead-bg">
                                            <tr class="text-capitalize">
                                                <th style="width:5%" class="text-center">
                                                    <label class="form-check form-check-inline">
                                                        <div
                                                            class="custom-control custom-checkbox custom-control-inline">
                                                            <input type="checkbox" id="talygenAssetCheckAll"
                                                                class="chkitems1 custom-control-input"
                                                                :checked="selectAll" @change="selectAllItems"
                                                                name="selectedItems1">
                                                            <label class="custom-control-label"
                                                                for="talygenAssetCheckAll"></label>
                                                        </div>
                                                    </label>
                                                </th>
                                                <th scope="col">{{ $t('ProductName') }}</th>
                                                <th scope="col" class="text-center">{{ $t('ProductType') }}</th>
                                                <th scope="col" class="text-center">{{ $t('ProductCode') }}</th>
                                                <th scope="col" class="text-center">{{ $t('Quantity') }}</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-if="InventoryListing.length === 0">
                                                <td class="text-center text-danger no-record" colspan="4">
                                                    {{ $t('NoRecordfound') }}
                                                </td>
                                            </tr>
                                            <tr v-else v-for="(item, index) in InventoryListing" :key="index">
                                                <td class="text-center">
                                                    <label class="form-check form-check-inline">
                                                        <div
                                                            class="custom-control custom-checkbox custom-control-inline">
                                                            <input type="checkbox" :id="'tabletablebordered' + index"
                                                                class="chkitems custom-control-input"
                                                                v-model="selectedItems" @change="updateSelectAll"
                                                                name="selectedItems" :value="item.asset_catalogue_id">
                                                            <label :for="'tabletablebordered' + index"
                                                                class="custom-control-label"></label>
                                                        </div>
                                                    </label>
                                                </td>
                                                <td>{{ item.product_name }}</td>
                                                <td class="text-center">{{ item.product_type }}</td>
                                                <td class="text-center">{{ item.product_code }}</td>
                                                <td class="text-center">{{ item.quantity }}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                        <div class="modal-footer">
                            <a href="javascript:;" class="btn btn-success save-btn mr-2" v-on:click="SaveData"
                                title="Save">
                                <i class="fa fa-floppy-o pr-2"></i>{{ $t('Save') }}
                            </a>
                            <a href="javascript:;" class="btn btn-danger delete-btn" v-on:click="ClosePopUp"
                                title="Cancel">
                                <i class="fa fa-times pr-2"></i>{{ $t('Cancel') }}
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>


<script>
import DataService from '../../services/DataService';

export default {
    props: {
        PlanId: {
            type: Number,
            required: false
        },
        callbackfunction: {
            type: Function
        }
    },
    data() {
        return {
            isLoading: false,
            InventoryListing: [],
            selectedItems: [],
            selectAll: false,
            noRecord: false,
            CurrentPage: 1,
            TotalPages: 0,
            TotalRecords: 0,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            GroupStart: 1,
            GroupEnd: 3,
            PageNumber: "1"
        };
    },
    async created() {
        await this.GetInventoryList();
    },
    methods: {
        async GetInventoryList() {
            var vueObj = this;
            vueObj.isLoading = true;
            const url = `pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}&plan_id=${vueObj.PlanId}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&search=`;
            await DataService.GetInventoryList(url).then(response => {
                if (response.data.Status = "Success") {
                    if (response.data != "" && response.data != null) {
                        if (response.data.result.data.length > 0) {
                            vueObj.InventoryListing = response.data.result.data;
                            vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                            vueObj.noRecord = false;
                        } else {
                            vueObj.noRecord = true;
                            vueObj.TotalRecords = 0
                            vueObj.InventoryListing = [];
                        }

                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0
                        vueObj.InventoryListing = [];
                    }
                }
            })
        },
        selectAllItems() {
            var vueObj = this;
            this.selectAll = !this.selectAll;
            if (this.selectAll) {
                this.InventoryListing.forEach(function (item) {
                    vueObj.selectedItems.push(item.asset_catalogue_id);
                })
            } else {
                this.selectedItems = [];
            }
        },
        updateSelectAll() {
            var vm = this;
            const totalItems = this.InventoryListing.length;
            const selectedCount = this.selectedItems.length;
            vm.selectAll = totalItems > 0 && selectedCount === totalItems;
        },
        async SaveData() {
            var vm = this;
            const filteredSkills = vm.InventoryListing.filter(inventory => this.selectedItems.includes(inventory.asset_catalogue_id));
            const obj = JSON.stringify(filteredSkills);

            var requestPayload = { objData: obj, planId: vm.PlanId };
            await DataService.AddSelectedInventory(requestPayload).then(response => {
                if (response.data != null) {
                    if (response.data.Status == "Success" && response.data.Code == "1") {
                        vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Inventory')]), "Success", true, vm.$t('Alert'));
                        vm.ClosePopUp();
                        // vm.$emit('data-saved', obj);
                        vm.callbackfunction();
                        vm.isLoading = false;
                    }
                    else {
                        vm.ShowAlert(vm.$t("ErrorWhileSaving", [vm.$t("Inventory")]), "danger", true, vm.$t("Alert"));
                        vm.isLoading = false;
                    }
                }
            });
        },
        ClosePopUp() {
            this.$parent.isExistingInventory = false;
        }
    }
}
</script>
