                    callbackfunction: 'DeleteRateCode',
                    icon: 'fa fa-trash-o action-icon text-danger',
                    additionalSpan: ''
                }],
            Headers: [
                {
                    "COLUMN_NAME": "work_code",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('RateCode'),
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "service_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": false,
                    "DISPLAY_NAME": this.$t('ServiceName'),
                    "DISPLAY_ORDER": 2,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "comment",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Comment'),
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "billing_code",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('BillingCode'),
                    "DISPLAY_ORDER": 4,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "rate",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Rate'),
                    "DISPLAY_ORDER": 5,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "status_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('HBStatus'),
                    "DISPLAY_ORDER": 6,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "start_date",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('EventStartDate'),
                    "DISPLAY_ORDER": 7,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "end_date",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('EndDate'),
                    "DISPLAY_ORDER": 8,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "created_by",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('SACreatedBy'),
                    "DISPLAY_ORDER": 9,
                },
                {
                    "COLUMN_NAME": "created_at",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('SACreatedAt'),
                    "DISPLAY_ORDER": 10,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "modified_by",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('modified_by'),
                    "DISPLAY_ORDER": 11,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "modified_at",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ModifiedAt'),
                    "DISPLAY_ORDER": 12,
                    settings: {
                        isInSlot: true
                    }
                },

            ],
            Buttons: [
                {
                    title: 'addrate',
                    iconClass: 'fa fa-plus pr-2 ',
                    callbackfunction: 'EditRateCode',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                },
                 {
                    title: 'Delete',
                    iconClass: 'fa fa-trash-o action-icon',
                    callbackfunction: 'DeleteAll',
                    additionalClass: '',
                    isdisabled: true, // disabled by default
        isvisible: true,

                }
            ],
            IsAddRate: false,
            RateObj: null,
            PlainId: 0
        }
    },
    mounted() {
    var vm = this;

    // Listen for checkbox change events
    $(document).on('change', '.chkItems, #chkAll_0', function () {
        vm.updateDeleteButtonState();
    });
},
    created: function () {
        var ths = this;
        ths.PlainId = this.$route.params.id;
        ths.Headers.forEach((itm => {
            itm.DISPLAY_NAME = ths.$t(itm.DISPLAY_NAME);
        }))
        ths.GetWorkCodeMappingList();
    },
    methods: {
        leftCommonSearch() { },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.GetWorkCodeMappingList();
        },
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {
                case 'EditRateCode':
                    this.EditRateCode(item);
                    break;
                case 'DeleteRateCode':
                    this.commonDeleteData(item.rate_schedule_id);
                    break;
            }
        },
       commonHeaderButtonClick: async function (event) {
    var vm = this;
    switch (event.callbackfunction) {
        case "EditRateCode":
            vm.EditRateCode(null);
            break;

        case "DeleteAll":
            
            vm.commonDeleteData("ALL");
            break;
    }
},

        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.GetWorkCodeMappingList();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        GetWorkCodeMappingList() {
            var vm = this;
            vm.isLoading = true;
            var url = `Planid=${vm.PlainId}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&Clientid=`;
            DataService.GetWorkCodeMappingList(url).then((response) => {
                vm.isLoading = false;
                if (response.data.result.DATA.length > 0) {
                    vm.ListingData = response.data.result.DATA;
                    vm.ListingData.forEach((element) => {
                        ['start_date', 'end_date', 'created_at', 'modified_at'].forEach((key) => {
                            if (element[key]) {
                                element[key] = new Date(element[key]);
                            }
                        });
                    });

                    vm.TotalRecords = response.data.result.DATA[0].TOTAL_RECORDS;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                } else {
                    vm.ListingData = [];
                    vm.TotalRecords = 0;
                    vm.TotalPages = 0;
                }
            }).catch(() => {
                vm.isLoading = false;
            });
        },
        EditRateCode(itm) {
            debugger
            var ths = this;
            ths.RateObj = itm;
            ths.IsAddRate = true;
            ths.GetWorkCodeMappingList();
        },
        CloseRateCode() {
            var ths = this;
            ths.IsAddRate = false;
            ths.GetWorkCodeMappingList();
        },
 commonDeleteData: function (event) {
    debugger
    var vm = this;
    var val = "";

    $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
        if (val.length > 0) val += ',';
        val += $(this).val();
    });

    if (event === "ALL") {
        if (val.length === 0) {
            vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "failure", true, vm.$t("Alert"));
            return;
        }
    }
    else if (val.length === 0 && typeof event !== "undefined" && event !== null && event !== "") {
        val = String(event);
    }

    if (!val || String(val).length === 0) {
        vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "failure", true, vm.$t("Alert"));
        return;
    }

    val = String(val);

    var isMultiple = (val.indexOf(',') !== -1);

    var deleteMessage = (isMultiple ? vm.$t("DeleteMultiple") : vm.$t("Delete")) + " " + vm.$t("RateCode");

    vm.confirmR(vm.$t("ConfirmDelete"), true, deleteMessage, async function () {
        var postJSON = {
            ids: val,
            moduleName: "RateCode",
            subModuleCode: "RateCode",
            refCode: "PLAN_RATE_CODE"
        };

        DataService.CommonCommonDelete(JSON.stringify(postJSON)).then(async function (response) {
            if (response.status == 200) {
//                 var successMessage = (isMultiple ? vm.$t('DeletedSuccessMultiple') : vm.$t('DeletedSuccess'));
// debugger;
//                 vm.ShowAlert(vm.$t(successMessage, [vm.$t('RateCode')]), 'success', true, vm.$t('Alert'));
                // vm.ShowAlert(successMessage, "success", true, vm.$t("Alert"));
                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('RateCode')]), "Success", true, vm.$t('Alert'));
                $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');

                if (typeof vm.updateDeleteButtonState === 'function') {
                    vm.updateDeleteButtonState();
                }

                await vm.GetWorkCodeMappingList();
            } else {
                vm.ShowAlert(vm.$t('DeletedError', [vm.$t('RateCode')]), "failure", true, vm.$t("Alert"));
            }
        }).catch(function(err){
            console.error("Delete API error:", err);
            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('RateCode')]), "failure", true, vm.$t("Alert"));
        });
    });
},

  updateDeleteButtonState: function () {
        var vm = this;
        var checkedCount = $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").length;
        var deleteButton = vm.Buttons.find(btn => btn.callbackfunction === 'DeleteAll');
        if (deleteButton) {
            deleteButton.isdisabled = checkedCount === 0;
        }
    },





        // DeleteAll: function (event) {
        //     debugger
        //     var vm = this;
        //     var val = "";
        //     $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
        //         if (val.length > 0)
        //             val += ',';
        //         val += $(this).val();
        //     });
        //     if (val.length > 0) {
        //         vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Report")), function () {
        //             var postJSON = {
        //                 ids: val,
        //                 moduleName: "RateCode",
        //                 refCode: "PLAN_RATE_CODE"
        //             };
        //             var objectPostString = JSON.stringify(postJSON);
        //             debugger
        //             DataService.CommonCommonDelete(objectPostString).then(async function (response) {
        //                 if (response.status == 200) {
        //                     $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
        //                     $("#DeleteMultiple").addClass("disabled");
        //                     vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Category')]), "success", true, vm.$t("Alert"));
        //                     vm.GetWorkCodeMappingList();
        //                 }
        //                 else {
        //                     vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Category')]), "failure", true, vm.$t("Alert"));
        //                 }

        //             },);
        //         });
        //     }
        //     else {
        //         vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "failure", true, vm.$t("Alert"));
        //     }

        // },
    }
}
</script>
<style >
.task-dec {
    color: #007bff !important;
    text-decoration: none;
    background-color: transparent;
}
td.single-action{width: 120px;}
</style>