<template>
    <section>
        <tg-list :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="Ticketing"
            IdentityColumn="plan_inventory_id" :HeaderText="$t('Inventory')" :listType="['List']" :LegendArray="[]"
            :ListData="PlanInventoryListing" :RenderRowActionMethod="renderActions"
            v-on:ActionButtonClick="actionButtonClick" :HeaderData="Headers" :TotalRecords="TotalRecords"
            :ListDataCallBackFunction="FetchRecords" :showCheckBox="false" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" :HeaderButtons="HeaderButtons" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'product_code'">
                    {{ data.row.product_code }}
                    <!-- <input type="text" class="form-control" disabled id="product_code" name="product_code"
                        :value="data.row.product_code"> -->
                </template>
                <template v-if="data.column.COLUMN_NAME == 'product_type'">
                    {{ data.row.product_type }}
                    <!-- <input type="text" class="form-control" disabled id="product_type" name="product_type"
                        :value="data.row.product_type"> -->
                </template>
                <template v-if="data.column.COLUMN_NAME == 'product_name'">
                    {{ data.row.product_name }}
                    <!-- <input type="text" class="form-control" disabled id="product_name" name="product_name"
                        :value="data.row.product_name"> -->
                </template>
                <template v-if="data.column.COLUMN_NAME == 'quantity'">
                    <input type="text" class="form-control" id="quantity" name="quantity" v-model="data.row.quantity">
                </template>
            </template>
        </tg-list>
        <AddInventory v-if="isExistingInventory" :callbackfunction="FetchRecords" :PlanId="PlanId">
        </AddInventory>
    </section>
</template>

<script>
import AddInventory from "../../components/Modals/AddInventory.vue";
import DataServices from "../../services/DataService";

export default {
    components: { AddInventory },
    data() {
        return {
            noRecords: false,
            TotalRecords: 0,
            CurrentPage: 1,
            PlanId: 0,
            isExistingInventory: false,
            TotalPages: 0,
            StatusListForListing: [
                { STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
                { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }
            ],
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            GroupStart: 1,
            GroupEnd: 3,
            Headers: [
                { "COLUMN_NAME": "product_code", "DATA_TYPE": "string", "SORTABLE": false, "VISIBLE": true, "DISPLAY_NAME": this.$t('ProductCode'), "DISPLAY_ORDER": 1 },
                { "COLUMN_NAME": "product_type", "DATA_TYPE": "string", "SORTABLE": false, "VISIBLE": true, "DISPLAY_NAME": this.$t('ProductType'), "DISPLAY_ORDER": 2 },
                { "COLUMN_NAME": "product_name", "DATA_TYPE": "string", "SORTABLE": false, "VISIBLE": true, "DISPLAY_NAME": this.$t('ProductName'), "DISPLAY_ORDER": 3 },
                { "COLUMN_NAME": "quantity", "DATA_TYPE": "string", "SORTABLE": false, "VISIBLE": true, "DISPLAY_NAME": "Quantity", "DISPLAY_ORDER": 4 }
            ],
            HeaderButtons: [
                {
                    title: 'SaveInventory',
                    iconClass: 'fa fa-save',
                    callbackfunction: 'saveInventory',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                    checkPrivilege: true,
                    id: 'SaveInventory',
                },
                {
                    title: 'SelectInventory',
                    iconClass: 'fa fa-plus',
                    callbackfunction: 'openSelectInventory',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                    checkPrivilege: true,
                    id: 'SelectInventory',
                },
                {
                    title: 'BacktoList',
                    iconClass: 'fa fa-angle-double-left',
                    href: '/Plan/PlanListing',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                    checkPrivilege: true,
                    id: 'BackToList'
                }
            ],
            listActions: [
                {
                    title: 'Delete',
                    href: 'javascript:void(0)',
                    icon: 'fa fa-trash',
                    callbackfunction: 'DeleteService',
                    additionalSpan: ''
                }
            ],
            PlanInventoryListing: []
        }
    },
    // updated() {
    //     const divElement = document.getElementsByClassName('theme-primary partition p-actions-expand');
    //     if (divElement[0] && divElement[0].lastChild.className != 'p-name text-white') {
    //         const spanElement = document.createElement('span');
    //         spanElement.className = "p-name text-white";
    //         spanElement.textContent = this.$t("Inventory");
    //         divElement[0].appendChild(spanElement);
    //     }
    // },
    created: async function () {
        this.PlanId = parseInt(this.$route.params.id);
        await this.FetchRecords();
    },
    methods: {
        FetchRecords: async function () {
            const url = `planId=${this.PlanId}&sortBy=${this.SortBy}&sortExp=${this.SortExp}&pagesize=${this.PageSize}&pagenumber=${this.PageNumber}`;
            try {
                const response = await DataServices.GetPlanInventoryListing(url);
                if (response.data && response.data.result) {
                    debugger;
                    this.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "product_code") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "product_type") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "product_name") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "quantity") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });
                    if (response.data.result.data.length > 0) {
                        this.PlanInventoryListing = response.data.result.data;
                        this.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                        this.TotalPages = Math.ceil(this.TotalRecords / this.PageSize);
                        this.CurrentPage = this.PageNumber;
                        this.noRecords = false;
                    } else {
                        this.noRecords = true;
                        this.TotalRecords = 0;
                        this.PlanInventoryListing = [];
                    }
                } else {
                    this.noRecords = true;
                    this.TotalRecords = 0;
                    this.PlanInventoryListing = [];
                }
                setTimeout(() => { this.ResponsiveDataTable('tablelistingdata'); }, 500);
            } catch (error) {
                console.error('Error fetching records:', error);
                this.noRecords = true;
                this.TotalRecords = 0;
                this.PlanInventoryListing = [];
            }
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            this.SortExp = this.SortExp === 'ASC' ? 'DESC' : 'ASC';
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        },
        commonHeaderButtonClick: function (event) {
            if (event.callbackfunction === "openSelectInventory") {
                this.openSelectInventory();
            }
            else if (event.callbackfunction === "saveInventory") {
                this.SaveData();
            }
        },
        openSelectInventory: function () {
            this.isExistingInventory = true;
        },
        // saveInventory: function () {
        //     this.isExistingInventory = true;
        // },
        renderActions: function (row) {
            return this.listActions.map(actionItem => ({
                ...actionItem,
                isVisible: true
            }));
        },
        actionButtonClick(callbackfunction, item) {
            if (callbackfunction === 'DeleteService') {
                this.DeleteInventoryData(item.plan_inventory_id);
            }
        },
        DeleteInventoryData: function (planInventoryId) {
            if (planInventoryId > 0) {
                this.confirmR(this.$t("ConfirmDelete"), true, `${this.$t("Delete")} ${this.$t("Service")}`, () => {
                    const postJSON = {
                        ids: planInventoryId,
                        moduleName: "BILLING",
                        refCode: "TALYGEN_PLAN_INVENTORY"
                    };
                    DataServices.CommonCommonDelete(JSON.stringify(postJSON)).then(response => {
                        if (response.status === 200) {
                            this.FetchRecords();
                            this.ShowAlert(this.$t('DeletedSuccess', [this.$t('Inventory')]), "Success", true, this.$t('Alert'));
                        } else {
                            this.ShowAlert(this.$t('DeletedError', [this.$t('Inventory')]), "failure", true, this.$t('Alert'));
                        }
                    });
                });
            } else {
                this.ShowAlert(this.$t("Selectanyrecordtodelete"), "warning", true, this.$t("Alert"));
            }
        },
        async SaveData() {
            debugger;
            var vm = this;
            const updatedInventory = this.PlanInventoryListing.map(item => ({
                plan_inventory_id: item.plan_inventory_id,
                quantity: item.quantity,
                asset_catalogue_id: item.asset_catalogue_id
            }));
            const obj = JSON.stringify(updatedInventory);
            const requestPayload = { objData: obj, planId: vm.PlanId };
            await DataServices.SavePlanInventory(requestPayload).then(response => {
                debugger;
                if (response.data != null) {
                    if (response.data.Status == "Success" && response.data.Code == "1") {
                        vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Inventory')]), "Success", true, vm.$t('Alert'));
                        vm.FetchRecords();
                        vm.isLoading = false;
                    }
                    else {
                        vm.ShowAlert(vm.$t("ErrorWhileSaving", [vm.$t("Inventory")]), "danger", true, vm.$t("Alert"));
                        vm.isLoading = false;
                    }
                }
            });
        }
    }
}
</script>
