<template>
    <section>
      <div class="my-popups">
        <div class="modal d-block">
          <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
              <div class="modal-header theme-primary partition-full d-flex">
                <h5 class="modal-title">Price Change Detection</h5>
                <button type="button" class="close" v-on:click="Close">
                  <em class="fa fa-times" aria-hidden="true"></em>
                </button>
              </div>
              <div class="modal-body">
                    <div class="row">
                        <div class="col-md-12">
                        <p class="alert alert-secondary">A new price change has been detected. Would you like to apply retroactive adjustments? Click 'Yes' to adjust past invoices, or 'No' to save the new pricing for future invoices.</p>
                        </div>
                    </div>
                    <div class="col-lg-12 float-left" v-if="isCheckYesOrNo">
                        <div class="row flex-row-reverse mt-2">
                            <div class="col-lg-6 text-right">
                                <a href="javascript:;" :title="$t('Yes')" v-on:click="CheckAdjustment('Yes')" class="btn btn-primary save-btn mr-2"><em class="fa fa-check pr-2" aria-hidden="true"></em>{{ $t('Yes') }}</a>
                                <a href="javascript:;" class="btn btn-primary delete-btn" v-on:click="CheckAdjustment('No')" :title="$t('No')"><em class="fa fa-times pr-2" aria-hidden="true"></em>{{ $t('No') }}</a>
                            </div>
                        </div>
                    </div>
                    <div v-show="isCheckAdjustment">
                        <dynamic-form lang="en" ref="validateCheckAdjustment" :buttons="buttons" :schema="FormSchema"></dynamic-form>
                    </div>
                  
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
</template>
<script>
export default {
    props: {
        retroActDetail: {
            type: Function,
            required: false
        },
        refCodeRetro: {
            type: String,
            required: false
        },
    },
    data() {
        return {
            isLoading: false,
            isCheckYesOrNo:true,
            isCheckAdjustment:false,
            FormSchema: [
                {
                    group_name: "Select Past Months for Adjustment:",
                    layoutType: "double",
                    Data: [
                        
                        {
                            astype: "DateField",
                            label: this.$t('StartDate'),
                            name: "StartDate",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                // minDate: new Date(),
                                maxDate: false
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EndDate'),
                            name: "EndDate",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                // minDate: new Date(),
                                maxDate: false
                            },
                            validationRules: { "required": true },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "RadioField",
                            label: 'What would you like to do with past invoices?',
                            name: "chkPastInvoice",
                            value: 1,
                            config:{
                                options:[{value:'1',name:'Create new invoices for affected months'},{value:'2',name:'Adjust in next billing cycle'}]
                            },
                            placeholder: "Select",
                            validationRules: { "required": false,},
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    value: 'Y',
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    onClick: this.SaveData
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ]
        }
    },
    created: async function () {
        var vm =this;
        if(vm.refCodeRetro == "Update"){
            vm.BindEditRetroActiveDetail(vm.retroActDetail);
        }
    },
    methods: {
        BindEditRetroActiveDetail(data){
            var vm = this;
            this.isCheckAdjustment = true;
            this.isCheckYesOrNo = false;
            vm.FormSchema[0].Data[0].value = data.StartDate;
            vm.FormSchema[0].Data[1].value = data.EndDate;
            vm.FormSchema[1].Data[0].value = data.ActionType;
        },
        CheckAdjustment(type) {
             if(type == "Yes"){
                this.isCheckAdjustment = true;
                this.isCheckYesOrNo = false;
             }
             else{
                this.isCheckAdjustment = false;
                this.$parent.isModalRetroactive = false;
                this.Close();
             }
        },
        Close: function() {
          var vm = this;
          vm.$parent.isModalRetroactive = false;
          this.$emit('cancelRetroActiveList', false);
        },
        async SaveData() {
            var vm = this;
            const isValid = await vm.$refs.validateCheckAdjustment.validateForm.validate();
            if (!isValid) {
                return;
            }
            var startDate = vm.FormSchema[0].Data[0].value;
            var endDate = vm.FormSchema[0].Data[1].value;
            var chkPastInvoice = vm.FormSchema[1].Data[0].value;
            var objData ={StartDate: startDate,EndDate: endDate,ActionType: chkPastInvoice}
            var obj = JSON.stringify(objData);
            this.$emit('retroActiveDetail', obj);
            }
    }
}
</script>
<style>
        .alert {
            background-color: #e9ecef;
            border: 1px solid #ced4da;
            border-radius: 0.25rem;
            padding: 1rem;
            font-size: 1.1rem;
            color: #050505;
        }
</style>