<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div id="content" class="modal-content pt-0">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('ManageBillingRule') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>

                    </div>
                    <div class="modal-body">
                        <div class="theme-primary partition p-actions-expand">
                            <span class="p-actions float-right">
                                <a class="p-action-btn text-white" title="" id="AddBillingRules" href="javascript:;"
                                    data-toggle-tooltip="tooltip" data-original-title="Add Billing Rule"
                                    v-on:click="commonHeaderButtonClick('AddBillingRule')">
                                    <em class=" fa fa-plus pr-2"></em> <br />{{
                                        $t("AddBillingRule") }}
                                </a>
                            </span>
                        </div>

                        <!-- <tg-list :IsShowAction="false" :showCheckBox="true" ModuleName="BillingRule"
                            :listType="['List']" :RenderRowActionMethod="renderActions"
                            v-on:ActionButtonClick="actionButtonClick" SubModuleCode=""
                            IdentityColumn="CONFIGURATION_ID" HeaderText="" :ListData="BillingRuleData"
                            :HeaderData="Headers" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons"
                            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" IsSearchFieldsOnTop="true"
                            SearchPosition="top" :ListDataCallBackFunction="FetchData" :LegendArray="[]"
                            :SortExp="sortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                            <template v-slot:slotdata="{ data }">
                                <template v-if="data.column.COLUMN_NAME == 'CATEGORY_NAME'">
                                    <td class="text-center single-action tourguide_a"
                                        v-if="data.row.CATEGORY_NAME != null | data.row.CATEGORY_NAME != ''">
                                        {{ data.row.CATEGORY_NAME }}
                                    </td>
                                    <td class="text-center single-action tourguide_a" v-else>--</td>
                                </template>
<template v-if="data.column.COLUMN_NAME == 'EFFICATIVE_DATE_FROM'">
                                    <td class="text-center single-action tourguide_a"
                                        v-if="data.row.EFFICATIVE_DATE_FROM">
                                        {{ $options.filters.formatDate(data.row.EFFICATIVE_DATE_FROM) }}
                                    </td>
                                    <td class="text-center single-action tourguide_a" v-else>--</td>
                                </template>
<template v-if="data.column.COLUMN_NAME == 'EFFICATIVE_DATE_TO'">
                                    <td class="text-center single-action tourguide_a"
                                        v-if="data.row.EFFICATIVE_DATE_TO">
                                        {{ $options.filters.formatDate(data.row.EFFICATIVE_DATE_TO) }}
                                    </td>
                                    <td class="text-center single-action tourguide_a" v-else>--</td>

                                </template>
</template>
</tg-list> -->

                        <div class="listing mt-2">
                            <div class="table-responsive table-fix-header">
                                <table class="table table-bordered">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th style="width: 20px;"></th>
                                            <th v-for="item in Headers" :key="item.COLUMN_NAME">
                                                {{ item.DISPLAY_NAME }}
                                            </th>
                                            <th class="text-center">{{ $t('View') }}</th>
                                        </tr>
                                    </thead>

                                    <tbody>
                                         <tr v-if="BillingRuleData.length === 0">
                                            <td colspan="8" class="text-center text-danger">{{ $t("NoRecordfound") }}</td>
                                        </tr>
                                        <tr v-for="(item, index) in BillingRuleData" :key="index">
                                            <td>
                                                <div class="custom-control custom-radio custom-control-inline">
                                                    <input type="radio" :name="'RadioForCondition'" :id="'RadioForCondition' + item.CONFIGURATION_ID"
                                                        v-model="ConditionSelected" :value="item.CONFIGURATION_ID" class="custom-control-input">
                                                    <label class="custom-control-label" :for="'RadioForCondition' + item.CONFIGURATION_ID">
                                                        {{ item.CONFIGURATION_NAME }}
                                                    </label>
                                                </div>
                                                <!-- <div class="custom-control custom-radio custom-control-inline">
                                                    <input type="radio" :value="item.CONFIGURATION_ID"
                                                        v-model="ConditionSelected"
                                                        class="chkItems custom-control-input"
                                                        :name="'RadioForCondition'" />
                                                    <label type="radio" class="custom-control-label"></label>
                                                </div> -->
                                            </td>

                                            <td>{{ item.RULE_NAME }}</td>
                                            <!-- <td>{{ item.CLIENT_NAME }}</td> -->
                                            <td>{{ item.SERVICE_NAME }}</td>

                                            <!-- <td>{{ item.UTILITYNAME }}</td>
                                            <td>{{ item.CATEGORY_NAME }}</td> -->
                                            <td>{{ item.STATENAME }}</td>
                                            <td>{{ item.EFFICATIVE_DATE_FROM != null ? $d(item.EFFICATIVE_DATE_FROM) :
                                                '--' }}</td>
                                            <td>{{ item.EFFICATIVE_DATE_TO != null ? $d(item.EFFICATIVE_DATE_TO) : '--'
                                                }}</td>
                                            <td>{{ item.CREATED_BY_NAME }}</td>
                                            <td class="text-center single-action">
                                                <a @click="BindModelChart(item)" title="View Details">
                                                    <em class="fa fa-eye text-info"></em>
                                                </a>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                            <!-- Pagination -->
                            <Pager :numberofrecords="TotalRecords" :prevText="$t('Previous')" :nextText="$t('Next')"
                                :callbackfunction="FetchData" :firstText="$t('First')" :lastText="$t('Last')"
                                v-on:SetCurrentPage="SetCurrentPage">
                            </Pager>
                        </div>
                        <!-- open chart ModelK -->

                        <!-- Clone Model  Start-->
                        <div class="my-popups">
                            <loader :is-visible="isLoading" />

                            <div class="modal d-block" v-if="isCloneModel">
                                <div class="modal-dialog modal-dialog-centered">
                                    <div class="modal-content">
                                        <div
                                            class="modal-header theme-primary partition-full d-flex justify-content-between align-items-center">
                                            <h5 class="modal-title">
                                                {{ 'CloneRule' }}
                                            </h5>
                                            <button type="button" class="close" @click="CloseCloneModel">
                                                <em class="fa fa-times" aria-hidden="true"></em>
                                            </button>
                                        </div>

                                        <div class="modal-body">
                                            <dynamic-form lang="en" ref="validatePlan" :buttons="buttons"
                                                :schema="FormSchema" :mandatorytext="$t('MandatoryString')">
                                            </dynamic-form>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <a href="javascript:;" type="submit" class="btn btn-primary save-btn mr-2"
                            @click="SaveMapRuleData();" title="Save"><em class="fa fa-save pr-2"
                                aria-hidden="true"></em><span>{{ $t('Save') }}</span></a>
                        <a href="javascript:;" type="submit" class="btn btn-primary delete-btn" @click="Close"
                            title="Cancel"> <em class="fa fa-times pr-2" aria-hidden="true"></em>
                            <span>{{ $t('Cancel') }}</span></a>
                    </div>
                </div>
            </div>
        </div>
        <!-- :modelDetail="modelDetail" -->
        <VerticalFlowChartModel v-if="modelopen" :modelDetail="modelDetail">
        </VerticalFlowChartModel>
        <ModalManageBillingConfiguration v-if="isModalBillingConfiguration" :serviceId="serviceId" reqType="fromServiceMapping"></ModalManageBillingConfiguration>
    </div>
</template>
<script>
import Schema from './../../views/BillingRule/BillingRuleModalSchema';
import DataService from '../../services/DataService';
import Pager from '../Common/Pager.vue';
import VerticalFlowChartModel from './VerticalFlowChartModel.vue';
import ModalManageBillingConfiguration from './ModalManageBillingConfiguration.vue';
export default {
    components: { Pager, VerticalFlowChartModel,ModalManageBillingConfiguration },
    props: {
        servicePlanId: {
            type: Object,
            required: true
        },
        serviceId: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            isModalBillingConfiguration: false,
            isLoading: false,
            modelopen: false,
            ConditionSelected: 0,
            BillingRuleData: [],
            isCloneModel: false,
            listActions: Schema.listActions,
            listheaderbuttons: Schema.buttons,
            cloneItem: [],
            leftSearchFields: [{
                fieldName: 'Search',
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName: "tagname",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            StatusListForListing: [
                {
                    STATUS: "Active",
                    STATUS_ID: 1001,
                    STATUS_COLOR: "#ffffff",
                    STATUS_BG_COLOR: "#28a745",
                },
                {
                    STATUS: "Inactive",
                    STATUS_ID: 1002,
                    STATUS_COLOR: "#ffffff",
                    STATUS_BG_COLOR: "#d72435",
                },
            ],
            Headers: [
                {
                    "COLUMN_NAME": "RULE_NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('BillingRuleName'),
                    "DISPLAY_ORDER": 1,
                },
                // {
                //     "COLUMN_NAME": "CLIENT_NAME",
                //     "DATA_TYPE": "text",
                //     "SORTABLE": true,
                //     "VISIBLE": true,
                //     "DISPLAY_NAME": this.$t('Contract'),
                //     "DISPLAY_ORDER": 2,
                // },
                // {
                //     "COLUMN_NAME": "UTILITYNAME",
                //     "DATA_TYPE": "text",
                //     "SORTABLE": false,
                //     "VISIBLE": true,
                //     "DISPLAY_NAME": this.$t('UtilityName'),
                //     "DISPLAY_ORDER": 3,
                // },
                // {
                //     "COLUMN_NAME": "CATEGORY_NAME",
                //     "DATA_TYPE": "text",
                //     "SORTABLE": true,
                //     "VISIBLE": true,
                //     "DISPLAY_NAME": this.$t('Category'),
                //     "DISPLAY_ORDER": 4,
                // },
                 {
                    "COLUMN_NAME": "ServiceName",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ServiceName'),
                    "DISPLAY_ORDER": 5,
                },
                {
                    "COLUMN_NAME": "STATENAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('STATE'),
                    "DISPLAY_ORDER": 5,
                },
                {
                    "COLUMN_NAME": "EFFICATIVE_DATE_FROM",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('EffectiveFrom'),
                    "DISPLAY_ORDER": 6,
                },
                {
                    "COLUMN_NAME": "EFFICATIVE_DATE_TO",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('EffectiveTo'),
                    "DISPLAY_ORDER": 7,
                },
                {
                    "COLUMN_NAME": "CREATED_BY_NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('CreatedBy'),
                    "DISPLAY_ORDER": 9,
                }

            ],
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('RuleName'),
                            name: 'RuleName',
                            palceholder: "Rule Name",
                            value: "",
                            validationRules: {
                                required: true
                            },
                            disabled: false,
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('CompanyName'),
                            name: "CompanyName",
                            value: "",
                            placeholder: "Select",
                            config: {
                                options: []
                            },
                            validationRules: {
                                required: true
                            },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EffectiveFrom'),
                            name: "Effectivefrom",
                            value: new Date().toLocaleDateString(),
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                minDate: new Date(),
                                onChange: this.setTimerDate,
                                maxDate: false
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EffectiveTo'),
                            name: "Effectiveto",
                            value: new Date('12/31/2099').toLocaleDateString(),
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                minDate: new Date(),
                                onChange: this.setTimerDate,
                                maxDate: false
                            },
                            validationRules: { "required": false },
                        }, {
                            astype: "RadioField",
                            label: this.$t('Is Rule Applicable to All Utilities'),
                            name: "allutilities",
                            value: "0",
                            config: {
                                options: [{ value: "1", name: "Yes" }, { value: "0", name: "No" }],
                                onChange: this.Ddlutilitys
                            },
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Contract Name'),
                            name: "ContractName",
                            value: "",
                            placeholder: "Select",
                            config: {
                                options: [],
                                onChange: this.GetUtility
                            },
                            validationRules: {
                                required: true
                            },
                        },
                        {
                            astype: "MultiSelectField",
                            label: this.$t('Utility Name'),
                            name: "UtilityName:",
                            value: "",
                            placeholder: "Select",
                            visibility: true,
                            config: {
                                options: [
                                    {
                                        name: 'select',
                                        value: 1
                                    }
                                ]
                            },
                            validationRules: {
                                required: true
                            },
                        },

                    ],

                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    onClick: this.CreateClone
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.CloseCloneModel,
                },
            ],
            leftSearchFields: Schema.leftsearchSchema,
            searchByName: '',
            searchByClientName: '',
            utilities: '',
            searchByState: [],
            SortBy: '',
            SortExp: '',
            noRecords: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            searchCondition: '',
            ListingData: [],
            modelDetail: []
        }
    },
    created: async function () {
        //debugger
        var vm = this;
        vm.FetchData();
        // vm.getConditionRecords();   
        vm.GetCompanylist();
        vm.GetGroupList();
        //await Schema.bindleftCommonSearchdropdown(this);
    },
    methods: {
        BindModelChart(item) {
            debugger
            var vm = this;
            vm.modelopen = true;
            vm.modelDetail = item;
        },
        FetchData: async function () {
            debugger
            var vm = this;
            const url = `searchByName=${vm.searchByName}&searchByClientName=${vm.searchByClientName}&utilities=${vm.utilities}&searchByState=${vm.searchByState}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&Plan_service_id=${vm.servicePlanId}`
            try {
                const response = await DataService.GetBillingRuleList(url);
                if (response.data && response.data.length > 0) {
                    vm.Headers.forEach((column) => {
                        column.settings = null;

                        if (["EFFICATIVE_DATE_FROM", "EFFICATIVE_DATE_TO", "STATUS_ID"].includes(column.COLUMN_NAME)) {
                            column.settings = { isInSlot: true };
                        }
                    });
                    response.data.forEach(element => {
                        ["EFFICATIVE_DATE_FROM", "EFFICATIVE_DATE_TO"].forEach((key) => {
                            if (element[key]) {
                                element[key] = new Date(element[key]);
                            }
                        });
                    });
                    debugger
                    vm.BillingRuleData = response.data;
                    vm.TotalRecords = response.data[0].TOTAL_RECORDS;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.noRecords = false;

                } else {
                    vm.BillingRuleData = [];
                    vm.TotalRecords = 0;
                    vm.noRecords = true;
                }

            } catch (error) {
                console.error("Error fetching data:", error);
            }
        },
        SetCurrentPage: function (x) {
            this.PageNumber = x.PageNumber;
            this.PageSize = x.PageSize;
        },
        getConditionRecords: function (x, y) {
            var vm = this;
            debugger
            if (vm.BillingRuleData != null) {
                vm.BillingRuleData.forEach(function (item) {
                    item.isLeafChecked = false;
                    item.showPopup = false;
                    item.rateCode = "";
                    item.selectedFlag = { value: 'grey', color: 'grey' };
                })
                vm.conditions = vm.BillingRuleData;
                vm.TotalRecords = vm.conditions[0].TOTALRECORDS
            }

        },
        GetUtility() {
            //debugger
            var vm = this;
            var id = vm.cloneItem.CONFIGURATION_ID;
            var contractname = vm.$refs.validatePlan.getPropertyValue("ContractName", "value");
            var url = `clientId=${contractname}&configId=${id}&reqFrom=${''}&clientsIds=`;
            DataService.GetDdlUtility(url).then((response) => {
                if (response.data != '') {
                    result = response.data;
                    result.forEach(element => {
                        let obj = {
                            name: element.UTILITY_NAME,
                            value: element.UTILITY_ID
                        }
                        vm.FormSchema[0].Data[6].config.options.push(obj);
                    });
                }
            });

        },
        CreateClone: async function () {
            var vm = this;
            const isValid = await vm.$refs.validatePlan.validateForm.validate();
            if (!isValid) {
                this.isLoading = false;
                return;
            }
            var id = vm.cloneItem.CONFIGURATION_ID;
            var serviceid = vm.cloneItem.SERVICE_ID;
            var rulename = vm.$refs.validatePlan.getPropertyValue("RuleName", "value");
            var companyname = vm.$refs.validatePlan.getPropertyValue("CompanyName", "value");
            var effectivefrom = vm.$refs.validatePlan.getPropertyValue("Effectivefrom", "value");
            var effectiveto = vm.$refs.validatePlan.getPropertyValue("Effectiveto", "value");
            var contractname = vm.$refs.validatePlan.getPropertyValue("ContractName", "value");
            var isruleapplication = vm.$refs.validatePlan.getPropertyValue("allutilities", "value");
            var selectUtility = vm.$refs.validatePlan.getPropertyValue("UtilityName", "value");
            const obj = {
                Id: id,
                ServiceId: serviceid,
                Client_Id: contractname,
                RuleName: rulename,
                CompanyName: companyname,
                EffectiveFrom: effectivefrom,
                EffectiveTo: effectiveto,
                ContractName: contractname,
                isRuleApplication: isruleapplication,
                SelectedUtility: selectUtility,
            }
            var jsonData = JSON.stringify(obj);
            await DataService.CreateRuleClone(jsonData).then((response) => {
                if (response.data != '') {
                    if (response.data == 0) {
                        this.ShowAlert(this.$t('AddedSuccess', [this.$t('Clone')]), "Success", true, this.$t('Alert'));
                        vm.FetchData();
                        vm.cloneItem = [];
                        vm.CloseCloneModel();
                    }
                    else {
                        this.ShowAlert(this.$t('ErrorWhileSaving', [this.$t('Clone')]), "danger", true, this.$t('Alert'));
                    }
                }
            })

        },
        Ddlutilitys() {
            var utilityselectval = this.$refs.validatePlan.getPropertyValue("allutilities", "value");
            switch (utilityselectval) {
                case "1":
                    this.FormSchema[0].Data[6].visibility = false;
                    break;
                default:
                    this.FormSchema[0].Data[6].visibility = true;
            }
        },
        setTimerDate: function (x, y, z) {
            var vm = this;
            if (y.name == "Effectivefrom") {
                vm.$refs.validatePlan.updateProperty("Effectivefrom", "config.minDate", x);
                vm.$refs.validatePlan.UpdateKeyValue();
            }
        },
        GetCompanylist() {
            var vm = this;

            DataService.GetDdlCompanyList().then((response) => {
                if (response.data != '') {
                    if (response.data.length) {

                        response.data.forEach(element => {
                            var obj = {
                                name: element.Company_Name,
                                value: element.company_id
                            }
                            vm.FormSchema[0].Data[1].config.options.push(obj);
                        })
                    }
                }
            })
        },
        GetGroupList() {
            var vm = this;
            DataService.GetDdlGroupList().then((response) => {
                if (response.data) {
                    if (response.data.length) {
                        response.data.forEach(element => {
                            var obj = {
                                name: element.CHILD_NAME,
                                value: element.CHILD_USER_ID
                            }
                            vm.FormSchema[0].Data[5].config.options.push(obj);
                        });
                    }
                }
            })
        },

        DeleteData: function () {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("SERVICE")), function () {
                    let url = `ids=${val}&tableName=TALYGEN_BE_CLIENT_CONFIGURATION&primaryKey=CONFIGURATION_ID`;
                    DataService.Delete(url).then(response => {
                        if (response.data == "DeletedSuccess") {
                            vm.ShowAlert(vm.$t('DeletedSuccessfully'), "Success", true, vm.$t('Alert'));
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('val')]), "failure", true, vm.$t('Alert'));
                        }
                        vm.FetchData();
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "failure", true, vm.$t("Alert"));
            }
        },
        renderActions(row) {
            let rtnActionArray = [];
            this.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                action.isVisible = true;
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (value.searchCondition) {
                let str = value.searchConditionJson;
                const regex = /(RULE_NAME|CLIENT_NAME|UTILITYNAME|STATENAME)\s*(?:LIKE\s+'?%?|=)\s*([^%')\s]+).*?/i;
                const m = str.match(regex);
                const column = m[1];
                const values = m[2];
                switch (column) {
                    case "RULE_NAME":
                        this.searchByName = values;
                        break;
                    case "CLIENT_NAME":
                        this.searchByClientName = values;
                        break;
                    case "UTILITYNAME":
                        this.utilities = values
                        break;
                    case "STATENAME":
                        this.searchByState = values;
                        break;
                }
            } else {
                this.searchByState = [];
                this.utilities = "";
                this.searchByClientName = "";
                this.searchCondition = "";
            }
            this.FetchData();
        },
        actionButtonClick: function (callbackfunction, item, index, event) {

            switch (callbackfunction) {
                case "ViewHistory":
                    this.OpenHistoryModal(item);
                    break;
                case "ResourceImpact":
                    this.ResourceImpact(item);
                    break;
                case "CloneRule":
                    this.cloneItem = item,
                        this.CloneRule(item);
                    break;
            }
        },
        CloneRule(item) {
            var vm = this;
            vm.isCloneModel = true;
        },
        CloseCloneModel() {
            var vm = this;
            vm.$refs.validatePlan.updateProperty("RuleName", "value", "");
            vm.$refs.validatePlan.updateProperty("CompanyName", "value", "");
            vm.$refs.validatePlan.updateProperty("ContractName", "value", "");
            this.isCloneModel = false;
            this.cloneItem = [];
            this.FetchData();
        },
        Close: function () {
            this.$parent.IsRuleListing = false;
        },
        commonHeaderButtonClick: function (event) {
            debugger
            switch (event) {
                case "AddBillingRule":
                    this.AddBillingRule();
                    break;
                case "ExportToExcel":
                    this.DownloadExcelData();
                    break;
                case "DeleteRecord":
                    this.DeleteData();
                    break;
            }
        },
        sortdata: function (sortBy) {

            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        getStatusClassList: function (status) {

            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center text-white d-block bg-success';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center text-white d-block bg-danger';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        AddBillingRule: function () {
            var vm = this;
            vm.isModalBillingConfiguration = true;
            // vm.$router.push({
            //     name: 'ManageConfiguration',
            //     params: { id: 0, serviceId: vm.serviceId }, query: { reqFrom: vm.$route.path }
            // });
        },
        SaveMapRuleData: function () {
            //debugger
            var vm = this;
            var selectedColumns = vm.ConditionSelected;
            // $.each(this.BillingRuleData, function (key, item) {
            //     if (item.isSelected == true) {
            //         selectedColumns = selectedColumns + ((selectedColumns == "") ? item.CONFIGURATION_ID : ("," + item.CONFIGURATION_ID));
            //     }
            // });
            if (selectedColumns) {
                var servicePlanId = vm.servicePlanId;
                var data = {
                    servicePlanId: servicePlanId,
                    SELECTEDCOLUMNS: selectedColumns
                };
                var objectPostString = JSON.stringify(data);
                DataService.SaveMapData(objectPostString).then(function (response) {
                    //debugger
                    if (response.data.status == "Success") {
                        vm.$parent.FetchRecords();
                        vm.Close();
                    }
                })
            } else {
                vm.ShowAlert(vm.$t("SelectRuleMapping"), "warning", vm.$t('Alert'));
            }
        }

    }
}
</script>