<template>
    <section id="main-content" class="main-content">
        <div class="card mb-3 probx">
            <div class="card-body">
                <div class="row">

                    <div class="col-md-4 col-lg-3">
                        <div class="form-group">
                            <label><b>{{ $t('UtilityType') }}: </b> <span
                                    v-if="planDetail[0].PLAN_CODE == 'UTILITY_BILLING'">Utility Billing</span>
                                <span v-else-if="planDetail[0].PLAN_CODE == 'SUBSCRIPTION_BILLING'">Subscription
                                    Billing</span></label>
                        </div>
                    </div>
                    <div class="col-md-4 col-lg-3" v-if="planDetail[0].PLAN_CODE != 'SUBSCRIPTION_BILLING'">
                        <div class="form-group">
                            <label><b>{{ $t('MeterType') }}: </b> {{ planDetail[0].METER_TYPE }}</label>
                        </div>
                    </div>
                    <div class="col-md-4 col-lg-3" v-if="planDetail[0].PLAN_CODE != 'SUBSCRIPTION_BILLING'">
                        <div class="form-group">
                            <label><b>{{ $t('CustomerType') }}: </b> {{ planDetail[0].CUSTOMER_SEGMENT }}</label>
                        </div>
                    </div>

                    <div class="col-md-4 col-lg-3">
                        <div class="form-group">
                            <label><b>{{ $t('PlanType') }}: </b>
                                <span v-if="planDetail[0].IS_FOR_PLAN == '1'">
                                    Prepaid
                                </span>
                                <span v-if="planDetail[0].IS_FOR_PLAN == '2'">
                                    Postpaid
                                </span>
                            </label>
                        </div>
                    </div>
                     <div class="col-md-4 col-lg-3">
                        <div class="form-group">
                            <label><b>{{ $t('Status') }}: </b>
                                <span v-bind:class="getStatusClassList(planDetail[0].STATUS_NAME, 'STATUS_NAME')"
                                    style="float:right;">
                                    {{ planDetail[0].STATUS_NAME }}
                                </span>
                            </label>
                        </div>
                    </div>
                    <div class="col-md-4 col-lg-3">
                        <div class="form-group">
                            <label><b>{{ $t('PlanName') }}: </b> {{ planDetail[0].PLAN_NAME }}
                            </label>
                        </div>
                    </div>
                    <div class="col-md-4 col-lg-3" v-if="planDetail[0].PLAN_CODE == 'SUBSCRIPTION_BILLING'">
                        <div class="form-group">
                            <label><b>{{ $t('BillingFrequency') }}: </b> <span
                                    v-if="planDetail[0].PAYMENT_TYPE == 'One Time'">One Time</span>
                                <span v-else>{{ planDetail[0].PAYMENT_TYPE }}</span></label>
                        </div>
                    </div>
                    <div class="col-md-4 col-lg-3" v-if="parsedFrequencies.length && planDetail[0].PLAN_CODE == 'SUBSCRIPTION_BILLING'">
                        <div class="form-group">
                            <label><b>{{ $t('Additional Frequency with Discount') }}:</b></label>
                            <span>
                         {{ frequencyLabels.join(', ') }}
                       
                            </span>
                        </div>
                    </div>
                  
                    <div class="col-md-4 col-lg-3">
                        <div class="form-group">
                            <label><b>{{ $t('InvoiceTemplate') }}: </b> {{ planDetail[0].INVOICE_TEMPLATE }}</label>
                        </div>
                    </div>
                    <!-- <div class="col-md-4 col-lg-3">
                        <div class="form-group">
                            <label><b>{{ $t('CustomerType') }}: </b> {{ planDetail[0].CUSTOMER_SEGMENT }}</label>
                        </div>
                    </div> -->
                   
                    <div class="col-md-4 col-lg-3">
                        <div class="form-group">
                            <label><b>{{ $t('EffectiveDate') }}:</b> {{
                                planDetail[0].EFFECTIVE_DATE != null ? $d(planDetail[0].EFFECTIVE_DATE) : '' }}</label>
                        </div>
                    </div>
                    <div class="col-md-4 col-lg-3">
                        <div class="form-group">
                            <label><b>{{ $t('ExpirationDate') }}:</b> {{
                                planDetail[0].EXPIRATION_DATE != null ? $d(planDetail[0].EXPIRATION_DATE) : ''
                            }}</label>
                        </div>
                    </div>
                    <div class="col-md-4 col-lg-3">
                        <div class="form-group">
                            <label><b>{{ $t('DESCRIPTION') }}:</b> {{ planDetail[0].PLAN_DESCRIPTION }}</label>
                        </div>
                    </div>
                    <div class="col-md-4 col-lg-3" v-if="planDetail[0].no_of_plan">
                        <div class="form-group">
                            <label><b>{{ $t('Days') }}:</b> {{ planDetail[0].no_of_plan }}</label>
                        </div>
                    </div>
                  


                </div>
            </div>
        </div>

    </section>
</template>
<script>
import DataServices from "../../services/DataService";
export default {
    data() {
        return {
            planDetail: []
        }
    },
    created: function () {
        debugger;
        var vm = this;
        vm.PlanId = parseInt(this.$route.params.id);
        vm.displayPlanDetails();
    },

 computed: {
  parsedFrequencies() {
    try {
      const raw = this.planDetail[0]?.ADDITIONAL_FREQUENCY;
      if (!raw) return [];

      const json = `[${raw}]`;
      return JSON.parse(json);
    } catch (error) {
      console.error("Error parsing ADDITIONAL_FREQUENCY:", error);
      return [];
    }
  },
  frequencyLabels() {
  return this.parsedFrequencies.map(
    f => `${f.frequency} (${Number(f.discount)}%)`
  );
  }
},
    methods: {
       
        displayPlanDetails: function () {
            debugger;
            var vm = this;
            var url = `planId=${vm.PlanId}`;
            DataServices.displayPlanDetails(url).then(response => {
                if (response.data.status == 'Success') {
                    if (JSON.parse(response.data.result).data.length > 0) {
                        vm.planDetail = JSON.parse(response.data.result).data;
                        vm.planDetail.forEach((element) => {
                            ['EFFECTIVE_DATE', 'EXPIRATION_DATE'].forEach((key) => {
                                if (element[key]) {
                                    element[key] = new Date(element[key]);
                                }
                            });
                        });
                    }
                }
            })
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'UnPublish':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Published':
                    returnClass = 'px-2 bg-primary text-center text-white d-block';
                    break;
                case 'Draft':
                    returnClass = 'px-2 text-center bg-warning text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;


                default:
                    returnClass = '';
            }
            return returnClass;
        }

    }
}

</script>