<template>
    <section>
        <div class="col-md-12 p-0 pt-2">
            <div class="ticketsmallfix theme-primary partition p-actions-expand">
                <span class="p-name text-white">{{ $t("Services") }}</span>
                <span class="p-actions float-right">
                    <a class="p-action-btn text-white" href="javascript:;" v-on:click="openExistingService()"><em
                            class="fa fa fa-check"></em><br />{{ $t("SelectService") }}</a>
                    <a class="p-action-btn text-white" href="javascript:;" v-on:click="openCreateService()"><em
                            class="fa fa fa-plus"></em><br />{{ $t("CreateService") }}</a>
                    <router-link to="/Plan/PlanListing" class="p-action-btn text-white"><em
                            class="fa fa-angle-double-left"></em><br />{{ $t("BacktoList") }}</router-link>
                </span>
            </div>
        </div>
        <div class="listing mt-2">
            <div class="table-responsive table-fix-header">
                <table class="table table-bordered mt-0">
                    <thead class="thead-bg">
                        <tr>
                            <th v-for="item in Headers" :key="item.COLUMN_NAME" v-if="!shouldHideColumn(item)">
                                {{ item.DISPLAY_NAME }}
                            </th>




                            <th class="text-center">{{ $t("Action") }}</th>
                        </tr>
                    </thead>
                    <tbody>
                        <template v-for="item in ServicePlanListing">
                           
                            <tr>
                                <td>{{ item.service_name }}</td>
                                <td>{{ item.service_code }}</td>
                                <td v-if="planCode != 'SUBSCRIPTION_BILLING'">{{ item.usage_measurement }}</td>
                                <!-- <td v-if="planCode != 'SUBSCRIPTION_BILLING'">{{ item.payment_type }}</td> -->

                                <!-- <td v-if="planCode != 'SUBSCRIPTION_BILLING'" class="single-action text-center "
                                    style="width: 100px;">
                                    <a class="" href="javascript:;" v-on:click="ViewBillingRule(item)">
                                        <em class="fa fa-eye text-info action-icon"></em>
                                    </a>
                                </td> -->
                                <td v-if="planCode != 'SUBSCRIPTION_BILLING'">
                                    <span v-if="item.RuleDetails != null">
                                        <a href="javascript:;" v-on:click="OpenRuleDetail(item)"
                                            :title="item.RuleDetails[0].billing_config_name">
                                            <i aria-hidden="true"></i> {{ item.RuleDetails[0].billing_config_name }}
                                        </a>
                                    </span>
                                    <span v-else> --</span>
                                    <!-- <a class="actions-onclick" href="javascript:;" v-on:click="ViewBillingRule(item)">
                                        <em class="fa fa-eye text-info action-icon"></em>{{item.billing_config_name}}
                                    </a> -->

                                </td>
                                <td class="single-action text-center" style="max-width: 100px;width:100px;">
                                    <!-- <a class="actions-onclick" href="javascript:;" v-on:click="AddBillingRule(item)">
                                        <em class="fa fa-plus text-success action-icon"></em>
                                    </a> -->
                                    <div class="d-flex content-justify-between">
                                        <a v-if="planCode != 'SUBSCRIPTION_BILLING'" title="Map Rule"
                                            class="actions-onclick"
                                            v-on:click="MapBillingRule(item.plan_service_id, item.service_id)"><em
                                                class="fa fa-plus text-success "></em></a>

                                        <a class="actions-onclick" href="javascript:;"
                                            v-on:click="DeleteServiceData(item.plan_service_id)">
                                            <em class="fa fa-trash-o text-danger action-icon"></em>
                                        </a>
                                    </div>

                                </td>
                            </tr>
                            <tr v-if="item.isRuleView == true">
                                <td :colspan="(Headers.length + 2)">
                                    <div class="listing">
                                        <div class="table-responsive table-fix-header">
                                            <table class="table table-bordered mt-0">
                                                <thead class="thead-bg">
                                                    <tr>
                                                        <th>{{ $t('Rule_Name') }}</th>
                                                        <th>{{ $t('PRODUCT_TYPE_DESC') }}</th>
                                                        <th>{{ $t('EfficativeDateFrom') }}</th>
                                                        <th>{{ $t('EfficativeDateTo') }}</th>
                                                        <th>{{ $t('ServiceName') }}</th>
                                                        <th>{{ $t('payment_type') }}</th>
                                                        <th>{{ $t('Created_by_Id') }}</th>
                                                        <th>{{ $t('CREATED_ON') }}</th>
                                                        <th>{{ $t('Action') }}</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr v-for="(rule, index) in item.RuleDetails" :key="index">
                                                        <td>{{ rule.billing_config_name }}</td>
                                                        <td>{{ rule.description }}</td>
                                                        <td>{{ $options.filters.formatDate(rule.efficative_date_from) }}
                                                        </td>
                                                        <td>{{ $options.filters.formatDate(rule.efficative_date_to) }}
                                                        </td>
                                                        <td>{{ rule.service_name }}</td>
                                                        <td>{{ rule.payment_type }}</td>
                                                        <td>{{ rule.created_by }}</td>
                                                        <td>{{ $options.filters.formatDate(rule.created_at) }}</td>
                                                        <td class="single-action text-center" style="width: 150px;">
                                                            <a class="actions-onclick" href="javascript:;"
                                                                v-on:click="ViewRuleData(rule)">
                                                                <em class="fa fa-eye text-info action-icon"></em>
                                                            </a>
                                                            <a class="actions-onclick" href="javascript:;"
                                                                v-on:click="DeleteRuleData(rule.id)">
                                                                <em class="fa fa-trash-o text-danger action-icon"></em>
                                                            </a>
                                                        </td>
                                                    </tr>
                                                    <tr v-if="item.RuleDetails.length == 0">
                                                        <td class="text-center text-danger" colspan="9">
                                                            {{ $t("NorecordfoundText") }}</td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>

                                    <!-- <div class="row">
                                        <div class="col-md-12 pt-0 mb-2" v-if="item.RuleDetails.length > 0">
                                            <ul class="nodechld mainlist ulnodellistdata ui-sortable">
                                                <li class="includenodechld userDragDrop bg-light brdrbtm position-relative ui-sortable-handle" v-for="rule in item.RuleDetails">
                                                    <div class="collexpnd">
                                                        <a class="aCollapseExpand visible nodetablecollapse timesheetcollapse timesheetexpand expanded d-inline-block parent-childrow m-0" href="javascript:;">
                                                            <i class="fa fa-chevron-down"></i>
                                                        </a>
                                                    </div>
                                                    <div class="content">
                                                        <label style="float: left; display: inline-block;">{{rule.ruleName}}</label>
                                                        <span class="pl-2 float-left textelipsis" id="spn_1" title="Is CDC Manually added to Ticket  ( feedname Starts With  MAN ) Stop" style="color: rgb(103, 132, 168);">
                                                            {{rule.ruleCondition.condition}}
                                                        </span>
                                                        <div class="text-right d-flex flex-wrap float-right">
                                                            <a class="mr-2 text-white node-a bg-success btnaddrule" href="javascript:;" style="position: relative; z-index: 0;" :title="$t('AddRule')" v-on:click="addRuleCondition(rule,item)">
                                                                <i class="fa fa-plus pr-1 fa-size"></i> {{$t("AddRule")}}
                                                            </a>
                                                            <a v-if="false" class="mr-2 text-white node-a bg-success btnaddrule" href="javascript:;" style="position: relative; z-index: 0;" :title="$t('EditRule')">
                                                                <i class="fa fa-pencil pr-1 fa-size"></i> {{$t("EditRule")}}
                                                            </a>
                                                            <a class="mr-2 text-white node-a bg-primary btnchildadd" href="javascript:;" style="position: relative; z-index: 0;" :title="$t('AdditionalCondition')">
                                                                <i class="fa fa-plus-circle pr-1 fa-size"></i> {{$t("AdditionalCondition")}}
                                                            </a>
                                                            <a class="mr-2 text-white node-a bg-info btnaddruleabove" href="javascript:;" style="position: relative; z-index: 0;" :title="$t('InsertAbove')">
                                                                <i class="fa fa-plus-circle pr-1 fa-size"></i> {{$t("InsertAbove")}}
                                                            </a>
                                                            <a class="mr-2 text-white node-a bg-info btnaddrulebelow" href="javascript:;" style="position: relative; z-index: 0;" :title="$t('InsertBelow')">
                                                                <i class="fa fa-plus-circle pr-1 fa-size"></i> {{$t("InsertBelow")}}
                                                            </a>
                                                        </div>
                                                    </div>
                                                    <div class="buttonsdlt" style="float: left;">
                                                        <a class="aDelete enable deleteNodeConf" href="javascript:;" data-node="1" title="" data-toggle-tooltip="tooltip" data-original-title="Click here to delete this rule">
                                                            <i class="fa fa-trash text-danger fa-size"></i>
                                                        </a>
                                                    </div>
                                                </li>
                                            </ul>
                                        </div> 
                                        <div class="col-md-12 pb-2" v-if="item.rules.length == 0">
                                            <div class="border-bottom text-center text-danger p-2">
                                                {{$t("NorecordfoundText")}}
                                            </div>  
                                        </div> 
                                    </div>-->
                                    <!-- <div class="text-right">
                                        <button type="button" class="btn btn-info mr-2" v-on:click="addMoreRule(item)"><em class="fa fa-plus"></em> {{$t("AddMoreRule")}}</button>
                                        <button type="button" class="btn btn-success mr-2" v-on:click="saveServiceRules(item)"><em class="fa fa-save"></em> {{$t("SaveRule")}}</button>
                                        <button type="button" class="btn btn-danger" v-on:click="item.isRuleView = false"><em class="fa fa-times"></em> {{$t("Close")}}</button>
                                    </div> -->
                                </td>
                            </tr>
                        </template>
                        <tr v-if="ServicePlanListing.length == 0">
                            <td :colspan="(Headers.length + 2)" class="text-center text-danger">
                                {{ $t("NorecordfoundText") }}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
        <!-- <tg-list :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="Ticketing" IdentityColumn="service_type_id"
            :HeaderText="$t('Services')" :listType="['List']" :LegendArray="[]" :ListData="ServicePlanListing"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :HeaderData="Headers"
            :TotalRecords="TotalRecords" :ListDataCallBackFunction="FetchRecords" :showCheckBox="false"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" :HeaderButtons="HeaderButtons"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'service_name'">
                    {{ data.row.service_name }}
                </template>
                <template v-if="data.column.COLUMN_NAME == 'service_code'">
                    {{ data.row.service_code }}
                </template>
                <template v-if="data.column.COLUMN_NAME == 'usage_measurement'">
                    {{ data.row.usage_measurement }}
                </template>
            </template>
        </tg-list> -->
        <ModalExistingService v-if="isExistingService" :callbackfunction="FetchRecords" :PlanId="PlanId"
            :planCode="planCode" :paymentType="paymentType">
        </ModalExistingService>
        <AddNewService v-if="isCreateService" :callbackfunction="FetchRecords" :PlanId="PlanId"
            :isForService="isForService" :planCode="planCode" :billingTypeOptions="billingTypeOptions"
            :serviceId="serviceId" :paymentType="paymentType">
        </AddNewService>
        <!-- <ModalAddBillingRule v-if="isModalAddRule" :reqFrom="reqFrom" :serviceDetailForBillingRule="serviceDetailForBillingRule"></ModalAddBillingRule> -->
        <ServiceRuleCondition v-if="serviceDetailConditions" :serviceRuleItem="serviceRuleItem"
            :serviceDetailForBillingRule="serviceDetailForBillingRule" v-on:close="serviceDetailConditions = false">
        </ServiceRuleCondition>
        <ModalBillingRuleListing v-if="IsRuleListing" :servicePlanId="servicePlanId" :serviceId="serviceId">
        </ModalBillingRuleListing>
        <ModalManageBillingConfiguration v-if="isModalBillingConfiguration" :serviceId="serviceId" reqType="fromServiceMapping" :configurationId="configurationId"></ModalManageBillingConfiguration>
    </section>
</template>
<script>
import AddNewService from "../../components/Modals/AddNewService.vue";
// import ModalAddBillingRule from "../../components/Modals/modalAddBillingRule.vue";
import ModalExistingService from "../../components/Modals/ModalExistingService.vue";
import DataServices from "../../services/DataService";
import ServiceRuleCondition from "./ServiceRules/ServiceRuleConditions.vue";
import ModalBillingRuleListing from "../../components/Modals/BillingRuleListingModal.vue";
import ModalManageBillingConfiguration from '../../components/Modals/ModalManageBillingConfiguration.vue';
export default {
    components: { ModalExistingService, AddNewService, ServiceRuleCondition, ModalBillingRuleListing,ModalManageBillingConfiguration },
    props: {
        paymentType: {
            type: String,
            required: false
        },
        billingTypeOptions: {
            type: Array,
            default: []
        }
    },
    data() {
        return {
            isModalBillingConfiguration: false,
            noRecords: false,
            IsRuleListing: false,
            TotalRecords: 0,
            serviceId: 0,
            CurrentPage: 1,
            PlanId: 0,
            paymentType: '',
            isForService: true,
            TotalPages: 0,
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            GroupStart: 1,
            GroupEnd: 3,
            Headers: [{
                "COLUMN_NAME": "service_name",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('ServiceName'),
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "service_code",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('ServiceType'),
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "usage_measurement",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('UsageMeasurement'),
                "DISPLAY_ORDER": 3
            },
            // {
            //     "COLUMN_NAME": "payment_type",
            //     "DATA_TYPE": "string",
            //     "SORTABLE": false,
            //     "VISIBLE": true,
            //     "DISPLAY_NAME": this.$t('BillingFrequency'),
            //     "DISPLAY_ORDER": 3
            // },
            {
                "COLUMN_NAME": "RuleDetails",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('BillingRuleName'),
                "DISPLAY_ORDER": 3
            }

            ],
            HeaderButtons:
                [
                    {
                        title: 'Select Service',
                        iconClass: 'fa fa-plus',
                        callbackfunction: 'openExistingService',
                        additionalClass: '',
                        isdisabled: false,
                        isvisible: true,
                        checkPrivilege: true,
                        id: 'ExistingService',
                    },
                    {
                        title: 'Create New',
                        iconClass: 'fa fa-plus',
                        callbackfunction: 'openCreateService',
                        additionalClass: '',
                        isdisabled: false,
                        isvisible: true,
                        checkPrivilege: true,
                        id: 'CreateNew'
                    },
                    {
                        title: 'BacktoList',
                        iconClass: 'fa fa-angle-double-left',
                        href: '/Plan/PlanListing',
                        additionalClass: '',
                        isdisabled: false,
                        isvisible: true,
                        checkPrivilege: true,
                        id: 'BackToList'
                    }
                ],
            listActions: [
                {
                    title: 'View Rule',
                    href: 'javascript:void(0)',
                    icon: 'fa fa-eye text-success',
                    callbackfunction: 'ViewBillingRule',
                    additionalSpan: ''
                },

                {
                    title: 'Delete',
                    href: 'javascript:void(0)',
                    icon: 'fa fa-trash',
                    isDisabled: false,
                    callbackfunction: 'DeleteService',
                    additionalSpan: ''
                }

            ],
            ServicePlanListing: [],
            isExistingService: false,
            isCreateService: false,
            //planCode:'SUBSCRIPTION_BILLING'
            planCode: '',
            // isModalAddRule:false,
            serviceDetailForBillingRule: [],
            serviceDetailConditions: false,
            serviceRuleItem: null,
            servicePlanId: null,
            configurationId:0

        }
    },
    created: async function () {
        var vm = this;
        vm.PlanId = parseInt(this.$route.params.id);
        await vm.displayPlanDetails();
        await vm.FetchRecords();
    },
    methods: {
        OpenRuleDetail: function (data) {
            var vm = this;
            vm.isModalBillingConfiguration = true;
            vm.serviceId = data.service_id;
            vm.configurationId = data.configuration_id;
            // vm.$router.push({ name: 'ManageConfiguration', params: { id: data.configuration_id, serviceId: data.service_id } });
        },
        shouldHideColumn(item) {
            return this.planCode === 'SUBSCRIPTION_BILLING' &&
                (item.COLUMN_NAME === 'payment_type' || item.COLUMN_NAME === 'RuleDetails' || item.COLUMN_NAME === 'usage_measurement');
        },
        saveServiceRules: function (item) {
            var obj = {
                planServiceId: item.plan_service_id,
                configurationId: item.configuration_id,
                rules: []
            };
            item.rules.forEach(function (rule, index) {
                var objRule = {
                    ruleId: 0,
                    ruleName: rule.ruleName,
                    conditionId: rule.ruleCondition.conditionId,
                    rateCodeId: rule.ruleCondition.rateCode,
                    isLeafChecked: rule.ruleCondition.isLeafChecked,
                    selectedflag: rule.ruleCondition.selectedFlag.value,
                    version: rule.version
                }
                obj.rules.push(objRule);
            });
            DataServices.SaveServiceRules(JSON.stringify(obj)).then(function (response) {

            })
        },
        MapBillingRule: function (PlanServiceId, ServiceId) {
            var vm = this;
            vm.servicePlanId = PlanServiceId;
            vm.serviceId = ServiceId;
            vm.IsRuleListing = true;
        },
        addRuleCondition: function (item, serviceData) {
            this.serviceDetailConditions = !this.serviceDetailConditions;
            this.serviceRuleItem = item;
            this.serviceDetailForBillingRule = serviceData;
        },
        addMoreRule: function (item) {
            var vm = this;
            item.rules.push({
                ruleDisplayOrder: (item.rules.length + 1),
                ruleName: "Rule " + (item.rules.length + 1),
                version: (item.rules.length + 1),
                ruleCondition: {}
            })
        },
        displayPlanDetails: async function () {
            debugger;
            var vm = this;
            var url = `planId=${vm.PlanId}`;
            await DataServices.displayPlanDetails(url).then(response => {
                if (response.data.status == 'Success') {
                    if (JSON.parse(response.data.result).data.length > 0) {
                        vm.planDetail = JSON.parse(response.data.result).data;
                        vm.paymentType = vm.planDetail[0].PAYMENT_TYPE;
                        vm.planCode = vm.planDetail[0].PLAN_CODE;
                        console.log("vm.planCode", vm.planCode)
                        if (vm.planDetail[0].CLIENT_COUNT > 0) {
                            vm.listActions[1].isDisabled = true;
                        }
                    }
                }
            })
        },
        FetchRecords: async function () {
            var vueObj = this;
            vueObj.ServicePlanListing = [];
            var url = `planId=${vueObj.PlanId}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&planCode=${vueObj.planCode}&pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}`;
            await DataServices.GetPlanServiceListing(url).then(function (response) {
                if (response.data != "" && response.data != null) {
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "service_name") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "service_code") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "usage_measurement") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });
                    if (response.data.result.data.length > 0) {
                        let dt = response.data.result.data;
                        dt.forEach(function (item) {
                            item.isRuleView = false;
                            item.rules = [];
                        });
                        vueObj.ServicePlanListing = dt;
                        vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                        vueObj.$emit('Bind-Plan-Details', vueObj.PlanId);
                    } else {
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0
                        vueObj.ServicePlanListing = [];
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.TotalRecords = 0
                    vueObj.ServicePlanListing = [];
                }

            })
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "openExistingService":
                    this.openExistingService();
                    break;
                case "openCreateService":
                    this.openCreateService();
                    break;
            }
        },
        openExistingService: function () {
            var vm = this;
            vm.isExistingService = true;
        },
        openCreateService: function () {
            var vm = this;
            vm.serviceId= 0;
            vm.isCreateService = true;
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {

                let action = { ...actionItem };
                switch (action.title) {

                    case 'Delete':
                        action.isVisible = true;
                        break;
                    case 'Map Rule':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        // actionButtonClick(callbackfunction, item, index, event, isChild) {
        //     switch (callbackfunction) {

        //         case 'DeleteService':
        //             this.DeleteServiceData(item.plan_service_id);
        //             break;
        //         case 'AddBillingRule':
        //             this.AddBillingRule(item);
        //             break;
        //         case "ViewBillingRule":
        //             this.ViewBillingRule(item);
        //             break;
        //     }
        // },
        ViewBillingRule: function (item) {
            if (!item.RuleDetails || item.RuleDetails.length === 0) {
                item.RuleDetails = [];
            }
            if (!('isRuleView' in item)) {
                this.$set(item, 'isRuleView', true);
            }
            else {
                item.isRuleView = !item.isRuleView; // Toggle view
            }

        },
        // AddBillingRule : function(item){
        //     var vm = this;
        //     vm.reqFrom = 'mapBillingRuleForService';
        //     vm.serviceDetailForBillingRule = item;
        //     vm.isModalAddRule = true;
        // },
        DeleteServiceData: function (planServiceId) {
            var vm = this;
            var val = planServiceId;
            if (val > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Service")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "BILLING",
                        refCode: "TALYGEN_PLAN_SERVICE"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.CommonCommonDelete(objectPostString).then(response => {
                        if (response.status == 200) {
                            vm.FetchRecords();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Service')]), "Success", true, vm.$t('Alert'));
                            vm.$emit('Bind-Plan-Details', vm.PlanId);
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Service')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "warning", true, vm.$t("Alert"));
            }
        },
        DeleteRuleData(RuleId) {
            var vm = this;
            var val = RuleId;
            if (val > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Rule")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "BILLING",
                        refCode: "TALYGEN_BE_RULE_PLAN_SERVICE_MAPPING"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.CommonCommonDelete(objectPostString).then(response => {
                        if (response.status == 200) {
                            vm.FetchRecords();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Rule')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Rule')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "warning", true, vm.$t("Alert"));
            }
        },
        ViewRuleData(data) {
            var vm = this;
            vm.$router.push({ name: 'ManageConfiguration', params: { id: data.configuration_id } });
        }
    },
}
</script>