<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered" style="max-width: 1100px;">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Add') }} {{ $t('RateCode') }}</h4>
                        <button type="button" class="close" v-on:click="CloseRateCode">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" ref="AddRate" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="SubmitData" :mandatorytext='MandatoryString'>
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../services/DataService';
export default {
    props: {
        RateObj: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            ListingData: [],
            FormSchema: [
                {
                    layoutType: "four",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('RateCode'),
                            name: "RateCode",
                            value: "",
                            disabled: false,
                            visibility: true,
                            config: {
                                options: []
                            },
                            placeholder: this.$t('RateCode'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EventStartDate'),
                            name: "StartDate",
                            value: new Date(),
                            placeholder: this.$t('EventStartDate'),
                            config: {
                                format: "MM/DD/YYYY",
                                //minDate: new Date(),
                                onChange: this.setTimerDate,
                                maxDate: false
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EndDate'),
                            name: "EndDate",
                            value: new Date(),
                            placeholder: this.$t('EndDate'),
                            config: {
                                format: "MM/DD/YYYY",
                                minDate: new Date(),
                                maxDate: false
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Rate'),
                            name: "Rate",
                            value: '',
                            placeholder: this.$t('Rate'),
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: '1001',
                            placeholder: this.$t('Status'),
                            config: {
                                options: [{ name: 'Active', value: 1001 }, { name: 'Inactive', value: 1002 }]
                            },
                            validationRules: { "required": false },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('CustomerWorkCode'),
                            name: "CustomerWorkCode",
                            value: '',
                            placeholder: this.$t('CustomerWorkCode'),
                            validationRules: { "required": false },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Comment'),
                            name: "Comment",
                            value: "",
                            placeholder: "",
                            config: {
                                rows: 3
                            },
                            validationRules: { "required": false, max: 2000 },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('Save')
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>" + this.$t('Cancel'),
                    onClick: this.CloseRateCode,
                },
            ],
            WorkCodeList: [],
            MandatoryString: this.$t('MandatoryString'),
            PlainId: 0,
            RateCodeId: 0
        }
    },
    created: function () {
        var ths = this;
        ths.PlainId = this.$route.params.id;
        ths.GetWorkCodes();
        if (ths.RateObj != null) {
            ths.BindRateDetails();
        }
    },
    methods: {
        setTimerDate: function (x, y, z) {
            var vm = this;
            if (y.name == "StartDate") {
                vm.$refs.AddRate.updateProperty("EndDate", "config.minDate", x);
                vm.$refs.AddRate.UpdateKeyValue();
            }
        },
        BindRateDetails() {
            var ths = this;
            ths.RateCodeId = ths.RateObj.rate_schedule_id;
            ths.FormSchema[0].Data[0].disabled = true;
            ths.FormSchema[0].Data[0].value = ths.RateObj.workcode;
            ths.FormSchema[0].Data[1].value = ths.RateObj.start_date;
            ths.FormSchema[0].Data[2].value = ths.RateObj.end_date;
            ths.FormSchema[0].Data[3].value = ths.RateObj.rate;
            ths.FormSchema[0].Data[4].value = ths.RateObj.status_id;
            ths.FormSchema[0].Data[5].value = ths.RateObj.billing_code;
            ths.FormSchema[1].Data[0].value = ths.RateObj.comment;
        },
        CloseRateCode() {
            this.$emit('CloseRateCode');
        },
        GetWorkCodes() {
            var url = `moduleName=Billing&type=WORK_CODES`;
            var vm = this;
            DataService.GetDDLData(url).then((result) => {
                vm.WorkCodeList = result.data.DATA;
                vm.WorkCodeList.forEach((item) => {
                    vm.FormSchema[0].Data[0].config.options.push({
                        name: `${item.name}`,
                        value: `${item.value}`,
                    });
                });
                this.$refs.AddRate.UpdateKeyValue();
            });
        },
        SubmitData() {
            var vm = this;
            vm.isLoading = true;
            vm.$refs.AddRate.validateForm.validate().then(async valid => {
                if (valid) {
                    var Obj = {
                        RateCodeId: vm.RateCodeId,
                        PlainId: vm.PlainId,
                        WorkCodeId: vm.FormSchema[0].Data[0].value,
                        StartDate: vm.FormSchema[0].Data[1].value,
                        EndDate: vm.FormSchema[0].Data[2].value,
                        Rate: vm.FormSchema[0].Data[3].value,
                        Status: vm.FormSchema[0].Data[4].value,
                        CustomerWorkCode: vm.FormSchema[0].Data[5].value,
                        Comment: vm.FormSchema[1].Data[0].value,
                        ClientId: null
                    }
                    var getJson = JSON.stringify(Obj);
                    DataService.SaveRateCode(getJson).then(function (response) {
                        if (response.data.Result.DATA.length > 0) {
                            vm.isLoading = false;
                            if (response.data.Result.DATA[0].VALUE == 'INSERT') {
                                vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Rate')]), "success", true, vm.$t("Alert"));
                                vm.CloseRateCode();
                            }
                            else if (response.data.Result.DATA[0].VALUE == 'UPDATE') {
                                vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Rate')]), "success", true, vm.$t("Alert"));
                                vm.CloseRateCode();
                            }
                            else if (response.data.Result.DATA[0].VALUE == 'EXISTS') {
                                vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Rate')]), "warning", true, vm.$t("Alert"));
                            }
                            else {
                                vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                            }
                        } else {
                            vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                        }
                    })
                } else {
                    vm.isLoading = false;
                }
            });
        }
    }
}
</script>