<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div class="padding-t_10 p-0" id="reportstabsdata">
            <div class="left-menu-tab mt-2">
                <div class="col-md-12 p-0">
                    <div class="theme-primary partition">
                        <span class="p-name text-white">{{ $t(EditPlanDetail[0].PLAN_NAME) }}</span>
                    </div>

                </div>
                <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab  nav-tabs">
                    <li v-for="tab in tabs" :key="tab.id" @click="changeTab(tab)"
                        :class="{ 'nav-item': true, 'active': currentTab.title === tab.title }">
                        <span :class="{ 'nav-link left-menu': true, 'active': currentTab.title === tab.title }">
                            <i :class="tab.icon" class="p-1"></i> {{ tab.displayName }}</span>
                    </li>
                </ul>

            </div>
            <div class="right-content forms-for-billing-plandetails">
                <div class="tab-content" id="myTabContent">
                    <div class="expand-list">
                        <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                            <i class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></i>
                        </a>
                    </div>
                    <div id="ReturnRequestHeading" class="theme-primary partition  p-actions-expand mt-2"
                        v-if="currentTab.title !== 'Services' && currentTab.title !== 'Inventory Items' && currentTab.title !== 'Rates' && currentTab.title !== 'Subsidy'">
                        <span class="p-name text-white"> {{ currentTab.displayName }}</span>
                        <span v-if="currentTab.title === 'Plan'">
                            <RouterLink to="/Plan/PlanListing" title="" class="p-action-btn text-white float-right"
                                data-toggle-tooltip="tooltip" data-original-title="Back to list"><i
                                    class="fa fa-angle-double-left pr-2"></i><br>{{ $t('BacktoList') }}</RouterLink>
                            <a id="addnewreview" href="javascript:;" title="Add Feedback"
                                class="p-action-btn text-white float-right" @click="openEditPlan">
                                <i class="fa fa-pencil pr-2"></i>
                                <br>
                                <span>{{ $t('EditPlan') }}</span>
                            </a>

                        </span>
                        <span v-if="currentTab.title === 'Billing Cycle' || currentTab.title === 'EMI Configuration'">
                            <RouterLink to="/Plan/PlanListing" title="" class="p-action-btn text-white float-right"
                                data-toggle-tooltip="tooltip" data-original-title="Back to list"><i
                                    class="fa fa-angle-double-left pr-2"></i><br>{{ $t('BacktoList') }}</RouterLink>
                        </span>
                    </div>
                    <div class="col-md-12 p-0 main-content">
                        <PlanDetail v-if="currentTab.title == 'Plan'" :key="refreshComponent"></PlanDetail>
                        <ServiceDetail v-if="currentTab.title == 'Services'" :billingTypeOptions="BillingTypeOptions">
                        </ServiceDetail>
                        <!-- <RateDetail v-if="currentTab.title == 'Rates'"></RateDetail> -->
                        <SubscriptionRate v-if="currentTab.title == 'Rates'"></SubscriptionRate>
                        <InventoryDetail v-if="currentTab.title == 'Inventory Items'"></InventoryDetail>
                        <BillingCycle v-if="currentTab.title == 'Billing Cycle'" :planid="planid"></BillingCycle>
                        <EMIConfiguration v-if="currentTab.title == 'EMI Configuration'" :planid="planid">
                        </EMIConfiguration>
                        <PlanSubsidy v-if="currentTab.title == 'Subsidy'"></PlanSubsidy>
                    </div>
                </div>
            </div>
        </div>
        <AddPlan v-if="isCheckEditPlan" :callbackfunction="FetchData" :refCode="refCode"
            @refreshPlanDetail="refreshPlanDetail" :editDetail="EditPlanDetail"
            :billingTypeOptions="BillingTypeOptions"></AddPlan>
    </section>
</template>
<script>

import ServiceDetail from '../Plan/ServiceDetail.vue'
import InventoryDetail from './InventoryDetail.vue';
import PlanDetail from './PlanDetail.vue';
import RateDetail from './RateDetail.vue';
import DataServices from "../../services/DataService";
import AddPlan from '../../components/Modals/AddPlan.vue';
import BillingCycle from '../Plan/BillingCycle.vue';
import EMIConfiguration from '../Plan/EMIConfiguration.vue';
import SubscriptionRate from './SubscriptionRate.vue'
import PlanSubsidy from './PlanSubsidy.vue';

export default {
    components: { ServiceDetail, InventoryDetail, RateDetail, PlanDetail, AddPlan, BillingCycle, EMIConfiguration, SubscriptionRate, PlanSubsidy },
    data() {
        return {
            activeTab: 1,
            tabs: [{ title: 'Plan', displayName: this.$t('Plan'), icon: "fa fa-file-text", id: 1 },
            { title: 'Services', displayName: this.$t('Services'), icon: "fa fa-file-text", id: 2 },
            { title: 'Rates', displayName: this.$t('Rates'), icon: "fa fa-money", id: 3 },
            { title: 'Inventory Items', displayName: this.$t('InventoryItems'), icon: "fa fa-wrench", id: 4 },
            { title: 'Billing Cycle', displayName: this.$t('BillingCycle'), icon: "fa-solid fa-money-bill", id: 5 },
            { title: 'EMI Configuration', displayName: this.$t('PaymentInstallmentSetttings'), icon: "fas fa-credit-card", id: 6 },
            { title: 'Subsidy', displayName: 'Subsidy', icon: "fas fa-credit-card", id: 7 }],

            currentTab: { title: 'Plan', displayName: this.$t('Plan'), icon: "fa fa-file-text", id: 1 },
            isLoading: false,
            isCheckEditPlan: false,
            EditPlanDetail: [],
            refCode: '',
            refreshComponent: 0,
            BillingTypeOptions: [],
            componentKey: 0
        }
    },
    created: async function () {
        debugger
        var vm = this;
        await vm.GetExtraFeatures();
        vm.PlanId = parseInt(vm.$route.params.id);
        vm.BindPlanDetails(vm.PlanId);
    },
    methods: {
        GetExtraFeatures: async function () {
            var vueObj = this;
            var url = `featureCode=GET_EXTRA_FEATURE_BILLING&outputType=list`;
            await DataServices.CheckExtraFeatures(url).then(function (response) {
                var parseData = JSON.parse(response.data);
                vueObj.BillingTypeOptions = parseData;
            });
        },
        refreshPlanDetail() {
            this.refreshComponent++;
        },
        changeTab(tab) {
            this.currentTab = tab;
        },
        openEditPlan: function () {
            var vm = this;
            vm.BindPlanDetails(vm.PlanId, 'Edit');
        },
        BindPlanDetails: function (planId, isFor) {
            debugger;
            var vm = this;
            var url = `planId=${planId}`;
            DataServices.displayPlanDetails(url).then(response => {
                if (response.data.status == 'Success') {
                    if (JSON.parse(response.data.result).data.length > 0) {
                        vm.EditPlanDetail = JSON.parse(response.data.result).data;
                        //debugger;
                        if (isFor == 'Edit') {
                            vm.isCheckEditPlan = true;
                            vm.refCode = 'Update';
                        }
                        // vm.isCheckEditPlan = true;
                        // vm.refCode = 'Update';
                    }
                }
            })
        }
    },
}
</script>