<template>
  <section>
    <loader :is-visible="isLoading"></loader>
    <div class="col-md-12 p-0 border float-left plan-rate-detail">
      <div class="col-md-12 mt-3 d-none">
        <div class="col-md-12 mb-2 p-2 bg-light border d-flex">
          <div class="form-check form-switch col-md-6 mt-1 p-0">
            <label class="form-check-label mr-2" for="peakHoursSwitch">Enable Minimum Charge</label>
            <label class="switch">
              <input class="form-check-input custom-control-input dynamic" type="checkbox"
                :checked="isCheckedMinimiumCharge" v-on:change="checkForEnableMinimiumCharge" />
              <span class="slider round" style="cursor: default;">
                <span class="slider-yes">{{ $t('Yes') }}</span>
                <span class="slider-no">{{ $t('No') }}</span>
              </span>
            </label>
          </div>
        </div>
      </div>
      <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mt-2">
        <div class="peak-rates listing mb-3" v-if="isCheckedMinimiumCharge">
          <div class="text-center text-danger" v-if="enableLimitData.length == 0">
            No record found
          </div>
          <div class="table-responsive" v-if="enableLimitData.length > 0">
            <table class="table table-bordered">
              <thead class="thead-bg">
                <tr>
                  <th>Applicable From Month</th>
                  <th>Service Name</th>
                  <th width="100" class="text-center">Action</th>
                </tr>
              </thead>
              <tbody>
                <tr v-if="enableLimitData.length > 0" :key="0">
                  <td>{{ monthNames() }}</td>
                  <td>{{ serviceNameList }}</td>
                  <td class="text-center single-action">
                    <a href="javascript:;" class="mr-3" @click="handleEdit(enableLimitData)">
                      <em class="fa fa-edit text-success float-none"></em>
                    </a>
                    <a href="javascript:;" class="" @click="handleDelete(0)">
                      <em class="fa fa-trash text-danger float-none"></em>
                    </a>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>

      <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mt-2">
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0 mb-3">
          <div class="heading-border">
            <h5 class="h5">
              <span>Details </span>
            </h5>
          </div>
        </div>
      </div>
      <div v-for="(item, rIndex) in PlanRateListing" :key="rIndex">
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
          <div class="form-group mb-0">
            <div class="row">
              <div class="col-md-3">
                <div class="form-group">
                  <label for="rateScheduleName">Rate Schedule Name:<span class="text-danger">*</span></label>
                  <input type="text" class="form-control" id="rateScheduleName" v-model="item.rateScheduleName"
                    placeholder="Enter rate schedule name" required>
                </div>
              </div>
              <div class="col-md-3">
                <div class="form-group">
                  <label for="currency">Currency:<span class="text-danger">*</span></label>
                  <select class="form-control" id="currency" v-model="item.currencyId" required>
                    <option value="">Select Currency</option>
                    <option v-for="(item, itemIndex) in currencies" :key="itemIndex" :value="item.value">{{ item.name }}
                    </option>
                  </select>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mb-0"
          v-for="(serviceType, stIndex) in item.serviceTypes" :key="stIndex">
          <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
            <div class="heading-border">
              <h5 class="h5">
                <a :class="{ 'card-link collapsed': stIndex > 1, 'card-link': stIndex <= 1 }" style="color:#000"
                  data-toggle="collapse" :href="'#collapseOneThread' + stIndex">
                  <div class="conversation-date heading-border"><span>{{ serviceType.serviceTypeName }}</span>
                  </div>
                </a>
                <!-- <span>{{ serviceType.serviceTypeName }}</span> -->
              </h5>
            </div>
          </div>
          <div class="card mb-3" v-for="(row, sIndex) in serviceType.serviceList" :key="sIndex">
            <div class="card-header py-1">
              <a :class="{ 'card-link collapsed': sIndex > 1, 'card-link': sIndex <= 1 }" style="color:#000"
                data-toggle="collapse" :href="'#collapseOneThread_' + sIndex + '_' + stIndex">
                <div class="conversation-date mb-0"><span> Service name: <strong>{{ row.serviceName }}</strong></span>
                </div>
              </a>
            </div>
            <!-- <div class="card-header py-2">
              Service name: <strong>{{ row.service_name }}</strong>
            </div> -->
            <div :id="'collapseOneThread_' + sIndex + '_' + stIndex"
              :class="{ 'collapse': sIndex > 1, 'collapse show': sIndex <= 1 }">
              <div class="card-body pt-3">
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0"
                  v-if="serviceType.serviceTypeName == 'Usage Based'">
                  <div class="form-group mb-0">
                    <div class="row">
                      <div class="col-md-6">
                        <div class="form-group">
                          <label for="billing-interval">Billing Interval:</label>
                          <div class="row mb-3">
                            <div class="col-md-8 d-flex">
                              <select class="form-control" id="billing-interval" v-model="row.serviceInterval"
                                @change="handleIntervalChange(row)">
                                <option value="monthly">Monthly</option>
                                <option value="quarterly">Quarterly</option>
                                <option value="yearly">Yearly</option>
                              </select>
                              <span class="p-1 mx-3">Every</span>
                              <input class="form-control" type="number" v-model.number="row.billingIntervalValue"
                                min="1" style="width: 60px;" />

                              <span class="p-1 mx-3" v-if="row.serviceInterval == 'monthly'">month(s)</span>
                              <span class="p-1 mx-3" v-else>{{ row.serviceInterval }}</span>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div class="col-md-3">
                        <div class="form-group">
                          <label for="billingType">Billing Type</label>
                          <select id="billingType" v-model="row.billingType" class="form-control">
                            <option value="">Select Billing Type</option>
                            <option value="FLT">Slab Rate</option>
                            <option value="INC">Flat Incremental Rate</option>
                            <option value="PRE">Preceding</option>
                          </select>
                        </div>
                      </div>
                      <div class="col-md-3">
                        <div class="form-group">
                          <label for="billableUnit">Billable Unit</label>
                          <select id="billableUnit" v-model="row.billableUnit" class="form-control">
                            <option value="">Select Billable Unit</option>
                            <option v-for="(item, itemIndex) in usageMeasurementList" :key="itemIndex"
                              :value="item.value">{{ item.name }}</option>
                          </select>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div :class="{ 'listing': true, 'mt-0 mb-0': (serviceType.serviceTypeName == 'Usage Based') }">
                  <div class="heading-border">
                    <h5 class="h5">
                      <span>Rates:</span>
                      <small class="float-right" v-if="serviceType.meterType != 'Electric'">
                        <a href="javascript:;" class="pr-3" title="Add" id="AddMember"
                          v-on:click="addNewRateLimit(row, sIndex)"><i class="fa fa-plus pr-2"></i> Add New Slab</a>
                      </small>
                    </h5>
                  </div>
                  <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0 mt-2 mb-3"
                    style="background: #f8f9fa;" v-for="(rateDetail, rateDetailIndex) in row.rateDetails">

                    <div class="row mx-0" v-if="rateDetail.rateLimitId != null">
                      <i class="fa fa-times"
                        style="right: 6px;position: absolute;top: 3px;border-radius: 15px;color: red;z-index: 999999;"
                        v-on:click="deleteRateDetailsWithLimit(row, rateDetailIndex)"></i>
                      <div :class="customerType === 'Domestic' ? 'col-md-2' : 'col-md-3'">
                        <div class="form-group">
                          <label>Start Date:</label>
                          <div class="input-group">
                            <v-date-picker mode="date" class="timedate input-group" v-model="rateDetail.startDate">
                              <template v-slot="{ inputValue, togglePopover }">
                                <input placeholder="MM/DD/YYYY" @click="togglePopover()"
                                  v-bind:class="{ 'form-control': true }" :value="inputValue" />
                                <div @click="togglePopover()" class="input-group-append"><span
                                    class="input-group-text"><em class="fa fa-calendar"></em></span></div>
                              </template>
                            </v-date-picker>
                          </div>
                        </div>
                      </div>
                      <div :class="customerType === 'Domestic' ? 'col-md-2' : 'col-md-3'">
                        <div class="form-group">
                          <label>End Date:</label>
                          <div class="input-group">
                            <v-date-picker mode="date" class="timedate input-group" v-model="rateDetail.endDate">
                              <template v-slot="{ inputValue, togglePopover }">
                                <input placeholder="MM/DD/YYYY" @click="togglePopover()"
                                  v-bind:class="{ 'form-control': true }" :value="inputValue" />
                                <div @click="togglePopover()" class="input-group-append"><span
                                    class="input-group-text"><em class="fa fa-calendar"></em></span></div>
                              </template>
                            </v-date-picker>
                          </div>
                        </div>
                      </div>
                      <div class="col-md-2">
                        <div class="form-group">
                          <label>Limit type:</label>
                          <select class="form-control" v-model="rateDetail.limitType">
                            <option value="HM3">HM3</option>
                            <option value="MMBTU">MMBTU</option>
                          </select>
                        </div>
                      </div>
                      <div class="col-md-2" v-if="customerType == 'Domestic'">
                        <div class="form-group">
                          <label>Customer Rate type:</label>
                          <select class="form-control" v-model="rateDetail.customerRateType">
                            <option value="PR">Protected</option>
                            <option value="NPR">Non-Protected</option>
                          </select>
                        </div>
                      </div>
                      <div class="col-md-2">
                        <div class="form-group">
                          <label>Limit from:</label>
                          <input type="text" class="form-control" v-model="rateDetail.limitFrom" />
                        </div>
                      </div>
                      <div class="col-md-2">
                        <div class="form-group">
                          <label>Limit to:</label>
                          <input type="text" class="form-control" v-model="rateDetail.limitTo" />
                        </div>
                      </div>
                    </div>
                    <div class="table-responsive">
                      <table class="table table-bordered dt-responsive nowrap mt-0">
                        <thead class="thead-bg border">
                          <tr>
                            <th v-if="serviceType.serviceTypeName == 'Usage Based'">Consumption From</th>
                            <th v-if="serviceType.serviceTypeName == 'Usage Based'">Consumption To</th>
                            <th>Base Rate <sup class="text-danger" style="font-size: 18px; top: auto !important">*</sup>
                            </th>
                            <th class="text-center" v-if="serviceType.serviceTypeName == 'Usage Based'">Action</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="(rate, ri) in rateDetail.rates" :key="ri">
                            <td v-if="serviceType.serviceTypeName == 'Usage Based'">
                              <input class="form-control" type="number" v-model.number="rate.from" />
                            </td>
                            <td v-if="serviceType.serviceTypeName == 'Usage Based'">
                              <input class="form-control" type="text" :value="toDisplay(rate.to)"
                                @input="rate.to = fromDisplay($event.target.value)" />
                            </td>
                            <td>
                              <input class="form-control" type="number" v-model.number="rate.rate" step="0.01" />
                            </td>
                            <td class="text-center" v-if="serviceType.serviceTypeName == 'Usage Based'">
                              <a href="javascript:;" @click="addRow(rateDetail, index)" v-if="ri == 0" class="mr-2"><em
                                  class="round-icon-small fa fa-plus bg-success float-none"></em></a>
                              <a href="javascript:;" v-if="ri > 0" @click.prevent="removeRow(rateDetail, ri)">
                                <em class="round-icon-small fa fa-trash bg-danger float-none"></em>
                              </a>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
                <div v-if="serviceType.serviceTypeName == 'Usage Based'">
                  <div class="row">
                    <div class="col-md-12">
                      <div class="col-md-12 mb-2 p-2 bg-light border d-flex">
                        <div class="form-check form-switch col-md-6 mt-1 p-0">
                          <label class="form-check-label mr-2" for="peakHoursSwitch">Peak Hours Applicable?</label>
                          <label class="switch">
                            <input class="form-check-input custom-control-input dynamic" type="checkbox"
                              :checked="row.isPeakHoursEnabled" v-on:change="checkForPeakHoursApplicable(row)" />
                            <span class="slider round" style="cursor: default;">
                              <span class="slider-yes">{{ $t('Yes') }}</span>
                              <span class="slider-no">{{ $t('No') }}</span>
                            </span>
                          </label>
                        </div>
                        <div class="col-md-6 float-right"><a v-if="row.isPeakHoursEnabled" href="javascript:;"
                            v-on:click="openPeakHour(row, rIndex, stIndex, sIndex)"
                            class="float-right btn btn-primary formbtn widthhalf">
                            <em class="fa fa-plus mr-2"></em>Add Peak Hours</a></div>
                      </div>
                    </div>
                  </div>
                  <div class="peak-rates listing mb-3" v-if="row.isPeakHoursEnabled">
                    <div v-if="row.peakRates.length == 0" class="text-center text-danger">
                      No record found
                    </div>
                    <div class="table-responsive" v-if="row.peakRates.length > 0">
                      <table class="table table-bordered">
                        <thead class="thead-bg">
                          <tr>
                            <th>Schedule Type</th>
                            <th>Schedule Range</th>
                            <th width="100" class="text-center">Action</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="(peakRate, index) in row.peakRates" :key="index">
                            <td>{{ showPeakHourType(peakRate.selectValue) }}</td>
                            <td>{{ showSelectedPeakHourOption(peakRate.selectValue, peakRate) }}</td>
                            <td class="text-center single-action">
                              <a href="javascript:;" class="mr-3"><em class="fa fa-edit text-success float-none"
                                  @click="editPeakRate(peakRate, index, rIndex, stIndex, sIndex)"></em></a>
                              <a href="javascript:;" class=""><em class="fa fa-trash text-danger float-none"
                                  @click="deletePeakRate(row, index)"></em></a>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

        </div>
      </div>

      <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mt-2" v-if="isRetroActive && retroactiveData">
        <a :class="card - link" style="color:#000" data-toggle="collapse" href="javascript:;">
          <div class="conversation-date heading-border"><span> Retroactive </span></div>
        </a>

        <div class="row alert alert-success m-0 mb-3">
          <div class="col-md-4">
            <strong>From Month</strong>: {{ formatDate(retroactiveData.StartDate) }} to
            {{ formatDate(retroactiveData.EndDate)
            }}
          </div>
          <div class="col-md-4" v-if="retroactiveData.ActionType == '1' || retroactiveData.ActionType == 1">
            <strong>Action</strong>: Create new invoices for affected months
          </div>
          <div class="col-md-4" v-if="retroactiveData.ActionType == '2' || retroactiveData.ActionType == 2">
            <strong>Adjustment</strong>: Adjust in next billing cycle
          </div>

          <div class="col-md-4 text-right single-action">
            <a href="#"><em class="fa fa-edit mx-2 text-success"
                v-on:click="editRetroactiveDetail(retroactiveData)"></em></a>
            <a href="#"><em class="fa fa-times mx-2 text-danger"
                v-on:click="deleteRetroactiveDetail(retroactiveData)"></em></a>
          </div>

        </div>
      </div>
      <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mb-2 mt-0">
        <div class="row flex-row-reverse">
          <div class="col-lg-6 text-right">
            <!-- <a href="javascript:;" @click="retroactive" class="float-right btn btn-primary formbtn widthhalf">
            <em class="fa fa-plus mr-2"></em>Retroactive</a> -->
            <a href="javascript:;" @click="handleSave" title="" class="btn btn-success formbtn widthhalf mr-2"
              data-toggle-tooltip="tooltip" data-original-title="Save"><i class="fa fa-save pr-2"></i>Save</a>
          </div>
          <div class="col-lg-6 text-left"><a href="javascript:;" title="" data-toggle-tooltip="tooltip"
              @click="addRateSchedule" data-original-title="Save" class="btn btn-primary formbtn widthhalf">
              <i class="fa fa-plus pr-2"></i>Add Rate Schedule</a></div>
        </div>

      </div>
    </div>
    <ModalRetroactive v-if="isModalRetroactive" @retroActiveDetail="handleRetroActiveDetail"
      @cancelRetroActiveList="cancelRetroActiveDetail" :refCodeRetro="refCodeRetro" :retroActDetail="retroActDetail">
    </ModalRetroactive>
    <AddPeakHours v-if="isAddPeakHours" @peakHoursDetail="handlePeakHoursDetail"
      @updatePeakHoursDetail="handleUpdatePeakHoursDetail" :peakRateData="peakRateData" :refCode="refCode"
      :currentEditIndex="currentEditIndex"></AddPeakHours>
    <ModalEnableMinimumCharge v-if="isEnableMinimiumCharge" :editEnableLimitData="editEnableLimitData"
      :MonthApplicable="MonthApplicable" @minimumChargesDetail="handleMinimumChargesDetail"
      :refCodeCharge="refCodeCharge">
    </ModalEnableMinimumCharge>
  </section>
</template>
<script>
import DataServices from "../../services/DataService";
import AddPeakHours from '../../components/Modals/AddPeakHours.vue';
import ModalRetroactive from '../../components/Modals/ModalRetroactive.vue';
import ModalEnableMinimumCharge from '../../components/Modals/ModalEnableMinimumCharge.vue';
export default {
  components: { AddPeakHours, ModalRetroactive, ModalEnableMinimumCharge },
  data() {
    return {
      billingInterval: "monthly",
      billingIntervalText: "Monthly",
      billingIntervalValue: 1,
      PlanRateListing: [],
      isAddPeakHours: false,
      currentEditIndex: null,
      SortBy: "",
      SortExp: "",
      PageSize: "100000",
      isChecked: false,
      rateScheduleName: '',
      refCode: '',
      refCodeRetro: '',
      refCodeCharge: '',
      PageNumber: "1",
      customerType: '',
      isLoading: false,
      serviceTypes: [],
      serviceList: [],
      selectedCurrency: "",
      selectedBillingType: "",
      selectedBillableUnit: "",
      isModalRetroactive: false,
      currencies: [],
      usageMeasurementList: [],
      rateArray: [],
      planRateId: 0,
      monthsArray: [
        { name: "Jan", value: "1" },
        { name: "Feb", value: "2" },
        { name: "Mar", value: "3" },
        { name: "Apr", value: "4" },
        { name: "May", value: "5" },
        { name: "Jun", value: "6" },
        { name: "Jul", value: "7" },
        { name: "Aug", value: "8" },
        { name: "Sep", value: "9" },
        { name: "Oct", value: "10" },
        { name: "Nov", value: "11" },
        { name: "Dec", value: "12" }
      ],
      peakHoursData: [],
      peakRateData: [],
      retroActDetail: null,
      previousJsonPlanListing: null,
      isRetroActive: false,
      retroactiveData: null,
      isCheckedMinimiumCharge: false,
      MonthApplicable: [],
      editEnableLimitData: {},
      enableLimitData: [],
      serviceNameList: '',
      isEnableMinimiumCharge: false,
      addPeakHourPopupData: {
        serviceId: null,
        rIndex: null,
        stIndex: null,
        sIndex: null
      },
    };
  },
  created: async function () {
    var vm = this;
    vm.PlanId = parseInt(this.$route.params.id);
    vm.GetDDLCurrencyData();
    await vm.GetDDLUsageMeasurement();
    await vm.FetchRecords();
  },
  methods: {
    monthNames() {
      if (!this.MonthApplicable) return '';
      return this.monthsArray
        .filter(month => this.MonthApplicable.includes(month.value))
        .map(month => month.name)
        .join(", ");
    },
    handleEdit(item) {
      this.editEnableLimitData = {};
      this.editEnableLimitData = item;
      this.refCodeCharge = 'Update';
      this.isEnableMinimiumCharge = true;
    },
    handleDelete(index) {
      this.enableLimitData = [];
      this.isCheckedMinimiumCharge = false;
    },
    checkForEnableMinimiumCharge(event) {
      this.isCheckedMinimiumCharge = event.target.checked;
      this.isEnableMinimiumCharge = this.isCheckedMinimiumCharge;
    },
    deleteRateDetailsWithLimit: function (RateDetails, index) {
      if (RateDetails.rateDetails.length > 1) {
        RateDetails.rateDetails.splice(index, 1);
      }
    },
    editRetroactiveDetail: function (data) {
      var vm = this;
      vm.retroActDetail = data;
      vm.refCodeRetro = 'Update';
      vm.isModalRetroactive = true;
    },
    addNewRateLimit: function (row, index) {
      row.rateDetails.splice(0, 0, {
        rateLimitId: row.rateDetails.length,
        startDate: "",
        endDate: "",
        limitType: "",
        limitFrom: "",
        customerRateType: "",
        limitTo: "",
        rates: [{
          from: 1,
          to: Infinity,
          rate: 0.0
        }]
      })
    },
    openPeakHour(row, rIndex, stIndex, sIndex) {
      var vm = this;
      vm.refCode = 'Insert';
      vm.addPeakHourPopupData = {
        serviceId: row.serviceId,
        rIndex: rIndex,
        stIndex: stIndex,
        sIndex: sIndex
      }
      vm.isAddPeakHours = true;
    },
    showPeakHourType: function (val) {

      let dayArray = [{ name: "Day", value: "1" },
      { name: "Month", value: "2" },
      { name: "Dates", value: "3" },
      { name: "Month with dates", value: "4" }]
      return (dayArray.filter((x) => { return x.value == val })[0].name)
    },
    showSelectedPeakHourOption: function (type, val) {
      var returnValue = [];
      let dayArray = [{ name: "Monday", value: "1" },
      { name: "Tuesday", value: "2" },
      { name: "Wednesday", value: "3" },
      { name: "Thursday", value: "4" },
      { name: "Friday", value: "5" },
      { name: "Saturday", value: "6" },
      { name: "Sunday", value: "0" }]
      let monthArray = [{ name: "Jan", value: "1" }, { name: "Feb", value: "2" }, { name: "Mar", value: "3" }, { name: "Apr", value: "4" }, { name: "May", value: "5" }, { name: "Jun", value: "6" }, { name: "Jul", value: "7" }, { name: "Aug", value: "8" }, { name: "Sep", value: "9" }, { name: "Oct", value: "10" }, { name: "Nov", value: "11" }, { name: "Dec", value: "12" }]
      var seletedOptionsValue = "";
      if (val.selectedOptions != null && val.selectedOptions != "") {
        seletedOptionsValue = val.selectedOptions.split(",")
      }

      if (val.selectedOptions != null && val.selectedOptions != "" && type == "1") {
        seletedOptionsValue.forEach((x) => {
          returnValue.push((dayArray.filter((y) => { return y.value == x })[0].name))
        });
        return returnValue.join(", ");
      }
      else if (val.selectedOptions != null && val.selectedOptions != "" && type == "2") {
        seletedOptionsValue.forEach((x) => {
          returnValue.push((monthArray.filter((y) => { return y.value == x })[0].name))
        });
        return returnValue.join(", ");
      }
      else if (val.selectedOptions != null && val.selectedOptions != "" && type == "3") {
        return val.selectedOptions;
      }
      else if (type == "4") {
        const fromStartMonth = monthArray.filter(x => x.value == val.fromStartMonth)[0].name;
        const tillEndMonth = monthArray.filter(x => x.value == val.tillEndMonth)[0].name;
        const datesAndMonths = val.fromStartDay + ", " + fromStartMonth + " - " + val.tillEndDay + ", " + tillEndMonth;
        return datesAndMonths;
      }
    },

    editPeakRate(peakRate, index, rIndex, stIndex, sIndex) {
      var vm = this;
      vm.peakRateData = peakRate;
      vm.addPeakHourPopupData = {
        serviceId: peakRate.serviceId,
        rIndex: rIndex,
        stIndex: stIndex,
        sIndex: sIndex
      }
      vm.refCode = 'Update';
      vm.isAddPeakHours = true;
      vm.currentEditIndex = index;
    },
    deletePeakRate(row, index) {
      row.peakRates.splice(index, 1);
    },
    handlePeakHoursDetail(data) {
      var vm = this;
      data.serviceId = vm.addPeakHourPopupData.serviceId;
      vm.PlanRateListing[vm.addPeakHourPopupData.rIndex].serviceTypes[vm.addPeakHourPopupData.stIndex].serviceList[vm.addPeakHourPopupData.sIndex].peakRates.push({ ...data });
      vm.isAddPeakHours = false;
    },

    handleMinimumChargesDetail(item) {
      var vm = this;
      debugger;
      vm.MonthApplicable = item.applicableForMonth;
      vm.enableLimitData = item.limitData;
      vm.serviceNameList = item.serviceNamesCSV;
      vm.isEnableMinimiumCharge = false;
      //debugger;
    },
    handleUpdatePeakHoursDetail(index, data) {
      var vm = this;
      data.serviceId = vm.addPeakHourPopupData.serviceId;
      vm.PlanRateListing[vm.addPeakHourPopupData.rIndex].serviceTypes[vm.addPeakHourPopupData.stIndex].serviceList[vm.addPeakHourPopupData.sIndex].peakRates[index] = {
        ...data,
      };
      vm.isAddPeakHours = false;
    },
    handleRetroActiveDetail(data) {
      this.retroactiveData = JSON.parse(data);
      this.isRetroActive = (this.retroactiveData != null);
      this.isModalRetroactive = false;
      this.retroSaveData();
    },

    deleteRetroactiveDetail() {
      var vm = this;
      vm.retroactiveData = null;
    },
    cancelRetroActiveDetail(type) {
      this.retroSaveData();

    },
    formatDate(dateString) {
      const date = new Date(dateString);
      const day = String(date.getDate()).padStart(2, '0');
      const month = date.toLocaleString('default', { month: 'long' });
      return `${day} ${month}`;
    },
    addRateSchedule() {
      const newRateSchedule = {
        currencyId: this.selectedCurrency || "",
        rateScheduleName: "New Rate Schedule",
        serviceTypes: []
      };

      this.PlanRateListing.forEach(schedule => {
        if (schedule.serviceTypes) {
          const clonedServiceTypes = schedule.serviceTypes.map(serviceType => {
            return {
              serviceTypeName: serviceType.serviceTypeName || "",
              serviceList: (serviceType.serviceList || []).map(service => {
                return {
                  ...service,
                  rates: (service.rates || []).map(rate => ({ ...rate })),
                  serviceInterval: service.serviceInterval || "",
                  billingIntervalValue: service.billingIntervalValue || null
                };
              })
            };
          });
          newRateSchedule.serviceTypes.push(...clonedServiceTypes);
        } else {
          console.warn("No serviceTypes found in schedule:", schedule);
        }
      });

      this.PlanRateListing.push(newRateSchedule);
    },
    checkForPeakHoursApplicable(row) {
      row.isPeakHoursEnabled = !row.isPeakHoursEnabled;
    },
    GetDDLUsageMeasurement: async function () {
      var vm = this;
      var params = `id=&moduleName=&type=UNIT_OF_MEASUREMENTS&search=`
      await DataServices.GetDDLData(params).then((response) => {
        if (response.data) {
          var json = response.data;
          if (json != null) {
            vm.usageMeasurementList = response.data.DATA;
          }
        }
      })
    },
    GetDDLCurrencyData: async function () {
      var vm = this;
      var params = `id=&moduleName=&type=CURRENCY&search=`
      await DataServices.GetDDLData(params).then((response) => {
        if (response.data) {
          var json = response.data;
          if (json != null) {
            vm.currencies = response.data.DATA;
          }
        }
      })
    },
    toDisplay(value) {
      return value === Infinity ? "" : value;
    },
    fromDisplay(value) {
      var res = "";
      if (value === "") {
        res = "";
      }
      else if (value === "") {
        res = Infinity;
      }
      else {
        res = parseFloat(value);
      }
      return res;
    },
    FetchRecords: async function () {
      var vueObj = this;
      vueObj.PlanRateListing = [];
      vueObj.serviceTypes = [];
      vueObj.serviceList = [];
      vueObj.isLoading = true;
      var url = `planId=${vueObj.PlanId}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}`;
      await DataServices.GetPlanRateListing(url).then(function (response) {
        if (response.data != "" && response.data != null) {
          if (response.data.result.data.length > 0) {
            var row = response.data.result.data;
            vueObj.planRateId = response.data.result.data[0].plan_rate_id;
            vueObj.customerType = response.data.result.customer_type;
            debugger;
            let retroActive = (typeof (response.data.result.RETROACTIVE_DATA) != 'undefined') ? (response.data.result.RETROACTIVE_DATA.length > 0 ? response.data.result.RETROACTIVE_DATA[0][0] : null) : null;
            if (retroActive != null) {
              vueObj.retroactiveData = retroActive;
              vueObj.isRetroActive = (vueObj.retroactiveData != null);
            }

            let enableCharge = (typeof (response.data.result.ENABLE_MINIMUM_CHARGE_DATA) != 'undefined') ? (response.data.result.ENABLE_MINIMUM_CHARGE_DATA.length > 0 ? response.data.result.ENABLE_MINIMUM_CHARGE_DATA[0] : null) : null;
            if (enableCharge != null) {
              vueObj.MonthApplicable = enableCharge[0].type_value.split(',');
              const serviceNames = enableCharge.map(item => item.service_names).join(", ");
              vueObj.serviceNameList = "";
              vueObj.serviceNameList = serviceNames;
              vueObj.enableLimitData = enableCharge;
              vueObj.isCheckedMinimiumCharge = true;
            }

            vueObj.serviceTypes = vueObj.serviceTypes || [];
            vueObj.serviceList = vueObj.serviceList || [];
            row.forEach(function (x) {
              var checkIfExists = vueObj.PlanRateListing.filter(function (y) {
                return y.currencyId == x.currency_id;
              });

              if (checkIfExists.length == 0) {
                var obj = {
                  currencyId: x.currency_id,
                  rateScheduleName: x.rate_schedule_name,
                  serviceTypes: []
                };

                var recordOfCurrencyId = row.filter(function (y) {
                  return y.currency_id == x.currency_id;
                });

                recordOfCurrencyId.forEach(function (currRecord) {
                  var ifExists = obj.serviceTypes.filter(function (y) {
                    return y.serviceTypeName === currRecord.service_type_name;
                  });
                  if (ifExists.length == 0) {
                    var serviceList = recordOfCurrencyId.filter(function (y) {
                      return y.service_type_name === currRecord.service_type_name;
                    });
                    var services = []

                    serviceList.forEach(function (service) {
                      var ifServiceAlreadyExists = services.filter((a) => {
                        return a.serviceId == service.service_id;
                      });
                      if (ifServiceAlreadyExists.length == 0) {
                        var serviceObject = {
                          planRateId: service.plan_rate_id,
                          planServiceId: service.plan_service_id,
                          isPeakHoursEnabled: service.peak_rates != null ? service.peak_rates.length > 0 : false,
                          serviceInterval: service.billing_interval != null ? service.billing_interval : "monthly",
                          billingIntervalValue: "1",
                          billingType: service.billing_type != null ? service.billing_type : "FLT",
                          billableUnit: service.billing_unit_id ?? "",
                          rateDetails: [],
                          serviceId: service.service_id,
                          serviceName: service.service_name,
                          peakRates: service.peak_rates ?? []
                        }

                        let getRateWithLimits = serviceList.filter((rates) => {
                          return rates.service_id == service.service_id;
                        });
                        getRateWithLimits.forEach(function (rates) {
                          var ifRateLimitAlreadyExists = serviceObject.rateDetails.filter((a) => {
                            return a.rateLimitId == rates.rate_limit_id;
                          });
                          if (ifRateLimitAlreadyExists.length == 0) {
                            var enterDates = []
                            getRateWithLimits.filter((a) => {
                              if (a.rate_limit_id == rates.rate_limit_id) {
                                enterDates.push({
                                  from: a.from !== null ? a.from : 1,
                                  to: a.to !== null ? a.to : Infinity,
                                  rate: a.rate ? a.rate : 0.0
                                })
                              }
                            })
                            var rateLimitObj = {
                              rateLimitId: rates.rate_limit_id,
                              limitType: rates.limit_value_type,
                              limitFrom: rates.limit_from,
                              limitTo: rates.limit_to,
                              customerRateType: rates.customer_rate_type,
                              startDate: rates.start_date,
                              endDate: rates.end_date,
                              rates: enterDates
                            }
                            serviceObject.rateDetails.push(rateLimitObj);
                          }
                        })
                        services.push(serviceObject);
                      }
                    });

                    var serviceTypeObj = {
                      serviceTypeName: currRecord.service_type_name,
                      meterType: x.utility_name,
                      serviceList: services
                    };

                    obj.serviceTypes.push(serviceTypeObj);
                  }
                });
                vueObj.PlanRateListing.push(obj);
                vueObj.previousJsonPlanListing = JSON.stringify(vueObj.PlanRateListing)
              }
            });
            vueObj.isLoading = false;
          } else {
            vueObj.isLoading = false;
            vueObj.PlanRateListing = [];
          }
        } else {
          vueObj.isLoading = false;
          vueObj.PlanRateListing = [];
        }
        setTimeout(function () {
          vueObj.ResponsiveDataTable("tablelistingdata");
        }, 500);
      });
    },
    handleIntervalChange: function (service) {
      switch (service.serviceInterval) {
        case "monthly":
          service.billingIntervalValue = "1";
          break;
        case "quarterly":
          service.billingIntervalValue = "3";
          break;
        case "yearly":
          service.billingIntervalValue = "12";
          break;
        default:
          service.billingIntervalValue = "1";
          break;
      }
    },
    addRow(row, index) {
      const lastRate = row.rates[row.rates.length - 1];
      // const newFrom =
      //   lastRate.to === Infinity ? lastRate.from : lastRate.to + 1;
      //debugger
      var newFrom = "";
      if (lastRate.to === Infinity) {
        newFrom = lastRate.from;
      }
      else if (lastRate.to > 1) {
        newFrom = lastRate.to + 1;
      }
      else if (lastRate.to > 0 && lastRate.to < 1) {
        newFrom = lastRate.to + 0.01;
      }
      if (lastRate.to === Infinity) {
        lastRate.to = 0;
      }
      const newRate = {
        from: newFrom,
        to: Infinity,
        rate: 0,
      };
      row.rates.push(newRate);
    },
    removeRow(row, index) {
      if (row.rates.length > 1) {
        row.rates.splice(index, 1);
      }
    },
    getJSONData() {
      var vm = this;
      vm.refCodeRetro = "Insert";
      vm.retroActDetail = null;
      const data = {
        PlanRateListing: vm.PlanRateListing.map(schedule => ({
          currencyId: 107,
          rateScheduleName: schedule.rateScheduleName,
          serviceTypes: schedule.serviceTypes.map(serviceType => ({
            serviceTypeName: serviceType.serviceTypeName,
            serviceList: serviceType.serviceList.map(service => {
              const peakHours = vm.peakHoursData.find(peak => peak.serviceId === service.service_id);
              return {
                ...service,
                peakHoursList: peakHours ? vm.peakHoursData : [],
              };
            }),
          })),
        })),
        chargeDetails: {
          EnableMinimiumChargeData: vm.enableLimitData,
          MonthApplicable: vm.MonthApplicable
        },
        RetroActiveData: vm.retroactiveData
      };
      return JSON.stringify(data);
    },
    retroSaveData() {
      var vm = this;
      const jsonData = vm.getJSONData();
      const obj = JSON.stringify(jsonData);
      const requestPayload = { objData: obj, planId: vm.PlanId };
      DataServices.SavePlanRate(requestPayload).then((response) => {
        if (response.data != null) {
          if (response.data.Status == "Success" && response.data.Code == "1") {
            vm.ShowAlert(
              vm.$t("UpdatedSuccess", [vm.$t("Rate")]),
              "Success",
              true,
              vm.$t("Alert")
            );
            setTimeout(() => { vm.FetchRecords(); }, 500);
            vm.isLoading = false;
          } else {
            vm.ShowAlert(
              vm.$t("ErrorWhileSaving", [vm.$t("Rate")]),
              "danger",
              true,
              vm.$t("Alert")
            );
            vm.isLoading = false;
          }
        }
      });
    },
    handleSave() {
      var vm = this;
      const jsonData = vm.getJSONData();
      if (vm.planRateId > 0) {
        if (vm.previousJsonPlanListing) {
          const parsedData = JSON.parse(jsonData);
          // Remove RetroActiveData
          delete parsedData.RetroActiveData;
          // Remove peakHoursList from each serviceList item
          parsedData.PlanRateListing.forEach(rate => {
            rate.serviceTypes.forEach(serviceType => {
              serviceType.serviceList.forEach(service => {
                delete service.peakHoursList;
              });
            });
          });
          // Stringify the modified data
          const modifiedString = JSON.stringify(parsedData.PlanRateListing);
          const isEqual = JSON.stringify(vm.previousJsonPlanListing) === JSON.stringify(modifiedString);
          if (isEqual) {
            vm.ShowAlert("There are no changes in rates", "danger", true, vm.$t("Alert"));
          } else {
            vm.isModalRetroactive = true;
          }
        }
      }
      else {
        const obj = JSON.stringify(jsonData);
        const requestPayload = { objData: obj, planId: vm.PlanId };
        DataServices.SavePlanRate(requestPayload).then((response) => {
          if (response.data != null) {
            if (response.data.Status == "Success" && response.data.Code == "1") {
              vm.ShowAlert(
                vm.$t("UpdatedSuccess", [vm.$t("Rate")]),
                "Success",
                true,
                vm.$t("Alert")
              );
              setTimeout(() => { vm.FetchRecords(); }, 500);
              vm.isLoading = false;
            } else {
              vm.ShowAlert(
                vm.$t("ErrorWhileSaving", [vm.$t("Rate")]),
                "danger",
                true,
                vm.$t("Alert")
              );
              vm.isLoading = false;
            }
          }
        });
      }
    },
  },
};
</script>